import {
	getDOMElements,
	isDefined,
	vsprintf
} from './Utilities.js';

class MetaboxNotice {
	constructor( itemType, noticeType, noticeMessage ){
		this.itemType 		= itemType;
		this.noticeType 	= noticeType;
		this.noticeMessage 	= noticeMessage;

		// Create notice
        this.create();
	}

	create(){
		// Append to the metabox
        let metabox = getDOMElements( 'metabox', {
            type: this.itemType
        });

        // Get notice
        let notice = this.createNotice();

        // Append to the metabox
        metabox.append( notice );
	}

	createNotice(){
		let container = $( '<div/>', { 'class': 'metabox__notice' });

		if ( isDefined( this.noticeType ) ){
			container.addClass( vsprintf( 'metabox__notice--%s', [ this.noticeType ] ) );
		}

		container.html( this.noticeMessage );
		return container;
	}
}

export default MetaboxNotice;


