import {
	isDefined,
	escapeHTML
} from './Utilities.js';

class Markdown {
	constructor( content = '' ){
		// Save content
		this.content = content;
	}

	getHTML(){
		// Define content and escape HTML
		let content = escapeHTML( this.content );
		
		content = this.parseItalicsAndBold( content );
		content = this.parseAnchors( content );

		return content;
	}

	parseItalicsAndBold( content ){
		// Underscores
		// Since we are consuming a \s character, we need to add it
		content = content.replace( /(^|\s|>|\b)__(?=\S)([^]+?)__(?=\b|<|\s|$)/gm, '$1<strong>$2</strong>' );
		content = content.replace( /(^|\s|>|\b)_(?=\S)([^]+?)_(?=\b|<|\s|$)/gm, '$1<em>$2</em>' );

		// Asterisks
		content = content.replace( /(\*\*)(?=\S)([^\r]*?\S[*]*)\1/g, '<strong>$2</strong>' );
		content = content.replace( /(\*)(?=\S)([^\r]*?\S)\1/g, '<em>$2</em>' );

		// Return content
		return content;
	}

	parseAnchors( content ){
		// Inline-style links: [link text](url "optional title")
		content = content.replace( /(\[((?:\[[^\]]*]|[^\[\]])*)]\([ \t]*()<?(.*?(?:\(.*?\).*?)?)>?[ \t]*((['"])(.*?)\6[ \t]*)?\))/g, function( wholeMatch, m1, m2, m3, m4, m5, m6, m7 ){
			if ( ! isDefined( m7 ) ){
				m7 = '';
			}

			wholeMatch = m1;
			
			var linkText = m2,
				linkId 	 = m3.toLowerCase(),
				url 	 = m4,
				title 	 = m7;

			if ( ! url ){
				if ( ! linkId ){
					// Lower-case and turn embedded newlines into spaces
					linkId = linkText.toLowerCase().replace( / ?\n/g, ' ' );
				}

				url = '#' + linkId;

				if ( wholeMatch.search( /\(\s*\)$/m ) > -1 ){
					// Special case for explicit empty url
					url = '';
				}
				else {
					return wholeMatch;
				}
			}

			url = encodeURI( url, '*_', false );
			var result = '<a href="' + url + '" target="_blank"';

			if ( title !== '' && title !== null ){
				title = title.replace( /"/g, '&quot;' );
				title = encodeURI( title, '*_', false );
				result += ' title="' + title + '"';
			}

			result += '>' + linkText + '</a>';

			return result;
		});

		// Return content
		return content;
	}
}

export default Markdown;


