import {
	restCall
} from './Utilities.js';

class Taxonomies {
	constructor(){
		// Save categories
		new Categories();

		// Save tags
		new Tags();
	}
}

class Categories {
	constructor(){
		// Get elements
		this.getElements();

		// Check if the required element exist before adding
		// the event listeners
		if ( this.$elements.allCategories.length == 1 ){
			// Listen changes to the checkboxes
			this.listenCheckboxes();
		}
	}

	getElements(){
		this.$elements = {
			container:     $( '#recipe_categorydiv' ),
			allCategories: $( '#recipe_categorychecklist' )
		}
	}

	listenCheckboxes(){
		// Listen changes to each checkbox
		this.$elements.container.on( 'change', 'input[type="checkbox"][name="tax_input[recipe_category][]"], .popular-category input[type="checkbox"]', ( event ) => {
			// Get checkbox
			const $checkbox = $( event.currentTarget );

			// Save category
			this.saveCategory( $checkbox );
		});

		// Listen when the user adds a new category. This will be
		// triggered when WP creates a new category after the user
		// used the "+ Add New Category" form
		new MutationObserver(( mutations ) => {
			// Iterate mutations
			mutations.forEach( ( mutation ) => {
				// Iterate new categories
				mutation.addedNodes.forEach(( addedNode ) => {
					// Get the checkbox of the new category
					const $checkbox = $( addedNode ).find( 'input[type="checkbox"][name="tax_input[recipe_category][]"]' );

					// Save category
					this.saveCategory( $checkbox );
				});
			});
		}).observe( this.$elements.allCategories[0], {
			childList: true
		});
	}

	getCheckboxData( $checkbox ){
		return {
			id:        $checkbox.val(),
			isChecked: $checkbox.is( ':checked' ),
		}
	}

	saveCategory( $checkbox ){
		// Get the data
		let checkboxData = this.getCheckboxData( $checkbox );

		// Set checkbox as loading
		this.setStatus( 'loading', true, $checkbox );

		// Do call
		restCall( 'set_recipe_terms', {
			term_id:      'recipe_category',
			recipe_id:    UncannyAutomator.recipe.id,
			category_id:  checkboxData.id,
			set_category: checkboxData.isChecked
		}, ( response ) => {
			// If the call (not necessarily the task) was successful...

			if ( response.success ){
				// If everything went well...
				// Show a green check icon
				this.setStatus( 'success', true, $checkbox );

				// Remove it after 2 seconds
				setTimeout(() => {
					// Remove the green check icon
					this.setStatus( 'success', false, $checkbox );
				}, 2000 );
			}
			else {
				console.log( ' ERROR-TODO. Couldn\'t change the status of the recipe category' );

				// If something went wrong,
				// revert the value of the checkbox
				this.revertCheckboxValue( $checkbox );

				// Show an error icon
				this.setStatus( 'error', true, $checkbox );
				// Remove it after 2 seconds
				setTimeout(() => {
					// Remove the error icon
					this.setStatus( 'error', false, $checkbox );
				});
			}

			// Remove loading animation
			this.setStatus( 'loading', false, $checkbox );
		}, ( response ) => {
			// On error...

			// Revert the value of the checkbox
			this.revertCheckboxValue( $checkbox );

			// Remove loading animation
			this.setStatus( 'loading', false, $checkbox );

			// Show an error icon
			this.setStatus( 'error', true, $checkbox );
			// Remove it after 2 seconds
			setTimeout(() => {
				// Remove the error icon
				this.setStatus( 'error', false, $checkbox );
			});
		}, {
			requiresRecipesObject: false,
			renderUI:              false
		});
	}

	revertCheckboxValue( $checkbox ){
		// Revert the value of the checkbox
		$checkbox.prop( 'checked', ! $checkbox.is( ':checked' ) );
	}

	setStatus( status = '', enable = true, $checkbox ){
		// Get label container
		const $label = $checkbox.closest( '.selectit' );

		// Check what status we have to set or unset
		if ( status == 'loading' ){
			// Check if we have to add or remove the loading status
			if ( enable ){
				// Add loading animation
				$label.addClass( 'uap-taxonomy-loading' );

				// Disable checkbox
				$checkbox.prop( 'disabled', true );
			}
			else {
				// Remove loading animation
				$label.removeClass( 'uap-taxonomy-loading' );

				// Enable checkbox
				$checkbox.prop( 'disabled', false );
			}
		}
		else if ( status == 'success' ){
			// Check if we have to add or remove the loading status
			if ( enable ){
				// Add loading animation
				$label.addClass( 'uap-taxonomy-success' );
			}
			else {
				// Remove loading animation
				$label.removeClass( 'uap-taxonomy-success' );
			}
		}
		else if ( status == 'error' ){
			// Check if we have to add or remove the loading status
			if ( enable ){
				// Add loading animation
				$label.addClass( 'uap-taxonomy-error' );
			}
			else {
				// Remove loading animation
				$label.removeClass( 'uap-taxonomy-error' );
			}
		}
	}
}

class Tags {
	constructor(){
		// Get elements
		this.getElements();

		// Check if the required elements exist before adding
		// the event listeners
		if ( this.$elements.tagsTextarea.length == 1 ){
			// Listen modifications to the textarea
			this.listenModificationsToTags();
		}
	}

	getElements(){
		this.$elements = {
			container:    $( '#tagsdiv-recipe_tag' ),
			tagsTextarea: $( 'textarea#tax-input-recipe_tag' ),
			tagsList:     $( '#tagsdiv-recipe_tag .tagchecklist' )
		}
	}

	listenModificationsToTags(){
		// Listen when the user adds a new tag. This will be
		// triggered when the user uses the input to add tags
		new MutationObserver(( mutations ) => {
			// Save tags
			this.saveTags();
		}).observe( this.$elements.tagsList[0], {
			childList: true
		});
	}

	saveTags(){
		// Get tags
		const tags = this.getTags();

		// Set checkbox as loading
		this.setStatus( 'loading', true );

		// Do call
		restCall( 'set_recipe_terms', {
			term_id:      'recipe_tag',
			recipe_id:    UncannyAutomator.recipe.id,
			tags:         tags,
		}, ( response ) => {
			// If the call (not necessarily the task) was successful...

			if ( response.success ){
				// If everything went well...
				// Show a green check icon
				this.setStatus( 'success', true );

				// Remove it after 2 seconds
				setTimeout(() => {
					// Remove the green check icon
					this.setStatus( 'success', false );
				}, 2000 );
			}
			else {
				// Reload
				// location.reload();
			}

			// Remove loading animation
			this.setStatus( 'loading', false );
		}, ( response ) => {
			// On error...

			// Reload
			location.reload();
		}, {
			requiresRecipesObject: false,
			renderUI:              false
		});
	}

	setStatus( status = '', enable = true ){
		// Check what status we have to set or unset
		if ( status == 'loading' ){
			// Check if we have to add or remove the loading status
			if ( enable ){
				// Add loading animation
				this.$elements.container.addClass( 'uap-tags-loading' );
			}
			else {
				// Remove loading animation
				this.$elements.container.removeClass( 'uap-tags-loading' );
			}
		}
		else if ( status == 'success' ){
			// Check if we have to add or remove the loading status
			if ( enable ){
				// Add loading animation
				this.$elements.container.addClass( 'uap-tags-success' );
			}
			else {
				// Remove loading animation
				this.$elements.container.removeClass( 'uap-tags-success' );
			}
		}
		else if ( status == 'error' ){
			// Check if we have to add or remove the loading status
			if ( enable ){
				// Add loading animation
				this.$elements.container.addClass( 'uap-tags-error' );
			}
			else {
				// Remove loading animation
				this.$elements.container.removeClass( 'uap-tags-error' );
			}
		}
	}

	getTags(){
		// Get tags separated by comma
		const tagsCommaSeparated = this.$elements.tagsTextarea.val();

		// Create array
		const tagsArray = tagsCommaSeparated.split( ',' );

		// Return both
		return {
			asArray:        tagsArray,
			commaSeparated: tagsCommaSeparated
		}
	}
}

export default Taxonomies;


