import {
    itemsObject,
    getDOMElements,
    isEmpty,
    isDefined,
    vsprintf
} from './Utilities.js';
import ItemCreatorIntegrations  from './ItemCreatorIntegrations.js';
import MetaboxNotice            from './MetaboxNotice.js';
import MetaboxItems             from './MetaboxItems.js';
import MetaboxFooter            from './MetaboxFooter.js';

class Triggers {
    constructor(){
        // Create console group
        // console.group( 'Triggers' );

        // Get and clear metabox
        let metabox = getDOMElements( 'metabox', { type: 'trigger' });
        metabox.empty();

        // Get recipe triggers
        let triggers = itemsObject( 'get', {
            filter: {
                itemType: 'trigger'
            }
        });

        // Check if it has triggers
        if ( ! isEmpty( triggers ) ){
            // Render items
            new MetaboxItems( 'trigger' );
        }
        else {
            new ItemCreatorIntegrations( 'trigger' );
        }

        // Create metabox footer
        new MetaboxFooter( 'trigger' );

        // Close console group
        // console.groupEnd();
    }
}

export default Triggers;


