import {
	AJAXRequest,
	compareStrings,
	getCurrentFieldsValues,
	getField,
	getFieldTypes,
	getTokens,
	getUniqueId,
	inArray,
	isDefined,
	isEmpty,
	removeSpaces,
	vsprintf
} from './Utilities.js';

import {
	initURLChecker,
	validateField
} from './Validation.js'

/**
 * Create a field
 *
 * @since 0.4
 *
 * @param {string}              type - Field type
 * @param {object}              attributes - Object with information about the field
 * @param {string}              attributes.itemType             Item type. Valid values are trigger, action and closure.
 * @param {int}                 attributes.itemID               ID of the recipe item
 * @param {(null|string|array)} attributes.defaultValue         Default value of the field
 * @param {string}              attributes.currentValue         Current value of the field
 * @param {string}              attributes.placeholder          Placeholder of the field
 * @param {string}              attributes.isHidden             Defines if the field should be hidden
 * @param {string}              attributes.label                Label of the field
 * @param {string}              attributes.description          Description of the field
 * @param {object}              attributes.options              Only used if the field is a select. All the options of the select.
 * @param {null|string}         [attributes.color]              Only used for checkboxes. Valid values are null, 'gutenberg' or 'green'
 * @param {null|string}         [attributes.align]              Only used for checkboxes. Valid values are null, 'left' or 'right'
 * @param {object}              [attributes.toggle]             Only used for checkboxes. Has On and Off strings
 * @param {string}              [attributes.toggle.on]          String shown when the toggle is selected
 * @param {string}              [attributes.toggle.off]         String shown when the toggle is unselected
 *
 * @param {boolean}             attributes.isAjax               Only used if the field is a select. Add listner for the AJAX request.
 * @param {(null|string|array)} attributes.targetField          Only used if the field is a select. AJAX return values and populate this field.
 * @param {string}              attributes.integrationID        Integration of plugin
 * @param {string}              attributes.endPoint             endPoint for Ajax Call
 */

class Field {
	constructor( type, attributes, createDOMelements = true, customValidationFunction = null ){
		this.type = type;
		
		this.attributes 			  = this.setAttributes( attributes );
		this.customValidationFunction = customValidationFunction;

		if ( createDOMelements ){
			this.createFormElement();
		}
	}

	/**
	 * Create a jQuery DOM element with the field
	 *
	 * @since 0.3
	 */

	createFormElement(){
		// Create response object
		let response = null;

		// Local variables
		let type = this.type;
	  	let attributes = this.attributes;

		// Check if it's a valid field type
		if ( inArray( type, getFieldTypes() ) ){
			// Create form element container, this is global
			let formElement = $( '<div/>', { 'class': 'form-element' } );

			// Check the user is trying to add a custom class to the formElement element
			if ( isDefined( attributes.formElementClass ) ){
				formElement.addClass( attributes.formElementClass );
			}

			// Fields attributes, those will be added to the field jQuery object
			let fieldAttributes = {
				'name':				     attributes.optionCode,
				'id':					 `${ attributes.optionCode }__${ getUniqueId() }`,

				'data-isajax':		     attributes.isAjax,
				'data-targetfield':	     attributes.targetField,
				'data-integrationid':	 attributes.integrationID,
				'data-endPoint':		 attributes.endPoint,

				'data-supportsTokens':   attributes.supportsTokens,
				'data-supportsTinymce':  attributes.supportsTinyMCE,
				'data-required':		 attributes.isRequired,
				'data-item_type':		 attributes.itemType,

				'data-is-hidden':        attributes.isHidden,
			}

			// Define field container variable once. An different jQuery object will be created based on the field type
			let fieldContainer, field;

			// Create field based in field type
			switch ( type ){
				case 'select':
					// Field container
					fieldContainer = $( '<div/>', {'class': 'form-select'} );

					this.fieldContainer = fieldContainer;

					// Create select element and add fieldAttributes to jQuery object. Merge second object into fieldAttributes, recursively
					field = $( '<select/>', $.extend( {}, fieldAttributes, {
						'data-type': 'select',
						'class':	 'form-element__select',
					}));

					this.field = field;

					let hasSelectedOption = false,
						numberOfOptions = 0,
						defaultValue,
						defaultOptionExists;

					// Check if it has default option and get the default option's value
					if ( attributes.hasDefaultValue ){
						if ( ! isEmpty( attributes.defaultValue.option_value ) && ! isEmpty( attributes.defaultValue.option_name ) ){
							// The default option has value and name, so we have to add a new option instead of searching for one
							defaultValue = attributes.defaultValue.option_value.toString();
						}
						else {
							defaultValue = attributes.defaultValue;
						}
					}

					// Create select options
					$.each( attributes.options, function ( value, option_data ){
						console.log(option_data.value);
						let selectOption = $( '<option/>', {
							'value':  option_data.value.toString(),
							'text':	  option_data.text
						});

						// Check if one of the options value match with the default value. In that case, just select it instead of creating another one with the same value
						if ( attributes.hasDefaultValue && compareStrings( defaultValue, value ) ){
							selectOption.prop( 'selected', true );
							defaultOptionExists = true;
							hasSelectedOption   = true;
						}

						// But also check if the select has a value (and it's not empty), in that case we have to select that one
						if ( attributes.hasValue && compareStrings( attributes.currentValue, option_data.value.toString() ) ){
							selectOption.prop( 'selected', true );
							hasSelectedOption = true;
						}

						selectOption.appendTo( field );
						numberOfOptions++;
					});

					// Check if this select supports the token selector
					// If it does, create the "Custom value" field
					/*
					if ( attributes.supportsCustomValue ){
						// Create "Other options" section
						const $otherOptions = $( `
								<optgroup label="${ UncannyAutomator.i18n.validation.field.select.otherOptions }">
									<option value="automator_custom_value" class="test" data-custom="asdasdasdas">${ UncannyAutomator.i18n.validation.field.select.customValue }</option>
								</optgroup>
							` );

						// Append "Other options" to the field
						field.append( $otherOptions );

						// Create custom value field
						let customValueField = new Field( 'text', {});
					}
					// Check if 
					else {

					}
					*/

					// Check if an option was selected, if not then check if we have a default option that doesn't exist in the select, in that case, create one
					if ( attributes.hasDefaultValue && ! defaultOptionExists ){
						// Create option

						if ( ! isEmpty( attributes.defaultValue.option_value ) && ! isEmpty( attributes.defaultValue.option_name ) ){

							let defaultOption = $( '<option/>', {
								'value':	 attributes.defaultValue.option_value,
								'text':	 attributes.defaultValue.option_name
							});

							if ( ! attributes.hasValue && ! compareStrings( attributes.defaultValue.option_value, attributes.currentValue ) ){
								defaultOption.prop( 'selected', true );
								hasSelectedOption = true;
							}

							// Add first (or second, if the select has a placeholder)
							defaultOption.prependTo( field );
							numberOfOptions++;
						}
					}

					// Check if we have to add a placeholder
					if ( attributes.hasPlaceholder ){
						// Add empty option at the top
						field.prepend( '<option></option>' );

						// If nothing else is selected, then remove the selected
						// value of the select
						if ( ! attributes.hasDefaultValue && ! attributes.hasValue ){
							field.find( 'option:selected' ).prop( 'selected', false );
						}
					}

					// Has a selected option
					this.selectHasSelectedOption = hasSelectedOption;

					// Check if it has options, if it doesn't then add one and disable
					if ( numberOfOptions == 0 ){
						let noResults = new Option( UncannyAutomator.i18n.noResults, '', true, true );
						this.field.append( noResults );
						this.field.prop( 'disabled', true );
					}

					// Bind changes
					field.on( 'change', function(){
						formElement.trigger( 'automator:field:change' );
					});

					break;

				case 'text':
				case 'url':
				case 'email':
				case 'int':
				case 'float':
					// FALLTHROUGH: These fields share the same structure as all of them are input text. The validation is done by another function. For now, these fields types only have two differences, the CSS class added in fieldContainer and the type property of the attributes object added to the field variable.

					// Create CSS classes that will be used by the field container element. This will create classes like "form-input--url". They are not used for validation but are useful to add custom CSS to each field
					let fieldContainerClass = 'form-input form-input--' + type;

					// Field container
					fieldContainer = $( '<div/>', { 'class': fieldContainerClass } );

					this.fieldContainer = fieldContainer;

					// Create input element and add fieldAttributes to jQuery object. Merge second object into fieldAttributes, recursively
					field = $( '<input/>', $.extend( {}, fieldAttributes, {
						'class':		'form-element__input',
						'type':		 'text',
						'data-type':	type,
					}));

					this.field = field;

					// Add tokens CSS class to field. This will be used to render the Token Selector once the element is added to the DOM
					if ( attributes.supportsTokens ){
						fieldContainer.addClass( 'form-input--token' );
					}

					// Check if the field has a placeholder and add it
					if ( attributes.hasPlaceholder ){
						field.prop( 'placeholder', attributes.placeholder );
					}

					// Check if the field has a value and add it
					if ( attributes.hasValue ){
						field.prop( 'value', attributes.currentValue );
					}
					else {
						// Now that we now that the field doesn't have a value we can check if the developer defined a default value
						if ( attributes.hasDefaultValue ){
							field.prop( 'value', attributes.defaultValue );
						}
					}

					// Bind changes
					field.on( 'input', function(){
						formElement.trigger( 'automator:field:change' );
					});

					break;

				case 'textarea':
					// Field container
					fieldContainer = $( '<div/>', {'class': 'form-textarea'} );

					this.fieldContainer = fieldContainer;

					// Create textarea
					field = $( '<textarea/>', $.extend( true, fieldAttributes, {
						'data-type':		 'text',
						'class':			 'form-element__textarea',
						'data-item_type':	 attributes.itemType
					}));

					this.field = field;

					// Add tokens CSS class to field. This will be used to render the Token Selector once the element is added to the DOM
					if ( attributes.supportsTokens ){
						fieldContainer.addClass( 'form-textarea--token' );
					}

					// Check if the field has a placeholder and add it
					if ( attributes.hasPlaceholder ){
						field.prop( 'placeholder', attributes.placeholder );
					}

					// Check if the field has a value and add it
					if ( attributes.hasValue ){
						field.text( attributes.currentValue );
					}
					else {
						// Now that we now that the field doesn't have a value we can check if the developer defined a default value
						if ( attributes.hasDefaultValue ){
							field.text( attributes.defaultValue );
						}
					}

					// Bind changes
					field.on( 'input', function(){
						formElement.trigger( 'automator:field:change' );
					});

					break;
		   
				case 'checkbox':
					// Field container
					fieldContainer = $( '<div/>', {'class': 'form-checkbox' } );

					this.fieldContainer = fieldContainer;

					let checkboxLabel = $( '<label/>', {'class': 'form-checkbox__container' } );
					let checkboxPseudo = $( '<div/>', {'class': 'checkbox--show' } );

					// Create checkbox
					field = $( '<input/>', $.extend( {}, fieldAttributes, {
						'type':	     'checkbox',
						'data-type': 'checkbox',
						'class':	 'checkbox--hidden checkbox--primary',
					}));

					// Check if checked by default
					if ( attributes.hasValue && attributes.currentValue ){
						field.prop( 'checked', true );
					}

					this.field = field;

					// Add label
					if ( attributes.hasLabel ){
						checkboxLabel.text( attributes.label );
					}

					// Append elements
					fieldContainer.append( checkboxLabel );
					checkboxLabel.append( field );
					checkboxLabel.append( checkboxPseudo );

					// Check if we have to render a Toggle
					if ( isDefined( attributes.isToggle ) && attributes.isToggle ){
						fieldContainer.addClass( 'form-checkbox--toggle' );

						// Check if we have to add on and off strings divs
						if ( isDefined( attributes.toggle ) ){
							// Check "on"
							if ( isDefined( attributes.toggle.on ) ){
								let checkboxToggleOn = $( '<span/>', { 'class': 'checkbox-label checkbox-label--on' }).text( attributes.toggle.on );
								checkboxLabel.append( checkboxToggleOn );
							}

							// Check "off"
							if ( isDefined( attributes.toggle.off ) ){
								let checkboxToggleOff = $( '<span/>', { 'class': 'checkbox-label checkbox-label--off' }).text( attributes.toggle.off );
								checkboxLabel.append( checkboxToggleOff );
							}
						}
					}

					// Define color
					if ( isDefined( attributes.color ) ){
						// Check if the color is valid
						if ( inArray( attributes.color, [ 'gutenberg', 'green' ] ) ){
							// Add class
							switch ( attributes.color ){
								case 'gutenberg':
									fieldContainer.addClass( 'form-checkbox--toggle-gutenberg' );
									break;

								case 'green':
									fieldContainer.addClass( 'form-checkbox--toggle-green' );
									break;
							}
						}
						else {
							console.error( vsprintf( 'The color %s does not exists. Default color will be used instead.', [ attributes.color ] ) );
						}
					}

					// Define align
					if ( isDefined( attributes.align ) ){
						// Check if it's a valid value
						if ( inArray( attributes.align, [ 'left', 'right' ] ) ){
							// Check if it's 'right', otherwise ignore, since 'left' doesn't needs a class
							if ( attributes.align == 'right' ){
								// Add class
								fieldContainer.addClass( 'form-checkbox--toggle-right' );
							}
						}
						else {
							console.error( vsprintf( 'The alignment %s does not exists. %s will be used instead.', [ attributes.align, '"left"' ] ) );
						}
					}

					// Bind changes
					field.on( 'change', function(){
						formElement.trigger( 'automator:field:change' );
					});

					// Change field variable with all the container
					field = checkboxLabel;

					break;

				case 'radio':
					// Field container
					fieldContainer = $( '<div/>', {'class': 'form-radio' } );
					this.fieldContainer = fieldContainer;

					// Create array to save all the jQuery DOM elements
					let $fields = $(),
						$fieldContainers = $();

					// Iterate options
					$.each( attributes.options, function( index, option ){
						// Create container
						const $formRadioContainer = $( '<label/>', { 'class': 'form-radio__container' });

						// Add label
						$formRadioContainer.text( option.text );

						// Create radio
						const $field = $( '<input/>', $.extend( {}, fieldAttributes, {
							'type':	     'radio',
							'data-type': 'radio',
							'class':	 'radio--hidden radio--primary',

							'value':      option.value,
							'name':       fieldAttributes.id
						}));

						// Save radio
						$fields = $fields.add( $field );

						// Create fake radio
						const $fakeRadio = $( '<div/>', { 'class': 'radio--show' });

						// Append elements
						$formRadioContainer.append( $field );
						$formRadioContainer.append( $fakeRadio );

						// Check if this field has a value, if it does, and the value
						// matches the input radio name, then check it
						if ( attributes.hasValue && attributes.currentValue == option.value ){
							$field.prop( 'checked', true );
						}
						// Otherwise, check if it has a default value and use that
						else if ( ! attributes.hasValue && attributes.hasDefaultValue && attributes.defaultValue == option.value ){
							$field.prop( 'checked', true );
						}

						// Save container
						$fieldContainers = $fieldContainers.add( $formRadioContainer );
					});

					// Save fields
					this.field = $fieldContainers;
					field = $fieldContainers;

					// Bind changes
					$fields.on( 'change', () => {
						// Check if the value is different
						formElement.trigger( 'automator:field:change' );
					});

					break;
			}	

			// Check if it supports tokens and render the token selector
			if ( attributes.supportsTokens ){
				let formVariable = $( '<div/>', { 'class': 'form-variable' } );

				let formVariableFieldContainer = $( '<div/>', { 'class': 'form-variable__element' } );
				let formVariableFieldContainerRender = $( '<div/>', { 'class': 'form-variable__render-container' } );
				let formVariableToggle = $( '<div/>', { 'class': 'form-variable__toggle' } );
				let formVariableToggleIcon = $( '<span/>', { 'class': 'uo-icon uo-icon--asterisk' } );
				formVariableFieldContainerRender.append( field );
				formVariableFieldContainer.append( formVariableFieldContainerRender );
				formVariableToggle.append( formVariableToggleIcon );
				formVariableFieldContainer.append( formVariableToggle );
				formVariable.append( formVariableFieldContainer );

				let formVariableDropdown = $( '<div/>', { 'class': 'form-variable__dropdown' } );
				formVariable.append( formVariableDropdown );

				// Replace original field
				field = formVariable;
			}

			// Append field jQuery object to field container
			fieldContainer.append( field );

			// Create label

			// Create Label, even if it's empty but the field is required, so we can add the asterisk
			let fieldLabel;

			if ( type != 'checkbox' && ( attributes.hasLabel || attributes.isRequired ) ){
				fieldLabel = $( '<label/>', {
					'class': 'form-element__label',
					'for': 	 attributes.optionCode
				});

				// Add label text
				if ( attributes.hasLabel ){
					fieldLabel.text( attributes.label );
				}
			}

			// Check if it's a required field
			if ( attributes.isRequired ){
				// Add required class to the field container
				fieldContainer.addClass( 'form-element--required' );

				// Check if it's a checkbox
				if ( attributes.fieldType == 'checkbox' ){
					// Add required attribute to the field
					field.find( 'input' ).prop( 'required', true );

					// Add asterisk to the label
					if ( attributes.hasLabel ){
						// Add required icon next to label
						let fieldLabelAsterisk = $( '<span/>', { 'class': 'form-element-label__required' } );
						field.append( fieldLabelAsterisk );
					}
				}
				else {
					// Add required attribute to the field
					field.prop( 'required', true );

					// Add asterisk to the label
					if ( attributes.hasLabel ){
						// Add required icon next to label
						let fieldLabelAsterisk = $( '<span/>', { 'class': 'form-element-label__required' } );
						fieldLabel.append( fieldLabelAsterisk );
					}
				}	
			}

			// Check if it's
			if ( isDefined( attributes.isReadOnly ) && attributes.isReadOnly ){
				// Check if it's a select
				if ( attributes.fieldType == 'select' ){
					// Disable the select
					field.prop( 'disabled', true );
				}
				else if ( [ 'checkbox', 'radio' ].includes( attributes.fieldType ) ){
					// Disable the radio and checkbox
					field.find( 'input' ).prop( 'disabled', true );
				}
				else {
					// Add read only attribute to the field
					field.prop( 'readonly', true );
				}
			}

			// Append label element if is defined
			if ( attributes.hasLabel || attributes.isRequired ){
				fieldContainer.prepend( fieldLabel);
			}

			// Error container
			let fieldError = $( '<div/>', { 'class': 'form-element__error' });
			fieldContainer.append( fieldError );

			this.fieldContainer = fieldContainer;

			// Field description
			if ( attributes.hasDescription ){
				let fieldDescription = $( '<div/>', { 'class': 'description' } );
				fieldDescription.html( attributes.description );
				fieldContainer.append( fieldDescription );
			}

			// Check if the field is hidden
			if ( attributes.isHidden ){
				// Add class to hide the field container
				fieldContainer.addClass( 'form-element--hidden' );
			}

			// Append everything to the form element container
			formElement.append( fieldContainer );

			// Response
			response = formElement;

			// Save Form Element
			this.formElement = formElement;

			// Init libraries
			this.initLibraries();

			// Trigger methods on change
			this.onChange();

			// Trigger methods on focus
			this.onFocus();
		}
		else {
			// Error: Invalid field type
			console.error( vsprintf( 'The field type %s is invalid. The valid ones are %s', [ type, getFieldTypes().join( ', ' ) ] ) );
		}

		return response;
	}

	/**
	 * Set attributes. It add properties like hasValue, hasPlaceholder, etc, automatically
	 * 
	 * @since 0.4
	 *
	 * @param  {object} attributes - The field attributes
	 *
	 * @return {object} attributes - The field attributes
	 */

	setAttributes( attributes ){
		// Check if it's a checkbox and sanitaze the current value
		if ( attributes.fieldType == 'checkbox' && ! isEmpty( attributes.currentValue ) ){
			// Check if the value is "true" or "false"
			if ( [ 'true', 'false' ].includes( attributes.currentValue ) ){
				// Make it a boolean
				attributes.currentValue = attributes.currentValue == 'true' ? true : false;

				// If it's false, set hasValue to false
				if ( ! attributes.currentValue ){
					attributes.hasValue = false;
				}
				else {
					attributes.hasValue = true;
				}
			}
		}

		// Has value
		if ( ! isDefined( attributes.hasValue ) ){
			if ( ! isEmpty( attributes.currentValue ) ){
				attributes.hasValue = true;
			}
			else {
				attributes.hasValue = false;
			}
		}

		// Has placeholder
		if ( ! isDefined( attributes.hasPlaceholder ) ){
			if ( ! isEmpty( attributes.placeholder ) ){
				attributes.hasPlaceholder = true;
			}
			else {
				attributes.hasPlaceholder = false;
			}
		}

		// Has default value
		if ( ! isDefined( attributes.hasDefaultValue ) ){
			if ( ! isEmpty( attributes.defaultValue ) ){
				attributes.hasDefaultValue = true;
			}
			else {
				attributes.hasDefaultValue = false;
			}
		}

		// Has label
		if ( ! isDefined( attributes.hasLabel ) ){
			if ( ! isEmpty( attributes.label ) ){
				attributes.hasLabel = true;
			}
			else {
				attributes.hasLabel = false;
			}
		}

		// Has description
		if ( ! isDefined( attributes.hasDescription ) ){
			if ( ! isEmpty( attributes.description ) ){
				attributes.hasDescription = true;
			}
			else {
				attributes.hasDescription = false;
			}
		}

		// Is Required
		if ( ! isDefined( attributes.isRequired ) ){
			attributes.isRequired = false;
		}

		// Supports tokens
		if ( ! isDefined( attributes.supportsTokens ) ){
			attributes.supportsTokens = false;
		}

		// Check if it can support tokens
		if ( attributes.supportsTokens && attributes.fieldType == 'select' ){
			attributes.supportsTokens = false;
		}

		// Supports tinyMCE
		if ( ! isDefined( attributes.supportsTinyMCE ) ){
			attributes.supportsTinyMCE = false;
		}

		// Is Toggle
		if ( ! isDefined( attributes.isToggle ) && isDefined( attributes.toggle ) ){
			attributes.isToggle = true;
		}

		// Set the default value for isHidden
		if ( ! isDefined( attributes.isHidden ) ){
			attributes.isHidden = false;
		}

		return attributes;
	}

	/**
	 * Validate field
	 * 
	 * @since 0.4
	 *
	 * @param  {boolean} renderError - TRUE if you want to render the field errors, FALSE if you only want to return the response object
	 *
	 * @return {validateField} validateField object. Read the comments on Validation.js to learn more
	 */

	validate( renderError = true ){
		// Parameters

		// Field (DOM)
		let field = null,
			value;

		if ( renderError ){
			field = this.getFieldContainer();
			value = this.getValue();
		}
		else {
			value = this.attributes.currentValue;
		}

		// Type
		let fieldType = this.type;

		// Attributes
		let attributes = {
			supportsTokens: this.attributes.supportsTokens,
			required:  	    this.attributes.isRequired
		}

		// This uses the validateField function. Check Validation.js for more information
		return validateField( field, this.type, value, attributes, this.customValidationFunction );
	}

	/**
	 * Invoke functions on field change
	 * Those functions are global for all the Field objects
	 * Don't use directly, use 'automator:field:change' instead
	 */

	onChange(){
		this.formElement.on( 'automator:field:change', () => {
			// Check if it's an AJAX field, but the target field is a different field
			if ( this.attributes.isAjax && this.attributes.targetField !== this.attributes.optionCode ){
				this.doAJAX();
			}
		});
	}

	/**
	 * Invoke functions on field change
	 * Those functions are global for all the Field objects
	 * Don't use directly, use 'automator:field:change' instead
	 */

	onFocus(){
		this.field.on( 'focusin', ( event ) => {
			// Check if it's a read-only field
			if ( this.attributes.isReadOnly ){
				// Select the text
				$( event.currentTarget ).select();
			}
		});

		this.field.on( 'focusout', () => {});
	}

	/**
	 * Check if the item's object has all the required properties
	 *
	 * @since 0.5
	 */

	isReady(){
		let ready = false;

		if ( isDefined( UncannyAutomator.recipe.items[ this.attributes.itemId ] ) ){
			ready = true;
		}

		return ready;
	}

	/**
	 * Do AJAX
	 *
	 * @since 0.5
	 */

	doAJAX(){
		// Get field value. If it isn't defined in the DOM then get the default value
		let fieldValue = isDefined( this.getValue() ) ? this.getValue() : this.getMetaValue();

		// Check if we have to do an AJAX call on this field
		if ( this.attributes.isAjax ){
			// Get the target field
			let targetField = this;

			if ( this.attributes.optionCode != this.attributes.targetField ){
				targetField = getField( this.attributes.itemId, this.attributes.targetField );
			}

			// Check that we have everything
			if ( ! isEmpty( this.attributes.integrationId ) && ! isEmpty( this.attributes.endpoint ) ){
				// Before doing the request, show a loading animation in the changed field
				this.startLoadingAnimation();
				// And show animation in the target field too
				targetField.startLoadingAnimation();

				AJAXRequest( this.attributes.endpoint, {
					recipe_id:      UncannyAutomator.recipe.id,
					integration_id: this.attributes.integrationId,
					item_id: 	    this.attributes.itemId,			
					group_id: 	    this.attributes.groupId,
					field_id: 	    this.attributes.optionCode,
					target_field:   this.attributes.targetField,
					value: 		    this.getValue(),
					values: 		getCurrentFieldsValues( this.attributes.itemId )
				}, ( response ) => {
					// Stop loading animation in both fields
					this.stopLoadingAnimation();
					targetField.stopLoadingAnimation();

					let targetFieldAttributes = targetField.getAttributes();

					// Check if the target field is a select and the response is an array
					if ( targetFieldAttributes.fieldType == 'select' && $.isArray( response ) ){
						// The developer wants to set new options for a select
						targetField.setSelectOptions( response );
					}
					else {
						// Try to set the value
						targetField.setValue( response );
					}
				});
			}
		}
	}

	/**
	 * Get form element
	 * 
	 * @since 0.4
	 *
	 * @return {object} jQuery object of the field's container
	 */

	getFormElement(){
		if ( this.isReady() ){
			this.doAJAX();
		}

		return this.formElement;
	}

	/**
	 * Get field container
	 * 
	 * @since 0.4
	 *
	 * @return {object} jQuery object of the field's container
	 */

	getFieldContainer(){
		return this.fieldContainer;
	}

	/**
	 * Get field ID
	 * 
	 * @since 0.5
	 *
	 * @return {string} The field ID
	 */

	getFieldID(){
		return this.attributes.optionCode;
	}

	/**
	 * Get field
	 * 
	 * @since 0.4
	 *
	 * @return {object} jQuery object of the field
	 */

	getField(){
		return this.field;
	}

	/**
	 * Get attributes
	 * 
	 * @since 0.5
	 *
	 * @return {object} Field attributes
	 */

	getAttributes(){
		return this.attributes;
	}

	/**
	 * Get field's value FROM THE DOM ELEMENT
	 * If you want to get the value from the main object then check {attributes.currentValue}
	 * 
	 * @since 0.4
	 *
	 * @return {string|int} Value of the field
	 */

	getValue(){
		let response = null;

		if ( isDefined( this.field ) ){
			switch ( this.type ){
				case 'select':
					/*
					// Get the value of the custom field
					let fieldValue       = this.field.val(),
						customFieldValue = this.getCustomValue();

					response = {
						value:    ( fieldValue == 'automator_custom_value' ) ? customFieldValue : fieldValue,
						isCustom: fieldValue == 'automator_custom_value'
					};
					break;
					*/

				case 'text':
				case 'url':
				case 'email':
				case 'int':
				case 'float':
				case 'textarea':
					response = this.field.val();
					break;

				case 'checkbox':
					response = this.field.is( ':checked' );
					break;

				case 'radio':
					response = this.field.find( 'input:checked' ).val();
					break;
			}
		}

		return response;
	}

	/**
	 * Get custom value
	 * Only for selects
	 *
	 * @since  2.0.3
	 */
	
	getCustomValue(){

	}

	/**
	 * Get readable value
	 *
	 * @since 0.6
	 *
	 * @return string with the value
	 */

	getReadableValue(){
		let response = null,
			value 	 = this.getValue();

		switch ( this.type ){
			case 'select':
				response = this.field.find( vsprintf( 'option[value="%s"]', [ value ] ) ).text();
				break;

			case 'text':
			case 'url':
			case 'email':
			case 'int':
			case 'float':
			case 'textarea':
				// Create DOM element to remove the HTML tags
				value = $( '<p/>' ).html( value ).text();
				break;

			case 'checkbox':
				if ( value ){
					response = UncannyAutomator.i18n.trueLabel;
				}
				else {
					response = UncannyAutomator.i18n.falseLabel;
				}
				break;

			case 'radio':
				// Get checked radio
				const $checkedRadio = this.field.find( 'input:checked' );

				// Check if there is a checked radio
				if ( $checkedRadio.length == 1 ){
					// Get container
					const $radioContainer = $checkedRadio.closest( '.form-radio__container' );

					// Get text
					response = $radioContainer.text().trim();
				}
				break;
		}

		return response;
	}

	/**
	 * Get field's meta value
	 * 
	 * @since 0.5
	 *
	 * @return {string|int} Value of the field
	 */

	getMetaValue(){
		let response;

		// If the item is defined
		if ( isDefined( UncannyAutomator.recipe.items[ this.attributes.itemId ] ) ){
			let metaValue = UncannyAutomator.recipe.items[ this.attributes.itemId ].meta[ this.attributes.optionCode ];

			if ( isDefined( metaValue ) ){
				response = metaValue;
			}
		}

		return response;
	}

	/**
	 * Set value
	 * 
	 * @since 0.4
	 *
	 * @param {string} Value of the field
	 */

	setValue( value, doTrigger = true ){

		switch ( this.type ){
			case 'select':
				// Get option
				let option = this.field.find( 'option[value="' + value + '"]' );

				// Check if option exists
				if ( option.length > 0 ){
					// Get all options and unselect them
					let allOptions = this.field.find( 'option' );
					allOptions.prop( 'selected', false );

					// Select option
					option.prop( 'selected', true );

					// Trigger change
					if ( doTrigger ){
						this.field.trigger( 'automator:field:change' );
					}
				}
				else {
					console.error( vsprintf( 'The option with ID %s does not exists', [ value ] ) );
				}

				break;

			case 'text':
			case 'url':
			case 'email':
			case 'int':
			case 'float':
			case 'textarea':
				// Set value
				this.field.val( value );

				// Trigger change
				if ( doTrigger ){
					this.field.trigger( 'automator:field:change' );
				}
				break;

			case 'checkbox':
				let wantsToCheck = value, // Just for readability
					isChecked = this.getValue();

				// If true, check
				if ( wantsToCheck && ! isChecked ){
					this.field.prop( 'checked', true );
				}

				if ( ! wantsToCheck && isChecked ) {
					this.field.prop( 'checked', false );
				}

				if ( doTrigger && ( ( wantsToCheck && ! isChecked ) || ( ! wantsToCheck && isChecked ) ) ){
					// Trigger change
					this.field.trigger( 'automator:field:change' );
				}

				break;

			case 'radio':
				// Search for the radio and check it
				this.field.filter( `[name="${ value }"]` ).prop( 'checked', true );

				// Trigger change
				if ( doTrigger ){
					// Trigger change
					this.field.trigger( 'automator:field:change' );
				}

				break;
		}
	}

	/**
	 * Check if a select has a selected option
	 *
	 * @since 0.5
	 */

	hasSelectedOption(){
		return this.selectHasSelectedOption;
	}

	/**
	 * Set select options
	 *
	 * @since 0.5
	 *
	 * @param {array} 
	 */

	setSelectOptions( options ){
		// Remove current options
		this.removeSelectOptions();

		if ( isDefined( this.field ) ){
			if ( options.length > 0 ){
				// Remove disabled
				this.field.prop( 'disabled', false );

				// Get value
				let value = isDefined( this.getValue() ) ? this.getValue() : this.getMetaValue();

				// Create array to save all options
				let optionsDOM = [];

				// Create and append options
				$.each( options, ( index, option ) => {
					let optionDOM = new Option( option.text, option.value, false, false );

					this.field.append( optionDOM );
					optionsDOM.push( optionDOM );
				});

				if ( ! isEmpty( value ) ){
					$.each( optionsDOM, ( index, option ) => {
						if ( option.value == value ){
							$( option ).prop( 'selected', true );
						}
					});
				}
			}
			else {
				let noResults = new Option( UncannyAutomator.i18n.noResults, '', true, true );
				this.field.append( noResults );
				this.field.prop( 'disabled', true );
			}

			this.field.trigger( 'change' );
		}
	}

	/**
	 * Remove select options
	 *
	 * @since 0.5
	 */

	removeSelectOptions(){
		if ( isDefined( this.field ) ){
			this.field.empty();
		}
	}

	/**
	 * Check if field has loading animation
	 * 
	 * @since 0.4
	 *
	 * @return {boolean} TRUE if the field has the loading animation
	 */

	isLoading(){
		return this.formElement.hasClass( 'form-element--loading' );
	}

	/**
	 * Add loading animation to field
	 * 
	 * @since 0.4
	 */

	startLoadingAnimation(){
		// Check if has class
		let CSSclass = 'form-element--loading';

		if ( isDefined( this.formElement ) && ! this.formElement.hasClass( CSSclass ) ){
			
			// Add class
			this.formElement.addClass( CSSclass );

			// Check if it's a checkbox
			if ( this.type == 'checkbox' && ! isDefined( this.attributes.isToggle ) ){
				this.fieldContainer.addClass( 'form-checkbox--loading-inline' );
			}

			// Trigger
			this.formElement.trigger( 'automator:field:loading' );
		}
	}

	/**
	 * Remove loading animation to field
	 * 
	 * @since 0.4
	 */

	stopLoadingAnimation(){
		// Check if has class
		let CSSclass = 'form-element--loading';

		if ( isDefined( this.formElement ) && this.formElement.hasClass( CSSclass ) ){
			
			// Remove class
			this.formElement.removeClass( CSSclass );

			// Remove checkbox class
			this.fieldContainer.removeClass( 'form-checkbox--loading-inline' );

			// Trigger
			this.formElement.trigger( 'automator:field:loading' );
		}
	}

	/**
	 * Init field libraries, like Select2
	 *
	 * @since 0.5
	 */

	initLibraries(){
		switch ( this.type ){
			case 'select':
				// Select2
				this.initSelect2( this.field );
				break;

			case 'text':
			case 'url':
			case 'email':
			case 'int':
			case 'float':
			case 'textarea':
				// Tokens
				if ( this.attributes.supportsTokens ){
					this.initTokenSelector( this.fieldContainer );
				}

				// URL Checker
				if ( this.attributes.fieldType == 'url' ){
					initURLChecker( this.fieldContainer );
				}

				break;
		}
	}

	/**
	 * Init Select2
	 * 
	 * @since 0.5
	 */

	initSelect2( $field ){
		// Define select2 settings
		let settings = {
			theme: 'default uap-select2',
	        searchInputPlaceholder: UncannyAutomator.i18n.search,
	        language: {
	            noResults: function () {
	                return UncannyAutomator.i18n.noResults;
	            },
	        },
            templateResult: function ( data ) {
                if ( typeof data.id !== "undefined" ) {
                    if ( Number.isInteger( parseInt( data.id ) ) && parseInt( data.id ) > 0 ) {
                        return '<span class="uap-select2-item__meta">(ID: ' + data.id + ')</span> ' + data.text;
                    } else {
                        return data.text;
                    }
                } else {
                    return data.text;
                }
            },
            templateSelection: function ( data ) {
                if ( typeof data.id !== "undefined" ) {
                    if ( Number.isInteger( parseInt( data.id ) ) && parseInt( data.id ) > 0 ) {
                        return '<span class="uap-select2-item__meta">(ID: ' + data.id + ')</span> ' + data.text;
                    } else {
                        return data.text;
                    }
                } else {
                    return data.text;
                }
            },
            escapeMarkup: function ( markUp ) {
                return markUp;
            }
	    };

	    // Check if we have to add a placeholder
		if ( this.attributes.hasPlaceholder ){
			// Add placeholder
			settings.placeholder = this.attributes.placeholder;
		}

		if ( ! $field.hasClass( 'select2-hidden-accessible' ) ){
			$field.select2( settings );
		}
	}

	/**
	 * Init Token Selector
	 * This is temporal and will be removed in the next version
	 *
	 * @since 0.5
	 */

	initTokenSelector( $input ){
	    // Get all inputs with token selectors and init
	    let tokens = getTokens( this.attributes.itemType, this.attributes.fieldType );
	    $input.TokenSelector( tokens );
	}

	/**
	 * Init TinyMCE
	 * We're going to call this in ItemOptions.js because TinyMCE needs the item in the DOM before changing it
	 *
	 * @since 0.5
	 */

	initTinyMCE(){
		let $textarea = this.field;

		// TinyMCE
		if ( isDefined( this.field ) && this.attributes.fieldType == 'textarea' && this.attributes.supportsTinyMCE ){
			// Get field ID
			const fieldId = this.field.prop( 'id' );

			// Get editor
	        let editor = tinymce.get( fieldId );

	        if ( isDefined( editor ) ){
	        	wp.editor.remove( fieldId );
	        }

			wp.editor.initialize( fieldId, {
				mediaButtons: true,
				tinymce: { 
					wpautop: true, 
					toolbar1: 'formatselect bold italic bullist numlist blockquote alignleft aligncenter alignright link unlink wp_more',
					toolbar2: 'strikethrough hr forecolor pastetext removeformat visualchars outdent indent undo redo'
				}, 
				quicktags: true,
			});

			// Get editor
	        editor = tinymce.get( fieldId );
	        
	        editor.on( 'input change paste', () => {
	        	this.field.text( editor.getContent() );
	        });
		}
	}
}

export default Field;

/**
 * Extend Select2 to add a placeholder in the search input
 *
 * @since 0.1
 *
 * @param   {object} Select2 Instance
 *
 * @return  {object} Select2 Instance
 */

(function ($){
    // Get default settings
    var Defaults = $.fn.select2.amd.require( 'select2/defaults' );

    // Set default value for searchInputPlaceholder
    $.extend(Defaults.defaults, {
        searchInputPlaceholder: ''
    });

    // Get search element
    var SearchDropdown = $.fn.select2.amd.require( 'select2/dropdown/search' );

    // Save instance of search element
    var _renderSearchDropdown = SearchDropdown.prototype.render;

    SearchDropdown.prototype.render = function ( decorated ){
        // Add placeholder attribute
        var $rendered = _renderSearchDropdown.apply( this, Array.prototype.slice.apply( arguments ) );
        this.$search.attr( 'placeholder', this.options.get( 'searchInputPlaceholder' ) );

        // Return search element
        return $rendered;
    };
})(window.jQuery);

/*** To-do: Move to the Field class as a method **/

(function( $ ){
    $.fn.TokenSelector = function( fieldTokens ){
        return this.each( function(){
            let $this = $(this);

            let settings = {
                noResults: UncannyAutomator.i18n.noResults,
                responsive: {
                    margin: 30, // min space the dropdown and the limit of the viewport
                },
            };

            let fieldElementType = $this.hasClass( 'form-input' ) ? 'input' : 'textarea';

            // Define CSS classes
            settings.classes = {};

            switch ( fieldElementType ){
                case 'input':
                    settings.classes.tokenReady  = 'form-input--token-ready';
                    settings.classes.field       = 'form-element__input';
                    settings.classes.hiddenField = 'form-element__input--hidden';
                    break;

                case 'textarea':
                    settings.classes.tokenReady  = 'form-textarea--token-ready';
                    settings.classes.field       = 'form-element__textarea';
                    settings.classes.hiddenField = 'form-element__textarea--hidden';

                    let $field = $this.find( 'textarea' );
                    settings.isTinyMCEinstance   = $field.data( 'supportstinymce' );
                    settings.TinyMCEinstanceID   = $field.prop( 'id' );
                    break;
            }

            // Init
            if ( ! $this.hasClass( settings.classes.tokenReady ) ){

                let itemType = $this.find( '.' + settings.classes.field ).data( 'item_type' );

                let tokens = {
                    list: fieldTokens,

                    getTokenList: function(){
                        return this.list;
                    },

                    hasTokens: function(){
                        let list = this.list;
                        return ! $.isEmptyObject( list );
                    }
                }

                let tokensList = tokens.getTokenList();

                if ( tokens.hasTokens() ){
                    let render = {
                        config: {
                            inputHeight: 35,
                        },

                        init: function(){
                            this.createElement();
                        },

                        createElement: function(){
                            let mainFormElement = $this,
                                input = mainFormElement.find( '.' + settings.classes.field );

                            let fieldType = input.data( 'type' );
                            /* Dropdown section */

                            let formDropdown = mainFormElement.find( '.form-variable__dropdown' ),
                                formDropdownContainer = $( '<div/>', { 'class': 'form-variable__dropdown-container' }),
                                formDropdownSearch = $( '<div/>', { 'class': 'form-variable__search' }),
                                formDropdownSearchInput = $( '<input/>', {
                                	'class': 'form-variable__search-input', 'placeholder': UncannyAutomator.i18n.tokens.search
                                });

                            formDropdownSearch.append( formDropdownSearchInput );

                            // Token groups

                            let formGroups = $('<div/>', { 'class': 'form-variable__groups' });

                            $.each( tokensList, function( tokenGroupID, tokenGroup ){
                                let groupContainer = $('<div/>', { 'class': 'form-variable__group' });

                                /* Group - header */

                                let groupSource = $('<div/>', { 'class': 'form-variable__group-source' }),
                                    groupIcon = $('<div/>', { 'class': 'form-variable-group-source__icon' }),
                                    groupName = $('<div/>', { 'class': 'form-variable-group-source__description' }),
                                    groupToggle = $('<div/>', { 'class': 'form-variable-group-source__toggle' }),
                                    groupToggleIcon = $('<span/>', { 'class': 'form-variable-group-source__toggle-icon' });

                                // Create the variable where we're going to save the icon
                                let $tokenIcon;

                                if ( isDefined( UncannyAutomator.integrations[ tokenGroup.integrationId ] ) ){
                                     // Search for an icon
                                    let integration = UncannyAutomator.integrations[ tokenGroup.integrationId ];

									// Check if it has a svg image
									if ( isDefined( integration.icon_svg ) ){
										$tokenIcon = $( `<img src="${ integration.icon_svg }" alt="${ integration.name }">` );
									}
									// Check if it has a png image
									else if ( isDefined( integration.icon_16 ) && isDefined( integration.icon_32 ) ){
										$tokenIcon = $( `<img src="${ integration.icon_16 }" srcset="${ vsprintf( '%s 1x, %s 2x', [ integration.icon_16, integration.icon_32 ]) }" alt="${ integration.name }">` );
									}
                                }
                                else {
                                    // Use default icon
                                    $tokenIcon = $('<i/>', { 'class': 'uo-icon ' + tokenGroup.icon });
                                }

                                groupIcon.append( $tokenIcon );

                                // Name
                                groupName.append( tokenGroup.name );

                                // Toggle
                                groupToggle.append( groupToggleIcon );

                                // Append everything to main container
                                groupSource.append( groupIcon );
                                groupSource.append( groupName );
                                groupSource.append( groupToggle );

                                /* Tokens */
                                let tokenGroups = $('<span/>', { 'class': 'form-variable__group-children' });

                                $.each( tokenGroup.tokens, function( individualTokenID, individualToken ){
                                    let tokenItem = $('<div/>', {
                                    	'class':      'form-variable__group-item',
                                    	'data-token': '{{' + individualToken.id + '}}',
                                    	'data-type':  individualToken.type });

                                    tokenItem.html( individualToken.name );
                                    tokenGroups.append( tokenItem );
                                });

                                groupContainer.append( groupSource );
                                groupContainer.append( tokenGroups );

                                formGroups.append( groupContainer );
                            });

                            let noResults = $( '<div/>', { 'class': 'form-variable__group-item-no-results form-variable__group-item-no-results--hide' } );
                            noResults.text( settings.noResults );
                            formGroups.append( noResults );

                            // Add containers to main dropdown container
                            formDropdownContainer.append( formDropdownSearch );
                            formDropdownContainer.append( formGroups );
                            formDropdown.append( formDropdownContainer );

                            mainFormElement.addClass( settings.classes.tokenReady );
                        },

                        getPosition: function(){
                            let usefulElements = {
                                window: $(window)
                            }

                            let space = {
                                above: elements.container.offset().top - usefulElements.window.scrollTop(),
                                below: usefulElements.window.height() - ( elements.container.offset().top - usefulElements.window.scrollTop() ) - this.config.inputHeight,
                            }

                            elements.dropdown.container_relative.addClass('uo-realsize');
                            let dropDownHeight = elements.dropdown.container_relative.height() + 75; // + 75px to show at least 1 option
                            elements.dropdown.container_relative.removeClass('uo-realsize');

                            // Show dropdown always below for textareas
                            if ( fieldElementType == 'textarea' ){
                                space.hasSpaceBelow = true;
                            }
                            else {
                                space.hasSpaceBelow = space.below >= dropDownHeight;
                            }

                            return space;
                        },

                        setDirection: function(){
                            let position = this.getPosition();

                            // Reset before adding another class

                            elements.container.removeClass( 'form-variable--above form-variable--below' );

                            // Decide

                            if ( position.below > position.above || position.hasSpaceBelow ){
                                // Let's show it below
                                elements.container.addClass( 'form-variable--below' );
                                status.position = 'below';
                            }
                            else {
                                // Let's show it above
                                elements.container.addClass( 'form-variable--above' );
                                status.position = 'above';
                            }
                        },

                        setScroll: function(){
                            if ( status.open ){
                                let maxHeight = this.getPosition()[ status.position ] - this.config.inputHeight - settings.responsive.margin;
                                elements.dropdown.options.container.css({ 'max-height' : maxHeight + 'px' });
                            }
                        },

                        addToken: function( token ){
                        	let cursorPosition 	  = elements.input.get( 0 ).selectionStart,
                        		newCursorPosition = cursorPosition + token.length;

                            if ( settings.isTinyMCEinstance ){
                                // TinyMCE
                                let instance = tinymce.get( settings.TinyMCEinstanceID );

                                if ( isDefined( instance ) ){
                                	// Remove possible spaces from token
                                	token = removeSpaces( token );

                                    // Check if "Text" view is selected
                                    if ( elements.input.is( ':visible' ) ){
                                        // Get content
                                        let currentValue = elements.input.val(),
                                        	newValue 	 = currentValue.substr( 0, cursorPosition ) + token + currentValue.substr( cursorPosition );

                                        // Set new value and focus
		                                elements.input.val( newValue ).focus();

		                                // Trigger input event
		                                elements.input.trigger( 'input' );

		                                // Set position of the cursor
		                                elements.input.get( 0 ).setSelectionRange( newCursorPosition, newCursorPosition );
                                    }
                                    else {
                                    	// Insert token
                                    	instance.execCommand( 'mceInsertContent', false, token );
                                    }
                                }
                            }
                            else {
                                // A normal HTML form element
                                let currentValue 	  = elements.input.val(),
                                	newValue 	 	  = currentValue.substr( 0, cursorPosition ) + token + currentValue.substr( cursorPosition );

                                // Set new value and focus
                                elements.input.val( newValue ).focus();

                                // Trigger input event
                                elements.input.trigger( 'input' );

                                // Set position of the cursor
                                elements.input.get( 0 ).setSelectionRange( newCursorPosition, newCursorPosition );
                            }

                            this.hideDropdown();
                            this.focusInput();
                        },

                        resetDropdown: function(){
                            elements.dropdown.options.groups.removeClass( 'form-variable__group--hide form-variable__group--open' );
                            elements.dropdown.options.groups.removeClass( 'form-variable__group--open' );
                            elements.dropdown.options.items.removeClass( 'form-variable__group-item--hide' );
                            elements.dropdown.options.no_results.addClass( 'form-variable__group-item-no-results--hide' );
                        },

                        hideDropdown: function(){
                            elements.container.removeClass( 'form-variable--open' );

                            // Save status
                            status.open = false;

                            // Rest input value and trigger 'input' event
                            elements.dropdown.search_input.val('').trigger('input');
                        },

                        focusInput: function(){
                            elements.input.focus();
                        },
                    }

                    let status = {
                        open: false,
                        position: null
                    }

                    let bind = {
                        init: function(){
                            this.toggles();
                            this.insert_tokens();
                            this.search();
                            this.resize();
                            this.mirrorContent();
                        },

                        toggles: function(){
                            /* Main dropdown */
                            elements.dropdown.button.on( 'click', () => {
                                elements.container.toggleClass( 'form-variable--open' );

                                // Get and save status
                                status.open = elements.container.hasClass( 'form-variable--open' );

                                // If the user tried to opened it, bind click outside
                                if ( status.open ){
                                	// Listen clicks outside
                                	this.bindClicksOutside();
                                }
                                else {
                                	// Otherwise, remove the listener
                            		$( document ).off( 'mouseup.uap-token-selector-click-outside' );
                                }

                                // Rest input value and trigger 'input' event
                                elements.dropdown.search_input.val('').trigger('input');

                                // Focus search
                                elements.dropdown.search_input.focus();

                                // Decide position
                                render.setDirection();

                                // Set scroll
                                render.setScroll();
                            });

                            /* Each group */
                            elements.dropdown.options.groups_title.on( 'click', function(){
                                let $this = $(this),
                                    group = $this.closest('.form-variable__group');

                                group.toggleClass( 'form-variable__group--open' );
                            });
                        },

                        bindClicksOutside: function(){
                        	// Bind clicks outside
                        	// Add event listener to listen clicks outside
	                        $( document ).on( 'mouseup.uap-token-selector-click-outside', function( event ){
	                            // Set the targetted container
	                            let container = elements.container;

	                            if ( ! container.is( event.target ) && container.has( event.target ).length === 0 ){
	                                // Hide bubble
	                                elements.container.removeClass( 'form-variable--open' );

		                            // Save status
		                            status.open = false;

		                            // Rest input value and trigger 'input' event
		                            elements.dropdown.search_input.val('').trigger('input');
	                            }
	                        });
                        },

                        resize: function(){
                            $(window).resize( function(){
                                render.setScroll();
                            });
                        },

                        insert_tokens: function(){
                            /* Insert tokens */

                            elements.dropdown.options.items.on( 'click', function(){
                                let $this = $(this),
                                    token = $this.data('token');

                                render.addToken( token );
                            });
                        },

                        search: function(){
                            elements.dropdown.search_input.on( 'input', function(){
                                let $this = $(this),
                                    value = $this.val();

                                if ( value != '' ){
                                    // Search

                                    // Prepare string. Remove spaces and convert all characters to lowercase
                                    value = value.toLowerCase().replace(/\s/g, '');

                                    // Open all groups
                                    elements.dropdown.options.groups.addClass( 'form-variable__group--open' );

                                    // Hide all groups and options
                                    elements.dropdown.options.groups.addClass( 'form-variable__group--hide' );
                                    elements.dropdown.options.items.addClass( 'form-variable__group-item--hide' );

                                    // Hide "No Results" container
                                    elements.dropdown.options.no_results.addClass( 'form-variable__group-item-no-results--hide' );

                                    // Create variable to search if there were results
                                    let hasResults = false;

                                    // Search matches and show group if there is at least one result
                                    elements.dropdown.options.items.each( function(){
                                        let $this = $(this),
                                            optionName = $this.text().toLowerCase().replace(/\s/g, '');

                                        if ( optionName.search( value ) !== -1 ){
                                            // Match! show option and group

                                            // Find group first
                                            let group = $this.closest( '.form-variable__group' );

                                            // Show group
                                            group.removeClass( 'form-variable__group--hide' );

                                            // Show option
                                            $this.removeClass( 'form-variable__group-item--hide' );

                                            // Change hasResults
                                            hasResults = true;
                                        }
                                    });

                                    // Check if there results, if not, then show no results container
                                    if ( ! hasResults ){
                                        elements.dropdown.options.no_results.removeClass( 'form-variable__group-item-no-results--hide' );
                                    }
                                }
                                else {
                                    // Reset
                                    render.resetDropdown();
                                }
                            });
                        },

                        mirrorContent: function(){
                            /*
                            elements.input.on( 'input', function(){
                                elements.input.val( elements.input.val() );
                            });
                            */
                        }
                    }

                    render.init();

                    let elements = {
                        container:              $this.find( '.form-variable' ),
                        input_area:             $this.find( '.form-variable__element' ),
                        input:                  $this.find( '.' + settings.classes.field ),
                        dropdown: {
                            container:          $this.find( '.form-variable__dropdown' ),
                            container_relative: $this.find( '.form-variable__dropdown-container' ),
                            button:             $this.find( '.form-variable__toggle' ),
                            search_input:       $this.find( '.form-variable__search-input' ),
                            options: {
                                container:      $this.find( '.form-variable__groups' ),
                                groups:         $this.find( '.form-variable__group' ),
                                groups_title:   $this.find( '.form-variable__group-source' ),
                                items:          $this.find( '.form-variable__group-item' ),
                                no_results:     $this.find( '.form-variable__group-item-no-results' ),
                            },
                        }
                    }

                    bind.init();
                }
                else {
                    $this.find( '.form-variable__element' ).addClass( 'form-variable__element--empty' );
                }
            }

            return this;
        });
    }
}( jQuery ));


