import {
    getDOMElements
}                              from './Utilities.js';
import ItemCreatorIntegrations from './ItemCreatorIntegrations.js';

class ItemCreatorAdd {
    constructor( itemType ){
        let $container    = $( '<div/>', { 'class': 'item item--start' }),
            $icon         = $( '<div/>', { 'class': 'item-icon' }),
            $containerBox = $( '<div/>', { 'class': 'item-title clear' }),
            $button       = $( '<div/>', { 'class': 'uap-btn uap-btn--secondary uap-btn--small' });

        $containerBox.append( $button );
        $container.append( $icon );
        $container.append( $containerBox );

        // Add to the metabox
        let $metabox = getDOMElements( 'metabox', {
            type: itemType
        });

        $metabox.append( $container );

        // Add text depending of the itemType
        if ( itemType == 'trigger' ){
            $button.text( UncannyAutomator.i18n.trigger.add );
        }

        if ( itemType == 'action' ){
            $button.text( UncannyAutomator.i18n.action.add );
        }

        $button.on( 'click', () => new ItemCreatorIntegrations( itemType ) );
    }
}

export default ItemCreatorAdd;


