import {
    isEmpty,
    getDOMElements,
    vsprintf,
} from './Utilities.js';

import ItemHeaderMenu     from './ItemHeaderMenu.js';
import ItemHeaderIcon     from './ItemHeaderIcon.js';
import ItemHeaderTag      from './ItemHeaderTag.js';
import ItemHeaderSentence from './ItemHeaderSentence.js';

class MetaboxItems {
    constructor( itemType ){
        $.each( UncannyAutomator.recipe.items, ( itemId, item ) => {
            if ( itemType == item.itemType ){
                // Create item
                let item = this.createItem( itemId );

                // Append to the metabox
                let metabox = getDOMElements( 'metabox', {
                    type: itemType
                });

                metabox.append( item );
            }
        });
    }

    createItem( itemId ){
        // Get item data
        let item = UncannyAutomator.recipe.items[ itemId ];

        // Create container
        let container = $( '<div/>', {
            'class':   'item',
            'data-id': itemId
        });

        if ( item.isMissing ){
            container.addClass( 'item--missing' );
        }

        // Replace tokens in the CSS
        if ( ! isEmpty( item.inlineCss ) ){
            item.inlineCss = item.inlineCss.replace( /{{item_id}}/g, itemId );

            // Add inline inlineCss
            container.prepend( item.inlineCss );
        }

        // Create header
        let header = this.createHeader(
            item.integrationCode,
            item.itemType,
            item.itemCode,
            itemId,
        );

        let notice = this.createNotice( item );

        container.append( header );
        container.append( notice );

        // Debug
        // console.groupCollapsed( vsprintf( 'ID: %s', [ itemId ] ) );
        // console.log( item );
        // console.groupEnd();

        return container;
    }

    /**
     * Creates item header
     * 
     * @since 0.5
     *
     * @param {string}    integrationCode - The integration code
     * @param {string}    itemType - The item type: 'trigger', 'action' or 'closure'
     * @param {string}    itemCode - The code of the item
     * @param {int}       itemId - The ID of the item
     */

    createHeader( integrationCode, itemType, itemCode, itemId ){
        let container = $( '<div/>' ),
            title     = $( '<div/>', { 'class': 'item-title clear' });

        // Get item data
        let item = UncannyAutomator.recipe.items[ itemId ];

        // If the item is missing remove the integrationCode
        // We don't want to show the integration icon
        if ( item.isMissing ){
            integrationCode = null;
        }

        let actions = new ItemHeaderMenu( itemType, integrationCode, itemCode, itemId );
        container.append( actions );

        let icon    = new ItemHeaderIcon( integrationCode );
        container.append( icon );

        // Integration name for missing items
        let integrationName = item.integrationName;
        
        let itemTag = new ItemHeaderTag( integrationCode, integrationName );
        title.append( itemTag );

        let titleSentence = new ItemHeaderSentence( itemType, integrationCode, itemCode, itemId );
        title.append( titleSentence );
        container.append( title );

        let itemContent = $( '<div/>', { 'class': 'item__content' });
        container.append( itemContent );

        return container;
    }

    /**
     * Create item notice
     *
     * @since 0.6
     *
     */

    createNotice( item ){
        let container;

        if ( item.isMissing ){
            container = $( '<div/>', { 'class': 'item-notice' });
            container.text( UncannyAutomator.i18n.itemMissing );
        }

        return container;
    }
}

export default MetaboxItems;


