import {
    restCall,
    getGlobalItem,
    getDOMElements,
    parseStringWithTokens,
    isEmpty,
    isDefined,
    replaceCurlyBraces
} from './Utilities.js';

import Modal       from './Modal.js';
import ItemOptions from './ItemOptions.js';
import Field       from './Field.js';
import Triggers    from './Triggers.js';
import Actions     from './Actions.js';

import {
    validateItem
} from './Validation.js';

import RecipeNotices from './RecipeNotices.js';

class ItemHeaderSentence {
    constructor( itemType, integrationID, itemCode, itemId ){
        this.itemType = itemType;
        this.integrationID = integrationID;
        this.itemCode = itemCode;
        this.itemId   = itemId;
        this.item     = UncannyAutomator.recipe.items[ itemId ];

        return this.createItemSentence( itemType, itemCode, itemId );
    }

    /**
     * Create item sentence + status toggle
     * 
     * @since 0.5     
     *
     * @return {object}   jQuery DOM object with the item sentence + status toggle
     */

    createItemSentence( itemType, itemCode, itemId ){
        // Get information about the global item
        let globalItem = getGlobalItem( this.integrationID, itemType, itemCode );
        this.globalItem = globalItem;

        // Get sentence
        let itemSentence;

        if ( ! this.item.isMissing ){
            itemSentence = getGlobalItem( this.integrationID, this.item.itemType, this.item.itemCode ).sentence;
        }
        else {
            itemSentence = this.item.sentence;
        }

        itemSentence = parseStringWithTokens( this.item, itemSentence, UncannyAutomator.recipe.items[ this.itemId ].meta );

        // Bind elements
        /*
        itemSentence.elements.normal.on( 'click', function(){
            //
        });
        */

        if ( ! this.item.isMissing ){
            // Listen clicks on tokens
            itemSentence.elements.token.on( 'click', ( event ) => {
                // Get token element
                const $token = $( event.currentTarget );

                // Remove "open" class from al items
                itemSentence.elements.token.removeClass( 'item-title__token--open' );
                // Add "open" class to the clicked token
                $token.addClass( 'item-title__token--open' );

                // Get options ID
                let optionsID = $token.data( 'options-id' );

                // Get option group
                let optionGroupDOM = getDOMElements( 'optionGroup', {
                    itemID:  itemId,
                    groupID: optionsID,
                });

                // Create and show
                new ItemOptions( itemId, optionsID );
            });
        }
            
        // Create DOM elements
        let container = $( '<div/>', { 'class': 'item-title__describe-container' }),
            status    = $( '<div/>', { 'class': 'item-status' });

        container.append( itemSentence.html.addClass( 'item-title__describe' ) );

        // Create toggle. We're using Field. Read Field.js comments for more information
        var itemToggle = new Field( 'checkbox', {
            color: 'green',
            align: 'right',
            toggle: {
                on:  UncannyAutomator.i18n.status.liveRecipeItem,
                off: UncannyAutomator.i18n.status.draftRecipeItem,
            },
            currentValue: UncannyAutomator.recipe.items[ itemId ].isLive
        });

        let itemStatusToggle = itemToggle.getFormElement();
            status.append( itemStatusToggle );
            container.append( status );

        if ( ! this.item.isMissing ){
            itemStatusToggle.on( 'automator:field:change', function(){
                let makeLive = itemToggle.getValue();
                var isValidItem = true; // True by default, will change to false is it's invalid

                // Check if the user wants to make it live
                if ( makeLive ){
                    // Validate
                    let validation = validateItem( itemId );
                    isValidItem = validation.isValid; 
                }

                // Do AJAX
                // Show loading animation
                itemToggle.startLoadingAnimation();

                if ( isValidItem ){
                    // Set post status
                    let post_status = makeLive ? 'publish' : 'draft';

                    // Check if they want to make an item draft in a live recipe
                    if ( ! makeLive && UncannyAutomator.recipe.isLive ){
                        // Create modal box
                        // Read Modal.js comments for more information
                        let modal = new Modal({
                            title:       UncannyAutomator.i18n.confirmationModal.title,
                            warning:     replaceCurlyBraces( UncannyAutomator.i18n.confirmationModal.statusWarning, '<strong>', '</strong>' ),
                            buttons: {
                                cancel:  UncannyAutomator.i18n.cancel,
                                confirm: UncannyAutomator.i18n.confirm,
                            }
                        });

                        modal.setEvents({
                            afterSetEvents: function(){
                                modal.show();
                            },
                            onConfirm: function(){
                                let elements        = modal.getElements();
                                let confirmButton   = elements.modal.box.buttons.confirm;

                                // Add loading class to button
                                confirmButton.addClass( 'uap-btn--loading' ).prop( 'disabled', true );

                                // Start loading animation on toggle
                                itemToggle.startLoadingAnimation();

                                restCall( 'change_post_status', {
                                    post_ID:      itemId,
                                    post_status:  post_status
                                }, function(){
                                    // On success
                                    // Destroy modal
                                    modal.destroy();

                                    // Refresh UI
                                    new Triggers();
                                    new Actions();

                                    // Show error
                                    new RecipeNotices();

                                    // Check if the recipe is still valid, if it isn't then make the recipe draft
                                    if ( ! UncannyAutomator.recipe.isValid ){
                                        UncannyAutomator.recipe.publish.statusToggle.setValue( false );
                                    }
                                }, function(){
                                    // On fail
                                    console.log( ' ERROR-TODO. Couldn\'t set the item status' );

                                    // Destroy modal
                                    modal.destroy();

                                    // Remove loading animation, data didn't change
                                    itemToggle.stopLoadingAnimation();

                                    // Revert user change
                                    itemToggle.setValue( ! makeLive, false );
                                });
                            },
                            onCancel: function(){
                                // Destroy modal
                                modal.destroy();

                                // Revert user change
                                itemToggle.setValue( ! makeLive, false );

                                // Stop loading animation in the toggle
                                itemToggle.stopLoadingAnimation();
                            },
                            afterHide: function(){
                                modal.destroy();
                            }
                        });
                    }
                    else {
                        // Is valid, make live
                        restCall( 'change_post_status', {
                            post_ID:      itemId,
                            post_status:  post_status
                        }, function(){
                            // On sucess
                            // Update object

                            // Refresh UI
                            // Debugging! don't re-render the UI
                            // new Triggers();
                            // new Actions();

                            // Remove loading animation, data didn't change
                            itemToggle.stopLoadingAnimation();

                            // Show error
                            new RecipeNotices();
                        }, function(){
                            // On fail
                            console.log( ' ERROR-TODO. Couldn\'t set the item status' );

                            // Remove loading animation, data didn't change
                            itemToggle.stopLoadingAnimation();

                            // Revert user change
                            itemToggle.setValue( ! makeLive, false );
                        });
                    }
                }
                else {
                    // JS won't take more than some milliseconds to do this, so we're going to wait one second just for the user to understand what's happening
                    setTimeout(function(){
                        // Remove loading animation
                        itemToggle.stopLoadingAnimation();

                        // Revert user change
                        itemToggle.setValue( false, false );
                    }, 1000 );
                }
            });
        }

        return container;
    }
}

export default ItemHeaderSentence;


