import {
	isDefined
} from './Utilities.js';

/**
 * Create a new modal box
 *
 * @since 0.4
 */

class Modal {
	/*
	 * Construct method
	 *
	 * @since 0.4
	 *
	 * @param  {object} settings
	 * @param  {object} [settings.content]
	 * @param  {string} [settings.content.title] - Title of the metabox
	 * @param  {string} [settings.content.warning] - Content of warning in the metabox
	 * @param  {object} [settings.content.buttons]
	 * @param  {string} [settings.content.buttons.cancel] - Text of the cancel button
	 * @param  {string} [settings.content.buttons.confirm] - Text of the confirm button
	 * @param  {object} [settings.events.onConfirm] - Function that is going to be invoked when the user press the confirm button
	 * @param  {object} [settings.events.onCancel] - Function that is going to be invoked when the user press the cancel button
	 * @param  {object} [settings.events.onShow] - Function that is going to be invoked when the modal box is shown
	 * @param  {object} [settings.events.onHide] - Function that is going to be invoked when the modal box is hidden
	 * 
	 * @return {object} - The modal box object
	 */

	constructor( content, config = {} ){
		// Create elements object
		this.elements = {
			container: null,
			automatorContainer: null, // ( .uap ) This DIV is only created to avoid conflicts with other plugins
			modal: {
				container: null,
				box: {
					container: null,
					title: null,
					warning: null,
					buttons: {
						container: null,
						cancel: null,
						confirm: null
					}
				}
			}
		}

		// Set container
		this.elements.container = $( 'body' );

		// Set isVisible property
		this.isVisible = false;

		// Define default configuration
		this.config = $.extend( true, {
			fadeDuration: 150,
		}, config );

		// Save modal box content. Add default values
		this.content = $.extend( {}, {
			buttons: {
				cancel: UncannyAutomator.i18n.cancel,
			}
		}, content );

		// Create new modal box
		this.create();

		// Return object
		return this;
	}

	/*
	 * Create modal box DOM elements
	 *
	 * @since 0.4
	 */

	create(){
		let automatorContainer = $( '<div/>', { 'class': 'uap' });
		let modal = $( '<div/>', { 'class': 'modal' });
		let modalBox = $( '<div/>', { 'class': 'modal__box' });

		// Check if the user wants a different modal size than the default size
		if ( isDefined( this.config.size ) && [ 'small', 'medium', 'large' ].includes( this.config.size ) ){
			// Add the correct size class
			modal.addClass( `modal--${ this.config.size }` );
		}

		// Save elements
		this.elements.automatorContainer = automatorContainer;
		this.elements.modal.container = modal;
		this.elements.modal.box.container = modalBox;

		// Set title
		if ( isDefined( this.content.title ) ){
			let modalTitle = $( '<div/>', { 'class': 'modal__title' }).html( this.content.title );
			modalBox.append( modalTitle );

			// Save element
			this.elements.modal.box.title = modalTitle;
		}

		// Set warning
		if ( isDefined( this.content.content ) ){
			let modalContent = $( '<div/>', { 'class': 'modal__content' }).html( this.content.content );
			modalBox.append( modalContent );

			// Save element
			this.elements.modal.box.content = modalContent;
		}

		// Set warning
		if ( isDefined( this.content.warning ) ){
			let modalWarning = $( '<div/>', { 'class': 'modal__warning' }).html( this.content.warning );
			modalBox.append( modalWarning );

			// Save element
			this.elements.modal.box.warning = modalWarning;
		}

		// Set buttons
		let modalButtons = $( '<div/>', { 'class': 'modal__buttons' });
		let cancelButton = $( '<button/>', { 'class': 'uap-btn uap-btn--secondary' }).html( this.content.buttons.cancel );
		modalButtons.append( cancelButton );

		// Save elements
		this.elements.modal.box.buttons.container = modalButtons;
		this.elements.modal.box.buttons.cancel = cancelButton;

		if ( isDefined( this.content.buttons.confirm ) ){
			let confirmButton = $( '<button/>', { 'class': 'uap-btn uap-btn--primary' }).html( this.content.buttons.confirm );
			modalButtons.append( confirmButton );

			// Save element
			this.elements.modal.box.buttons.confirm = confirmButton;
		}

		modalBox.append( modalButtons );

		// Append elements and add to the container
		modal.append( modalBox );
		automatorContainer.append( modal );
		this.elements.container.append( automatorContainer );

		// Add open class to the html tag
		$( 'html' ).addClass( 'automator-modal-open' );

		// Bind
		this.bind();
	}

	/*
	 * Define events
	 *
	 * @since 0.4
	 */

	setEvents( events ){
		let emptyFunction = () => {};

		// Define events
		this.events = {
			afterSetEvents: isDefined( events.afterSetEvents ) 	? events.afterSetEvents : emptyFunction,
			afterShow: 		isDefined( events.afterShow ) 	 	? events.afterShow 		: emptyFunction,
			afterHide: 		isDefined( events.afterHide ) 	 	? events.afterHide 		: emptyFunction,
			onConfirm:  	isDefined( events.onConfirm ) 		? events.onConfirm 		: emptyFunction,
			onCancel:   	isDefined( events.onCancel )  		? events.onCancel 		: emptyFunction,
		};

		if ( ! isDefined( events.onCancel ) ){
			this.events.onCancel = () => {
				// Default onCancel function
				// Hide modal box
				this.hide();
			};
		}

		// Fire afterSetEvents event
		this.events.afterSetEvents();
	}

	/*
	 * Remove all the DOM elements
	 *
	 * @since 0.4
	 */

	destroy(){
		let thisModal = this;

		// Unbind everything
		this.unbind();

		// Check if it's visible
		if ( this.isVisible ){
			// We have to hide it first
			this.hide(function(){
				thisModal.elements.automatorContainer.remove();
			});
		}
		else {
			thisModal.elements.automatorContainer.remove();
		}

		// Remove modal open class from the html tag
		$( 'html' ).removeClass( 'automator-modal-open' );
	}

	/*
	 * Bind the clicks on the buttons
	 *
	 * @since 0.4
	 */

	bind(){
		let thisModal = this;

		// Bind cancel button
		this.elements.modal.box.buttons.cancel.on( 'click', function(){
			thisModal.events.onCancel();
		});

		// Bind confirm button
		this.elements.modal.box.buttons.confirm.on( 'click', function(){
			thisModal.events.onConfirm();
		});
	}

	/*
	 * Unbind all events
	 *
	 * @since 0.4
	 */

	unbind(){
		this.elements.modal.box.buttons.cancel.unbind( 'click' );
		this.elements.modal.box.buttons.confirm.unbind( 'click' );
	}

	/*
	 * Show modal box
	 *
	 * @since 0.4
	 *
	 * @param {object} [callback] - Custom function that it's going to be invoked after we show the modal box
	 */

	show( callback = null ){
		let thisModal = this;

		// Set callback
		let event = function(){};

		if ( isDefined( callback ) ){
			event = callback;
		}
		else {
			event = this.events.afterShow;
		}

		callback = event;

		// Change is visible status
		this.isVisible = true;

		// Fade in
		this.elements.modal.container.fadeIn( thisModal.config.fadeDuration, callback );
	}

	/*
	 * Show modal box
	 *
	 * @since 0.4
	 *
	 * @param {object} [callback] - Custom function that it's going to be invoked after we hide the modal box
	 */

	hide( callback = null ){
		let thisModal = this;

		// Set callback
		let event = function(){};

		if ( isDefined( callback ) ){
			event = callback;
		}
		else {
			event = this.events.afterHide;
		}

		callback = event;

		// Change is visible status
		this.isVisible = false;

		// Fade out
		this.elements.modal.container.fadeOut( thisModal.config.fadeDuration, callback );

		// Remove modal open class from the html tag
		$( 'html' ).removeClass( 'automator-modal-open' );
	}

	/*
	 * Returns all the DOM elements
	 *
	 * @since 0.4
	 *
	 * @return {object} All the DOM elements created by this class
	 */

	getElements(){
		return this.elements;
	}
}

export default Modal;


