import {
    validateField
} from './Validation';

import RecipeTypeSelector from './RecipeTypeSelector';
import Field from './Field';
import FieldsGroup from './FieldsGroup';
import FieldsRepeater from './FieldsRepeater';

/**
 * Create the data required for Automator to render
 * 
 * @since 2.1.6
 * 
 * @param  {Function} callback The function to invoke after the data is ready
 * @return undefined
 */
export function createDataOnLoad( callback = '' ){
	consoleLogLabel( 'Start creating the main object' );

	// Check if there is a callback defined
	callback = ! isEmpty( callback ) ? callback : () => {};

	// Create a function to continue with the process once we know what's
	// the recipe type
	const completeProcess = () => {
		// Filter Automator items
		filterAutomatorItemsByRecipeType();
		// Group the triggers and actions by integration
		groupItemsByIntegration();

		// Get recipe items on page load
		recipeObject( 'create', {
			allRecipes: UncannyAutomator.recipes_object
		});

		// Remove unnecessary data
		delete UncannyAutomator.recipes_object;
	    delete UncannyAutomator.triggers;
	    delete UncannyAutomator.actions;

	    consoleLogLabel( 'Ended creating the main object' );

	    // Invoke the callback
	    callback();
	}
	
	// Check if WP is ready
	if ( ! isDefined( UncannyAutomator.wp ) || ! UncannyAutomator.wp ){
		// Define recipe type manually
		UncannyAutomator.recipe.type = 'user';

		// Complete the process
		completeProcess();
	}
	else {
		if ( ! isEmpty( UncannyAutomator.recipe.type ) ){
			// Complete the process
			completeProcess();
		}
		else {
			// Call to Recipe Type Selector
			new RecipeTypeSelector(() => {
				// Complete the process
				completeProcess();
			});
		}
	}
}

/**
 * Update all the objects used for the UI using the recipes_object object
 *
 * @since 0.5
 *
 * @param {string}   action - Action to perform. Valid actions: 'create', 'delete',
 * @param {object}   attributes - Data, check each switch to see the required data
 *
 * @return {boolean} TRUE if the action was made successfully
 */

export function recipeObject( action = null, attributes = {} ){
    // Create response variable and set default value
    let response     = false;

    // Define valid actions
    let validActions = [ 'create', 'validate' ];

    // Check if the action is not undefined and if it's a valid action
    if ( isDefined( action ) && inArray( action, validActions ) ){
        switch ( action ){
            case 'create':
                // Define required attributes properties
                let requiredData = [ 'allRecipes' ];

                // Check if the attributes object has all the required properties
                if ( inArray( requiredData, Object.keys( attributes ) ) ){
                    // Get this recipe in the allRecipes object
                    let recipe;

                    // Create allRecipes variable
                    let allRecipes = attributes.allRecipes;

                    $.each( allRecipes, function( index, theRecipe ){
                        if ( theRecipe.ID == UncannyAutomator.recipe.id ){
                            recipe = theRecipe;
                        }
                    });

                    // Add element to all triggers, actions and closures to define the itemType. We're going to need this later
                    $.each( recipe.triggers, ( index, trigger ) => trigger.itemType = 'trigger' );
                    $.each( recipe.actions,  ( index, action  ) => action.itemType  = 'action'  );
                    $.each( recipe.closures, ( index, closure ) => closure.itemType = 'closure' );

                    // Merge all the recipe triggers, actions and closures into one array
                    let items = [].concat.apply( [], [ recipe.triggers, recipe.actions, recipe.closures ] );

                    // Reset items object
                    itemsObject( 'reset' );

                    // Check if the recipe is anonymous
                    const recipeIsAnonymous = UncannyAutomator.recipe.type == 'anonymous';

                    // Iterate each item and add it to the itemsObject
                    $.each( items, function( index, item ){
                        // We're going to get all the required information to before adding the item to the items object
                        let itemToAdd;

                        // Get the data from the global item
                        let globalItem = getGlobalItem( item.meta.integration, item.itemType, item.meta.code );

                        // Get the item code in this variable
                        let itemCode = item.meta.code;

                        // Check if the required PHP code for this item exists
                        if ( isDefined( globalItem ) ){
                            // Create variable to save all the options
                            let options = {};

                            // Check if it has simple options (options that aren't part of a group of options)
                            if ( isDefined( globalItem.options ) ){
                                $.each( globalItem.options, function( index, option ){
                                    options[ option.option_code ] = [ option ];
                                });
                            }

                            // Add group of options
                            if ( isDefined( globalItem.options_group ) ){
                                $.extend( true, options, globalItem.options_group );
                            }

                            // We're going to create another options variable to create the options object as we're going to add it to the item object
                            let groupsOfOptions = {}

                            $.each( options, function( groupKey, fields ){
                                let allFieldsOnThisGroup = [];

                                // Check if it has more this group of fields has more than one option
                                let hasMoreThanOneOption = fields.length > 1;

                                $.each( fields, function( index, field ){
                                    // Check if value is undefined but has a default value
                                    /*
                                    if ( ! isDefined( item.meta[ field.option_code ] ) && isDefined( item.meta.default_value ) ){
                                        item.meta[ field.option_code ] = field.default_value;
                                    }
                                    */

                                    // Sanitize field data
                                    const sanitizeFieldData = ( field ) => {
                                        // Create field object
                                        let fieldObject = {
                                            isGroup:            hasMoreThanOneOption,
                                            itemId:             item.ID,
                                            itemType:           item.itemType,

                                            isRequired:         isDefined( field.required ) ? field.required : false,
                                            isReadOnly:         isDefined( field.read_only ) ? field.read_only : false,
                                            supportsTinyMCE:    isDefined( field.supports_tinymce ) ? field.supports_tinymce : false,
                                            supportsCopyToClipboard: isDefined( field.copy_to_clipboard ) ? field.copy_to_clipboard : false,

                                            isAjax:             isDefined( field.is_ajax ) ? field.is_ajax : false,

                                            groupId:            groupKey,
                                            optionCode:         field.option_code,
                                            fieldType:          field.input_type,

                                            label:              field.label,
                                            description:        field.description,
                                            placeholder:        field.placeholder,
                                            isHidden:           isDefined( field.is_hidden ) ? field.is_hidden : false,
                                            tokenName:          isDefined( field.token_name ) ? field.token_name : field.label,

                                            defaultValue:       field.default_value,
                                            currentValue:       isDefined( item.meta[ field.option_code ] ) ? item.meta[ field.option_code ] : field.default_value,
                                            relevant_tokens:    isDefined( field.relevant_tokens ) ? field.relevant_tokens : false,
                                        }

                                        /**
                                         * We want to make supportsTokens true by
                                         * default, but we have to check if the field
                                         * should support tokens first.
                                         * 
                                         * - Field type: For now, the token selector
                                         * only supports text, textarea, integer, float,
                                         * email, and URL fields. Check if this field
                                         * is one of those.
                                         * 
                                         * - Item type: The tokens are rendered only in
                                         * the user selector, actions and closures. 
                                         * Check if the item type if one of those.
                                         * 
                                         * - Read-only property: Check if the field is
                                         * not read-only. Even if the field meets the
                                         * previous conditions, it doesn't make sense
                                         * to render the token selector on a field
                                         * which value is supposed to be unmodifiable.
                                         * 
                                         * If the field meets all the previous
                                         * conditions, check if it has a defined value
                                         * for the supportsTokens property. Otherwise,
                                         * set it true. If it doesn't meet at least
                                         * one of the conditions, make it false.
                                         */
                                        
                                        if (
                                            [ 'text', 'textarea', 'int', 'float', 'email', 'url' ].includes( fieldObject.fieldType ) &&
                                            [ 'action', 'closure' ].includes( fieldObject.itemType ) &&
                                            ! fieldObject.isReadOnly
                                        ){
                                            fieldObject.supportsTokens = isDefined( field.supports_tokens ) ? field.supports_tokens : true;
                                        }
                                        else {
                                            fieldObject.supportsTokens = false;
                                        }

                                        // Check if it an AJAX field
                                        if ( fieldObject.isAjax ){
                                            fieldObject.targetField   = isDefined( field.fill_values_in ) ? field.fill_values_in : '';
                                            fieldObject.endpoint      = isDefined( field.endpoint ) ? field.endpoint : '';
                                            fieldObject.integrationId = globalItem.integration;

                                            // Check if it's an ajax field without target field
                                            // This means no other field will change when this one changes
                                            // and that we have to do an AJAX request when rendering the field, on page load 
                                            if ( isEmpty( fieldObject.targetField ) ){
                                                fieldObject.targetField = field.option_code;
                                            }
                                        }

                                        // Check if it's select or radio. In that case, add the options
                                        if ( [ 'select', 'radio' ].includes( fieldObject.fieldType ) ){
                                            fieldObject.options = isDefined( field.options ) ? field.options : [];
                                        }

                                        // Return field object
                                        return fieldObject;
                                    }
                                    
                                    let fieldObject, fieldInstance;

                                    // Check if it's a group of fields
                                    if ( field.input_type == 'group' ){
                                        // Get the field data
                                        fieldObject = {
                                            groupId:          groupKey,
                                            optionCode:       field.option_code,
                                            fieldType:        field.input_type,

                                            isGroup:          hasMoreThanOneOption,
                                            itemId:           item.ID,
                                            itemType:         item.itemType,

                                            label:            field.label,
                                            description:      field.description,

                                            tokenName:        isDefined( field.token_name ) ? field.token_name : field.label,

                                            isRequired:       isDefined( field.required ) ? field.required : false,

                                            fields: field.fields.map(( fieldData ) => {
                                                return sanitizeFieldData( fieldData );
                                            })
                                        }

                                        // Create field instance
                                        fieldInstance = new FieldsGroup( fieldObject );
                                    }
                                    else if ( field.input_type == 'repeater' ){
                                        // Try to parse the value of the repeater
                                        try {
                                            item.meta[ field.option_code ] = JSON.parse( item.meta[ field.option_code ] );
                                        } catch ( e ){}

                                        // Get field data
                                        fieldObject = {
                                            groupId:          groupKey,
                                            optionCode:       field.option_code,
                                            fieldType:        field.input_type,

                                            isGroup:          hasMoreThanOneOption,
                                            itemId:           item.ID,
                                            itemType:         item.itemType,

                                            label:            field.label,
                                            description:      field.description,

                                            tokenName:        isDefined( field.token_name ) ? field.token_name : field.label,

                                            defaultValue:     field.default_value,
                                            currentValue:     isDefined( item.meta[ field.option_code ] ) ? item.meta[ field.option_code ] : field.default_value,

                                            isRequired:       isDefined( field.required ) ? field.required : false,

                                            fields: field.fields.map(( fieldData ) => {
                                                return sanitizeFieldData( fieldData );
                                            })
                                        }

                                        // Check if this one has a custom "Add row" label
                                        if ( isDefined( field.add_row_button ) ){
                                            fieldObject.addRowButtonLabel = field.add_row_button;
                                        }

                                        // Check if this one has a custom "Remove row" label
                                        if ( isDefined( field.remove_row_button ) ){
                                            fieldObject.removeRowButtonLabel = field.remove_row_button;
                                        }

                                        // Create field instance
                                        fieldInstance = new FieldsRepeater( fieldObject );
                                    }
                                    // Otherwise, treat it as a normal field
                                    else {
                                        // Get the field data
                                        fieldObject = sanitizeFieldData( field );

                                        // Create the field instance
                                        fieldInstance = new Field( fieldObject.fieldType, fieldObject, false );
                                    }
                                   
                                    // Add field to the group of fields
                                    allFieldsOnThisGroup.push( fieldInstance );
                                });

                                // Array where we're going to save the buttons of this item
                                let buttons = [];

                                // Check if this group of options has buttons
                                if ( isDefined( globalItem.buttons ) ){
                                    // Iterate buttons
                                    $.each( globalItem.buttons, ( index, button ) => {
                                        // Check if we have to add the button to this group
                                        if ( button.show_in == groupKey ){
                                            buttons.push( button );
                                        }
                                    });
                                }

                                // Push fields to the groupsOfOptions object
                                groupsOfOptions[ groupKey ] = {
                                    fields:  allFieldsOnThisGroup,
                                    isValid: false, // FALSE by default, but we're going to do a validation later
                                    buttons: buttons
                                }
                            });

                            // Create object with the item data
                            itemToAdd = {
                                id:              item.ID,
                                itemType:        item.itemType,
                                itemCode:        itemCode,
                                integrationCode: globalItem.integration,
                                isLive:          item.post_status == 'publish' ? true : false,
                                options:         groupsOfOptions,
                                meta:            item.meta,
                                isMissing:       false,
                                tokens:          isDefined( item.tokens ) ? item.tokens : [],
                                inlineCss:       isDefined( globalItem.inline_css ) ? globalItem.inline_css : '',
                                filterTokens:    isDefined( globalItem.filter_tokens ) ? globalItem.filter_tokens : '',
                            }

                            // Check if the support link is defined
                            if ( isDefined( globalItem.support ) ){
                                itemToAdd.support = globalItem.support;
                            }

                            // Create meta for those field that don't have a value saved in the database but have a default value

                            // Get all fields
                            let fields = {};

                            // Create an object with all the options from the field
                            $.each( groupsOfOptions, function( optionKey, options ){
                                $.each( options.fields, function( index, field ){
                                    fields[ field.getAttributes().optionCode ] = field;
                                });
                            });

                            $.each( fields, ( index, field ) => {
                                let fieldAttributes = field.getAttributes();
                                if ( ( ! isDefined( itemToAdd.meta[ fieldAttributes.optionCode ] ) || isEmpty( itemToAdd.meta[ fieldAttributes.optionCode ] ) ) && ! isEmpty( fieldAttributes.currentValue ) ){
                                    itemToAdd.meta[ fieldAttributes.optionCode ] = fieldAttributes.currentValue;
                                }
                            });

                            // If it's anonymous, add the "canLogUserIn" property
                            // Also set the default value
                            if ( recipeIsAnonymous ){
                                itemToAdd.canLogNewUserIn = isDefined( globalItem.can_log_in_new_user ) ? globalItem.can_log_in_new_user : true;
                            }

                            /*

                            // Create readable meta
                            itemToAdd.metaReadable = jQuery.extend( true, {}, itemToAdd.meta );

                            // Iterate each meta and search for a readable value
                            $.each( itemToAdd.metaReadable, ( metaKey, metaValue ) => {
                                let field = fields[ metaKey ];

                                // Check if there is a field with that ID
                                if ( isDefined( field ) ){
                                    field = field.getAttributes();

                                    // Check if it's a select
                                    if ( field.fieldType == 'select' ){
                                        let value;

                                        // Iterate each option of the select and search for the text instead of the value
                                        $.each( field.options, function( index, selectOption ){
                                            // Compare the value with the metaValue
                                            if ( selectOption.value == metaValue ){
                                                // Set new metaValue
                                                itemToAdd.metaReadable[ metaKey ] = selectOption.text;
                                            }
                                        });
                                    }
                                }
                            });

                            // Check if we have to add default values
                            $.each( fields, function( index, field ){
                                if ( ! isDefined( itemToAdd.metaReadable[ field.optionCode ] ) && ! isEmpty( field.defaultValue ) ){
                                    if ( field.fieldType == 'select' ){

                                        // Iterate each option of the select and search for the text instead of the value
                                        $.each( field.options, function( index, selectOption ){
                                            // Compare the value with the metaValue
                                            if ( selectOption.value == metaValue ){
                                                // Set new metaValue
                                                itemToAdd.metaReadable[ field.optionCode ] = selectOption.text;
                                            }
                                        });
                                    }
                                    else {
                                        itemToAdd.metaReadable[ field.optionCode ] = field.defaultValue;
                                    }
                                }
                            });

                            */
                        }
                        else {
                            // Create object with the item data
                            itemToAdd = {
                                id:              item.ID,
                                itemType:        item.itemType,
                                itemCode:        item.meta.code,
                                integrationCode: item.meta.integration,
                                integrationName: item.meta.integration_name,
                                sentence:        item.meta.sentence,
                                isLive:          item.post_status == 'publish' ? true : false,
                                tokens:          [],
                                options:         {},
                                meta:            item.meta,
                                isMissing:       true,
                            }

                            // If it's anonymous, add the "canLogUserIn" property
                            // Also set the default value
                            if ( recipeIsAnonymous ){
                                itemToAdd.canLogNewUserIn = isDefined( item.meta.can_log_in_new_user ) ? ( item.meta.can_log_in_new_user == 'true' ) : true;
                            }
                        }

                        // We have to add it
                        itemsObject( 'add', itemToAdd );
                    });

                    // Check if the recipe is anonymous and can log the new user in
                    if ( recipeIsAnonymous ){
                        // Set the default value
                        UncannyAutomator.recipe.canLogNewUserIn = false;

                        // Get the recipe items
                        const recipeTriggers = itemsObject( 'get', {
                            filter: {
                                itemType: 'trigger'
                            }
                        });

                        // Check if it has items
                        if ( Object.keys( recipeTriggers ) > 0 ){
                            // Change the default value
                            UncannyAutomator.recipe.canLogNewUserIn = true;

                            // Iterate the triggers
                            $.each( recipeTriggers, ( index, trigger ) => {
                                // Check if this trigger allows the recipe
                                // to log the new user in
                                UncannyAutomator.recipe.canLogNewUserIn = UncannyAutomator.recipe.canLogNewUserIn && trigger.canLogNewUserIn;
                            });
                        }
                    }

					// Check if we should validate the data
					if ( ! isDefined( attributes.validate ) || ( isDefined( attributes.validate ) && attributes.validate ) ){
						// Validate the recipe to update the isValid elements
                    	recipeObject( 'validate' );
					}
                }
                else {
                    // Show to the developer what attributes are missing
                    let missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );
                    console.error( vsprintf( '%s: Some attributes are missing: %s', [ 'recipeObject', missingAttributes ] ) );
                }

                break;

            case 'validate':
                // Valid by default, will be changed to false if we find an error
                let isRecipeValid = true;

                // Check if the recipe has triggers, actions and/or closures
                // Update UncannyAutomator.recipe.hasLive
                $.each( UncannyAutomator.recipe.hasLive, function( itemType, has ){
                    // All false
                    UncannyAutomator.recipe.hasLive[ itemType ] = false;
                });

                // Check if there is at least one item of that type in the main object
                $.each( UncannyAutomator.recipe.items, function( itemID, itemData ){
                    // If at least one live item, then set TRUE
                    if ( itemData.isLive ){
                        UncannyAutomator.recipe.hasLive[ itemData.itemType ] = true;
                    }
                });

                // Iterate each item
                $.each( UncannyAutomator.recipe.items, function( itemID, itemData ){

                    // We will use this variable to decide if the item is valid or not
                    let itemIsValid = true;

                    // Iterate each group of fields
                    $.each( itemData.options, function( optionsGroupID, optionsGroupData ){

                        // We will use this variable to decide if the group of fields is valid or not
                        let groupIsValid = true;

                        // Iterate each field
                        $.each( optionsGroupData.fields, function( index, field ){
                            // Validate the field. Read validateField comments to learn more.
                            let fieldValidation = field.validate( false );

                            // Check if the option is invalid, if at least one field is invalid make all the group invalid
                            if ( ! fieldValidation.isValid ){
                                groupIsValid = false;
                            }
                        });

                        // Set result
                        UncannyAutomator.recipe.items[ itemID ].options[ optionsGroupID ].isValid = groupIsValid;

                        // Check if the group is invalid, if at least on group of fields is invalid make all the item invalid
                        if ( ! groupIsValid ){
                            itemIsValid = false;
                        }
                    });

                    UncannyAutomator.recipe.items[ itemID ].isValid = itemIsValid;

                    // Check if the item is invalid. If at least one item is invalid make all the recipe invalid
                    if ( itemData.isLive && ! itemIsValid ){
                        isRecipeValid = false;
                    }
                });

                // Now check what items we have in the object
                // We must have at least one trigger and an action or closure
                // Check if it has at least one trigger
                if ( ! UncannyAutomator.recipe.hasLive.trigger ){
                    isRecipeValid = false;

                    // Set error message
                    UncannyAutomator.recipe.message.error = UncannyAutomator.i18n.validation.recipe.oneTrigger;
                }
                else {
                    // Validate user selector
                    if ( UncannyAutomator.recipe.type == 'anonymous' && ! UncannyAutomator.recipe.userSelector.isValid ){
                        // Then the recipe is not valid
                        isRecipeValid = false;

                        // Add message
                        UncannyAutomator.recipe.message.error = UncannyAutomator.i18n.validation.recipe.userSelector;
                    }
                    else {
                        // Check if it has at least one action or closure
                        if ( ! UncannyAutomator.recipe.hasLive.action && ! UncannyAutomator.recipe.hasLive.closure ){
                            isRecipeValid = false;

                            // Set error message
                            UncannyAutomator.recipe.message.error = UncannyAutomator.i18n.validation.recipe.oneAction;
                        }
                        else {
                            // Check if the message has to be more generic
                            if ( ! UncannyAutomator.recipe.hasLive.trigger && ( ! UncannyAutomator.recipe.hasLive.action && ! UncannyAutomator.recipe.hasLive.closure ) ){
                                // Set error message
                                UncannyAutomator.recipe.message.error = UncannyAutomator.i18n.validation.recipe.liveItems;
                            }
                        }
                    }
                }

                UncannyAutomator.recipe.isValid = isRecipeValid;

                // Reset message if recipe is valid
                if ( isRecipeValid ){
                    // Reset message, we don't need it
                    UncannyAutomator.recipe.message.error = '';
                }

                // Everything is ok, return true
                response = true;

                break;
        }
    }
    else {
        // Has to add error code
        console.error( vsprintf( '%s: The action "%s" isn\'t defined', [ 'recipeObject', action ] ) );
    }

    return response;
}

/**
 * Add, update, delete, update, validate and clean the main recipe object
 *
 * @since 0.2
 *
 * @param {string}   action - Action to perform. Valid actions: 'add', 'update', 'delete', 'reset', 'get', and 'clean'.
 * @param {object}   attributes
 * The required properties depend of the requested action.
 * For the 'update' action it have to include the properties that need to be updated in the items object.
 * For the 'add' action:
 * @param {int}      attributes.id        Item ID
 * @param {string}   attributes.itemType  The item type. The valid types are 'trigger', 'action' and 'closure'
 *
 * @return {boolean} TRUE if the action was made successfully
 */

export function itemsObject( action = null, attributes = {} ){
    // Create response variable and set default value
    let response     = false;

    // Define valid actions
    let validActions = [ 'add', 'delete', 'clean', 'reset', 'update', 'get' ];

    // Check if the action is not undefined and if it's a valid action
    if ( isDefined( action ) && inArray( action, validActions ) ){

        switch ( action ){
            case 'add':
                // Extend default attributes object with default values
                attributes = $.extend({
                    isValid: false, // Because it's going to be empty
                    isLive: false, // Draft by default
                }, attributes );

                // Define required attributes properties
                let requiredData = [ 'id', 'itemType' ];

                // Check if the attributes object has all the required properties
                if ( inArray( requiredData, Object.keys( attributes ) ) ){

                    // Has ID, check if it wasn't added
                    if ( ! isDefined( UncannyAutomator.recipe.items[ attributes.id ] ) ){

                        // Create property
                        UncannyAutomator.recipe.items[ attributes.id ] = attributes;

                        // Clean undefined properties
                        itemsObject( 'clean', {} );

                        // Everything is ok, return true
                        response = true;
                    }
                }
                else {
                    // Show to the developer what attributes are missing
                    let missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );
                    console.error( vsprintf( '%s: Some attributes are missing: %s', [ 'itemsObject', missingAttributes ] ) );
                }

                break;

            case 'delete':
                attributes = $.extend({
                    isValid: false,
                }, attributes );

                if ( ! isEmpty( attributes.id ) ){

                    if ( isDefined( UncannyAutomator.recipe.items[ attributes.id ] ) ){
                        delete UncannyAutomator.recipe.items[ attributes.id ];

                        // Everything is ok, return true
                        response = true;
                    }
                    else {
                        console.error( vsprintf( '%s: We didn\'t find an item with this the ID %s', [ 'itemsObject', attributes.id ] ) );
                    }

                }
                else {
                    console.error( vsprintf( '%s: Invalid item ID', [ 'itemsObject' ] ) );
                }

                break;

            case 'clean':
                // Delete undefined and null properties

                let items = {};

                $.each( UncannyAutomator.recipe.items, function( index, value ){
                    if ( isDefined( value ) ){
                        items[ index ] = value;
                    }
                });

                UncannyAutomator.recipe.items = items;

                // Everything is ok, return true
                response = true;

                break;

            case 'update':
                // Update item attributes

                if ( ! isEmpty( attributes.id ) ){

                    if ( isDefined( UncannyAutomator.recipe.items[ attributes.id ] ) ){

                        // Extend item object properties with new ones
                        UncannyAutomator.recipe.items[ attributes.id ] = $.extend( {}, UncannyAutomator.recipe.items[ attributes.id ], attributes );

                        // Everything is ok, return true
                        response = true;

                    }
                    else {
                        console.error( vsprintf( '%s: We didn\'t find an item with this the ID %s', [ 'itemsObject', attributes.id ] ) );
                    }

                }
                else {
                    console.error( vsprintf( '%s: Invalid item ID', [ 'itemsObject' ] ) );
                }

                break;

            case 'reset':
                // Delete all the items in the items object
                UncannyAutomator.recipe.items = {}

                // Return true
                response = true;
                break;

            case 'get':
                // Check if we have to filter items
                if ( ! isEmpty( attributes.filter ) ){
                    // Create a new items object. We're going to add items to this one if the filters match
                    let items = {}

                    // Iterate all current items
                    $.each( UncannyAutomator.recipe.items, function( itemId, item ){
                        let matchFilters = true;

                        $.each( attributes.filter, function( key, value ){
                            if ( item[ key ] != value ){
                                matchFilters = false;
                            }
                        });

                        // Check if we have to add this item
                        if ( matchFilters ){
                            items[ itemId ] = item;
                        }
                    });

                    // Return the new items object
                    response = items;
                }
                else {
                    response = UncannyAutomator.recipe.items;
                }

                break;
        }
    }
    else {
        // Has to add error code
        console.error( vsprintf( '%s: The action "%s" isn\'t defined', [ 'itemsObject', action ] ) );
    }

    return response;
}

/**
 * Add the triggers and actions to each integration.
 * Change the data structure so it's possible to know
 * what items and integrations are pro
 *
 * @since 2.1.4
 */
export function groupItemsByIntegration(){
    // Iterate the integrations
    Object.entries( UncannyAutomator.integrations ).forEach(([ integrationID, integration ]) => {
        // Get the list of available triggers and actions
        // Sort them alphabetically
        let triggers = {
                available:   sortArrayOfObjectsAlphabetically( UncannyAutomator.triggers.filter( item => item.integration == integrationID ), 'select_option_name', sentence => replaceCurlyBraces( sentence ) ),
                unavailable: [],
            },
            actions  = {
                available:   sortArrayOfObjectsAlphabetically( UncannyAutomator.actions.filter( item => item.integration == integrationID ), 'select_option_name', sentence => replaceCurlyBraces( sentence ) ),
                unavailable: []
            };

        if ( ! isDefined( UncannyAutomator.wp ) || ! UncannyAutomator.wp ){
            // Check if there are PRO items for this integration
            if (
                isDefined( UncannyAutomator.proFeatures ) && 
                isDefined( UncannyAutomator.proFeatures[ integrationID ] )
            ){
                // Push the pro logged-in triggers to the unavailable triggers array
                // Sort them alphabetically
                triggers.unavailable = sortArrayOfObjectsAlphabetically( UncannyAutomator.proFeatures[ integrationID ].triggers.filter( trigger => trigger.type == 'logged-in' ), 'name', sentence => replaceCurlyBraces( sentence ) );

                // Push the pro actions to the unavailable actions array
                // Sort them alphabetically
                actions.unavailable  = sortArrayOfObjectsAlphabetically( UncannyAutomator.proFeatures[ integrationID ].actions, 'name', sentence => replaceCurlyBraces( sentence ) );
            }
        }

        // Evaluate if all the available items are pro
        // To do that, iterate every element using the method every
        // and return the value of "is_pro"
        triggers.allItemsArePro     = triggers.available.every( item => isDefined( item.is_pro ) ? item.is_pro : false );
        actions.allItemsArePro      = actions.available.every( item => isDefined( item.is_pro ) ? item.is_pro : false );

        // Check if the integration has available triggers and actions
        triggers.hasAvailableItems  = triggers.available.length > 0;
        actions.hasAvailableItems   = actions.available.length > 0;

        // Check if the integration has unavailable triggers and actions
        triggers.hasUnavailableItems = triggers.unavailable.length > 0;
        actions.hasUnavailableItems  = actions.unavailable.length > 0;

        // Check if it has any kind of items
        triggers.hasTriggers = triggers.hasAvailableItems || triggers.unavailable.length > 0;
        actions.hasActions   = actions.hasAvailableItems || actions.unavailable.length > 0;

        // Add the items to the integration
        UncannyAutomator.integrations[ integrationID ].items = {
            triggers: triggers,
            actions:  actions
        }
    });
}

/**
 * Sort an array of objects using one of the properties
 *
 * @since  2.1.4
 * @param  {Array}  items             The array
 * @param  {String} propertyToCompare The property used to compare the elements
 * @return {Array}                    The array, sorted
 */
export function sortArrayOfObjectsAlphabetically( items = [], propertyToCompare = '', filter ){
    // Check if there isn't a filter defined
    if ( ! isDefined( filter ) ){
        filter = value => value;
    }

    // Check if it's an array and it's not empty
    if ( Array.isArray( items ) && ! isEmpty( items ) ){
        // Sort the array of integrations using the integration name
        items = items.sort(( a, b ) => {
            // Define the default value
            let compare = 0;

            // Check if the elements have the properties we're going to use to compare
            if ( isDefined( a[ propertyToCompare ] ) && isDefined( b[ propertyToCompare ] ) ){
                // Filter the values
                let valueA = filter( a[ propertyToCompare ] ),
                    valueB = filter( b[ propertyToCompare ] );

                // Check which integration name should go first
                compare = valueA.localeCompare( valueB );

                // Validate the value
                // Do NOT rely on exact return values of -1 or 1
                // Negative and positive integer results vary between browsers (as well as between browser versions) because the W3C specification only mandates negative and positive values. Some browsers may return -2 or 2, or even some other negative or positive value.
                compare = compare > 0 ? 1 : compare;
                compare = compare < 0 ? -1 : compare;
            }  

            // Return result
            return compare;
        });
    }

    // Return the new array
    return items;
}

/**
 * Get a list of triggers or actions by integration
 * @param  {String} integrationID The integration ID
 * @param  {String} itemType      The item type. Valid values: "trigger", "triggers", "action" and "actions"
 * @return {Object}               And object with the available and unavailable items
 *
 * @since 2.1.4
 */
export function getItemsByIntegration( integrationID = '', itemType = 'triggers' ){
    // Create the array where we're going to save the items
    let items = {
        available: {},
        unavailable: {}
    };

    // Convert itemType "trigger" to "triggers"
    itemType = itemType == 'trigger' ? 'triggers' : itemType;
    // Convert itemType "action" to "actions"
    itemType = itemType == 'action' ? 'actions' : itemType;

    // Validate the parameter itemType
    if ( [ 'triggers', 'actions' ].includes( itemType ) ){
        // Check if the integration ID is defined
        if ( ! isEmpty( integrationID ) ){
            // Check if the integration exists
            if ( isDefined( UncannyAutomator.integrations[ integrationID ] ) ){
                // Get the requested items
                items = UncannyAutomator.integrations[ integrationID ].items[ itemType ];
            }
        }
    }

    // Return the triggers
    return items;
}

/**
 * Update user selector data
 *
 * @since  2.0
 */

export function userSelectorObject( action = null, attributes = {} ){
    // Create response variable and set default value
    let response     = false;

    // Define valid actions
    let validActions = [ 'set-step', 'get-step', 'set-source', 'update-fields', 'reset-fields' ];

    // Create shortcut of the user selector object
    const userSelector = UncannyAutomator.recipe.userSelector;

    // Check if the action is not undefined and if it's a valid action
    if ( isDefined( action ) && inArray( action, validActions ) ){
        // Create empty array to check if the actions have the required data
        let requiredData = [];

        switch ( action ){
            /**
             * Get the current step so we know what we have to render
             * There are 4 steps:
             *
             * 1. select-source: Where the recipe creator select the source of the user.
             *                   The options are "Existing user" and "New user".
             * 2. set-options: Where the recipe creator defines values of the fields.
             * 3. show-sentence: When the data is complete and valid.
             */
    
            case 'set-step':
                // Define required attributes properties
                requiredData = [ 'step' ];

                // Check if the attributes object has all the required properties
                if ( inArray( requiredData, Object.keys( attributes ) ) ){
                    // Set status
                    UncannyAutomator.recipe.userSelector.step = attributes.step;
                }
                else {
                    // Show to the developer what attributes are missing
                    let missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );
                    console.error( vsprintf( '%s: Some attributes are missing: %s', [ 'userSelectorObject', missingAttributes ] ) );
                }

                break;

            case 'get-step':
                // Create the variable we're going to return
                // Set default value
                let step = 'select-source';

                // Check if the step property is defined
                if ( isDefined( UncannyAutomator.recipe.userSelector.step ) ){
                    // If it is, then return that value
                    step = UncannyAutomator.recipe.userSelector.step;
                }
                else {
                    // Try to get step
                    // Check if there are fields saved
                    if ( ! isEmpty( UncannyAutomator.recipe.userSelector.fields[ UncannyAutomator.recipe.userSelector.source ] ) ){
                        // Show sentence
                        step = 'show-sentence';

                        // If we have fields it's because we already valid them before,
                        // thus the user selector is valid
                        UncannyAutomator.recipe.userSelector.isValid = true;
                    }
                }

                // Return step
                response = step;

                break;

            case 'set-source':
                // Define required attributes properties
                requiredData = [ 'source' ];

                // Check if the attributes object has all the required properties
                if ( inArray( requiredData, Object.keys( attributes ) ) ){
                    // Set status
                    UncannyAutomator.recipe.userSelector.source = attributes.source;
                }
                else {
                    // Show to the developer what attributes are missing
                    let missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );
                    console.error( vsprintf( '%s: Some attributes are missing: %s', [ 'userSelectorObject', missingAttributes ] ) );
                }

                break;

            case 'update-fields':
                // Define required attributes properties
                requiredData = [ 'source', 'fields' ];

                // Check if the attributes object has all the required properties
                if ( inArray( requiredData, Object.keys( attributes ) ) ){
                    // Check if the source parameter is valid
                    if ( [ 'existingUser', 'newUser' ].includes( attributes.source ) ){
                        // Check if we already have fields
                        if ( isEmpty( UncannyAutomator.recipe.userSelector.fields ) ){
                            UncannyAutomator.recipe.userSelector.fields = {
                                existingUser: {},
                                newUser: {}
                            }
                        }

                        // Update fields
                        UncannyAutomator.recipe.userSelector.fields[ attributes.source ] = $.extend( {}, UncannyAutomator.recipe.userSelector.fields[ attributes.source ], attributes.fields );
                    }
                    else {
                        // Show to the developer what's wrong
                        console.error( vsprintf( '%s: The source parameter is invalid. Valid values are: %s', [ 'userSelectorObject', [ 'existingUser', 'newUser' ] ] ) );
                    }  
                }
                else {
                    // Show to the developer what attributes are missing
                    let missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );
                    console.error( vsprintf( '%s: Some attributes are missing: %s', [ 'userSelectorObject', missingAttributes ] ) );
                }

                break;

            case 'reset-fields':
                // Return boolean
                UncannyAutomator.recipe.userSelector.fields = {
                    existingUser: {},
                    newUser: {}
                }
                break;
        }
    }
    else {
        // Has to add error code
        console.error( vsprintf( '%s: The action "%s" isn\'t defined', [ 'userSelectorObject', action ] ) );
    }

    return response;
}

/**
 * Filter triggers, actions and closures by recipe type.
 * This won't filter the items of a recipe, but the available
 * items in Automator
 *
 * @since 2.0
 * 
 * @return null 
 */

export function filterAutomatorItemsByRecipeType(){
    // Check if the type attribute has a valid value
    if ( [ 'user', 'anonymous' ].includes( UncannyAutomator.recipe.type ) ){
        // Check if debug mode is enabled
        if ( UncannyAutomator.dev.debugMode ){
            // Start group
            consoleLogLabel( 'Filter Automator items', true, {
                collapseGroup: true
            });

            // Show current recipe type
            console.log( vsprintf( 'Current recipe type: %s', [ UncannyAutomator.recipe.type ] ) );
        }

        // Filter triggers
        UncannyAutomator.triggers = UncannyAutomator.triggers.filter(( trigger ) => {
            // Check if debug mode is enabled
            if ( UncannyAutomator.dev.debugMode ){
                // Show trigger
                console.log( trigger );

                // Show decision
                if ( trigger.type == UncannyAutomator.recipe.type ){
                    consoleLogResult( 'success', 'Keep' );
                }
                else {
                    consoleLogResult( 'error', 'Remove' );
                }
            }

            // Check if it matches the recipe type
            return trigger.type == UncannyAutomator.recipe.type;
        });

        // Check if debug mode is enabled
        if ( UncannyAutomator.dev.debugMode ){
            // Start group
            console.groupEnd();
        }
    }
}

/**
 * Performs a REST Call
 *
 * @since     0.1
 * @modified  0.5
 *
 * @param {string}    endPoint - Endpoint of the REST call
 * @param {object}    data - Data to be sent in the call
 * @param {callback}  [onSuccess] - Function to be invoked if the call is successful
 * @param {callback}  [onFail] - Function to be invoked if the call fails
 *
 * @return null
 */

export function restCall( endPoint = null, data = null, onSuccess = null, onFail = null, options = {} ){
    // Define the valid endpoints
    let validEndpoints = [ 'add', 'delete', 'update', 'change_post_status', 'recipe_completions_allowed', 'change_post_title', 'change_post_recipe_type', 'user-selector', 'set_recipe_terms' ];

    // Check custom options
    options = $.extend({}, {
        'requiresRecipesObject': true,
        'renderUI':              true
    }, options );

    // Check if the endPoint parameter is a valid endpoint
    if ( inArray( endPoint, validEndpoints ) ){

        // Do AJAX
        $.ajax({
            method: 'POST',
            url:    `${ UncannyAutomator.restURL }/${endPoint}/`,
            data:   $.param( data ) + '&' + $.param({ doing_rest: 1 }),

            // Attach Nonce the the header of the request
            beforeSend: function( xhr ){
                xhr.setRequestHeader( 'X-WP-Nonce', UncannyAutomator.nonce );
            },

            success: function( response ){
                // Show console messages if debug is enabled
                if ( UncannyAutomator.dev.debugMode ){
                    consoleLogLabel( 'REST call ', true, {
                        collapseGroup: true
                    });
                    console.log( `Endpoint: ${ endPoint }` );
                    console.log( 'Data:' );
                    console.log( data );
                    console.log( 'Response:' );
                    console.log( response );
                    console.groupEnd();
                }

                // Check if the request succeeded
                if ( response.success ){
                    // Check if we have the new recipes_object, otherwise the data will change on the server but not in the UI
                    if ( isDefined( response.recipes_object ) || ! options.requiresRecipesObject ){
                        // Check if onSuccess
                        if ( isDefined( onSuccess ) ){
                            // Check if we have to render the UI again
                            if ( options.renderUI ){
                                recipeObject( 'create', {
                                    allRecipes: response.recipes_object
                                });
                            }

                            // Invoke callback
                            onSuccess( response );
                        }
                    }
                    else {
                        // The call was successful, but the new recipes_object is missing
                        console.error( 'The call was successful, but the new recipes_object is missing.' );

                        // Check if the onFail callback is defined
                        if ( isDefined( onFail ) ){
                            // Invoke callback
                            onFail( response );
                        }
                    }
                }
                else {
                    // The call was successful, but there were errors,
                    console.error( 'The call was successful, but there were errors.' );
                    // console.log( response );

                    // Check if the onFail callback is defined
                    if ( isDefined( onFail ) ){
                        // Invoke callback
                        onFail( response );
                    }
                }
            },

            statusCode: {
                403: function( response ){
                    // Show console messages if debug is enabled
                    if ( UncannyAutomator.dev.debugMode ){
                        consoleLogLabel( 'REST call ', true, {
                            collapseGroup: true
                        });
                        console.log( 'Data:' );
                        console.log( data );
                        console.log( 'Response' );
                        console.log( '%c403: Access denied', 'color: red' );
                        console.groupEnd();
                    }

                    if ( isDefined( onFail ) ){
                        onFail( response );
                    }
                }
            },

            fail: function ( response ){
                // Show console messages if debug is enabled
                if ( UncannyAutomator.dev.debugMode ){
                    consoleLogLabel( 'REST call ', true, {
                        collapseGroup: true
                    });
                    console.log( 'Data:' );
                    console.log( data );
                    console.log( 'Response' );
                    console.log( '%cUnknown error', 'color: red' );
                    console.groupEnd();
                }

                if ( isDefined( onFail ) ){
                    onFail( response );
                }
            },
        });
    }
    else {
        console.error( vsprintf( 'The %s endPoint does not exists. The valid endpoints are %s', [ endPoint, validEndpoints.join( ', ' ) ] ) );
    }
}

/**
 * Performs an AJAX request
 *
 * @since 0.5
 *
 * @param {object}    data - Data to be sent in the request
 * @param {callback}  [onSuccess] - Function to be invoked if the request is successful
 * @param {callback}  [onFail] - Function to be invoked if the request fails
 *
 * @return null
 */

export function AJAXRequest( action = null, data = null, onSuccess = null, onFail = null ){

    // Check that {action} is defined
    if ( isDefined( action ) ){
        // Add {action} to the data object
        data = $.extend( true, data, {
            action: action,
            nonce: UncannyAutomator.nonce
        });

        // Do AJAX
        $.ajax({
            method:   'POST',
            dataType: 'json',
            url:      ajaxurl,
            data:     data,

            success: function( response  ){
                // Show console messages if debug is enabled
                if ( UncannyAutomator.dev.debugMode ){
                    consoleLogLabel( 'AJAX call ', true, {
                        collapseGroup: true
                    });
                    console.log( 'Data:' );
                    console.log( data );
                    console.log( 'Response' );
                    console.log( response );
                    console.groupEnd();
                }

                // Reload page if user authentication fails
                if ( isDefined( response.status ) && response.status == 'auth-failed' ){
                    location.reload();
                }
                else {
                    // Check if onSuccess is defined
                    if ( isDefined( onSuccess ) ){
                        // Invoke callback
                        onSuccess( response, data );
                    }
                }
            },

            statusCode: {
                403: function(){
                    // Show console messages if debug is enabled
                    if ( UncannyAutomator.dev.debugMode ){
                        consoleLogLabel( 'AJAX call ', true, {
                            collapseGroup: true
                        });
                        console.log( 'Data:' );
                        console.log( data );
                        console.log( 'Response' );
                        consoleLogLabel( '403: Access denied', false, { bgColor: '#ff0000' });
                        console.groupEnd();
                    }

                    if ( isDefined( onFail ) ){
                        onFail( response );
                    }

                    // Check if the onFail callback is defined
                    if ( isDefined( onFail ) ){
                        // Invoke callback
                        onFail( response );
                    }
                }
            },

            fail: function ( response ){
                // Show console messages if debug is enabled
                if ( UncannyAutomator.dev.debugMode ){
                    consoleLogLabel( 'AJAX call ', true, {
                        collapseGroup: true
                    });
                    console.log( 'Data:' );
                    console.log( data );
                    console.log( 'Response' );
                    console.log( '%cUnknown error', 'color: red' );
                    console.groupEnd();
                }

                if ( isDefined( onFail ) ){
                    onFail( response );
                }

                // console.log( '%c Failed ', 'background: #ba3535; padding: 2px; color: #fff' );
                // console.log( response );
            }
        });
    }
    else {
        console.error( 'We cannot do this AJAX request. {action} is not defined.' );
    }
}

/**
 * This will return all the current value of the field, even if they aren't saved on the database
 * This will use .val() to get the values
 *
 * @since  1.0
 *
 * @param {int} itemId - The ID of the item
 */

export function getCurrentFieldsValues( itemId ){
    let values = {};

    // Iterate the items' group of fields
    $.each( UncannyAutomator.recipe.items[ itemId ].options, ( index, groupOfFields) => {
        // Iterate each field
        $.each( groupOfFields.fields, ( index, field ) => {
            // Get value
            let fieldId    = field.getFieldID(),
                fieldValue = field.getValue();

            // Check if it isn't defined. In that case get the current value from the currentValue attribute
            if ( ! isDefined( fieldValue ) ){
                fieldValue = field.getAttributes().currentValue;
            }

            // Save value
            values[ fieldId ] = fieldValue;
        });
    });

    return values;
}

/**
 * Get a Field instance
 *
 * @since 0.5
 *
 * @param {int}     itemId - The item ID
 * @param {string}  fieldId - The field ID
 *
 * @return {Field}  a Field instance
 */

export function getField( itemId, fieldId ){
    let field;

    $.each( UncannyAutomator.recipe.items[ itemId ].options, function( optionsGroupId, optionsGroup ){
        $.each( optionsGroup.fields, function( index, fieldInstance ){
            if ( fieldId == fieldInstance.getFieldID() ){
                field = fieldInstance;
            }
        });
    });

    return field;
}

/**
 * Get data about a trigger, action or closure
 *
 * @since 0.5
 *
 * @param {string}   itemType - The item type. Valid values are trigger, action and closure
 * @param {string}   itemCode - The item code
 *
 * @return {object}  Item's data
 */

export function getGlobalItem( integrationID = '', itemType = '', itemCode = '' ){
	let theItem;

	// Check if all are defined
	if ( ! isEmpty( integrationID ) && ! isEmpty( itemType ) && ! isEmpty( itemCode ) ){
		let integrationItems = {};

		// Check if the we're trying to get a trigger or action
		if ( itemType == 'trigger' || itemType == 'action' ){
			// Check if the integration exists
			if ( isDefined( UncannyAutomator.integrations[ integrationID ] ) ){
				// Create a reference to the integration
				let integration = UncannyAutomator.integrations[ integrationID ];

				// Check if the items are defined
				if ( isDefined( integration.items ) ){
					// Set integrationItems
					integrationItems = itemType == 'trigger' ? integration.items.triggers.available : integration.items.actions.available;
				}
				else {
					console.log( '%cNot ready, who is calling this?', 'background: red; color: #fff; font-weight: 500; padding: 2px 6px;' );
				}
			}
		}
		else if ( itemType == 'closure' ){
			integrationItems = UncannyAutomator.closures;
		}

		// Search for the item
		$.each( integrationItems, function( index, item ){
			if ( item.code == itemCode ){
				// Save the item
				theItem = item;

				// Return false in the loop callback to break the loop
		 		return false;
			}
		});
	}

	// Return the item
	return theItem;
}

/**
 * In Automator DOM elements are deleted and created in each step, so the reference to a DOM element change.
 * Use this method to get a metabox, item of optionGroup
 *
 * @since 0.2
 *
 * @param {string}   element - Code of the element that you want to get
 * @param {object}   attributes - Contains required data to get the elements. Depends of the "element" value. Check the variable requiredData in each case.
 *
 * @return {object}  Object with DOM elements. The properties depend of "element"
 */

export function getDOMElements( element, attributes = {} ){
    // Create response object
    let response = {}

    // Define valid actions
    let validElements = [ 'metabox', 'items', 'item', 'optionGroup', 'lastItem', 'user-selector' ];

    // Check if the action is not undefined and if it's a valid action
    if ( isDefined( element ) && inArray( element, validElements ) ){
        // Create array that will store all the requires attributes properties based on the requested element
        let requiredData = [];

        switch ( element ){
            case 'metabox':
                // Extend default attributes object with default values
                attributes = $.extend({}, attributes );

                // Define required attributes properties
                requiredData = [ 'type' ];

                // Check if the attributes object has all the required properties
                if ( inArray( requiredData, Object.keys( attributes ) ) ){
                    attributes.cssSelector__metabox;

                    // Check the itemType
                    if ( attributes.type == 'trigger' ){
                        attributes.cssSelector__metabox = '#recipe-triggers-ui';
                    }
                    else if ( attributes.type == 'action' ){
                        attributes.cssSelector__metabox = '#recipe-actions-ui';
                    }

                    let $metabox = $( attributes.cssSelector__metabox )

                    response = $metabox;
                }
                else {
                    // Show to the developer what attributes are missing
                    let missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );
                    console.error( vsprintf( '%s: Some attributes are missing: %s', [ 'itemsObject', missingAttributes ] ) );
                }

                break;

            case 'items':
                // Extend default attributes object with default values
                attributes = $.extend({}, attributes );

                // Define required attributes properties
                requiredData = [ 'type' ];

                // Check if the attributes object has all the required properties
                if ( inArray( requiredData, Object.keys( attributes ) ) ){
                    let metabox = getDOMElements( 'metabox', {
                        type: attributes.type
                    });

                    // Get jQuery DOM element
                    let $items = metabox.find( '.item:not(.item--user-selector):not(.item--start):not(.item--closure)' );

                    // Add element to the response
                    response = $items;
                }
                else {
                    // Show to the developer what attributes are missing
                    let missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );
                    console.error( vsprintf( '%s: Some attributes are missing: %s', [ 'itemsObject', missingAttributes ] ) );
                }

                break;

            case 'item':
                // Extend default attributes object with default values
                attributes = $.extend({}, attributes );

                // Define required attributes properties
                requiredData = [ 'id' ];

                // Check if the attributes object has all the required properties
                if ( inArray( requiredData, Object.keys( attributes ) ) ){
                    // Define CSS Selector
                    attributes.cssSelector = '.uap .item[data-id="' + attributes.id + '"]';

                    // Get jQuery DOM element
                    let $item = $( attributes.cssSelector ),
                        $contentContainer = $item.find( '.item__content' );

                    // Add element to the response
                    response.item = $item;
                    response.contentContainer = $contentContainer;
                }
                else {
                    // Show to the developer what attributes are missing
                    let missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );
                    console.error( vsprintf( '%s: Some attributes are missing: %s', [ 'itemsObject', missingAttributes ] ) );
                }

                break;

            case 'optionGroup':
                // Extend default attributes object with default values
                attributes = $.extend({}, attributes );

                // Define required attributes properties
                requiredData = [ 'itemID', 'groupID' ];

                // Check if the attributes object has all the required properties
                if ( inArray( requiredData, Object.keys( attributes ) ) ){
                    // Get main object
                    let item = getDOMElements( 'item', { id: attributes.itemID } ).item;

                    // Define CSS Selector
                    attributes.cssSelector__tokenInSentence = '.item-title__token[data-token-id="' + attributes.groupID + '"]';
                    attributes.cssSelector__fieldsContainer = '.item-options__group[data-optiongroup="' + attributes.groupID + '"]';

                    let $tokenInSentence  = item.find( attributes.cssSelector__tokenInSentence ),
                        $fieldsContainer  = item.find( attributes.cssSelector__fieldsContainer ),
                        $optionsContainer = item.find( '.item-options' );

                    response.tokenInSentence  = $tokenInSentence;
                    response.fieldsContainer  = $fieldsContainer;
                    response.optionsContainer = $optionsContainer;
                }
                else {
                    // Show to the developer what attributes are missing
                    let missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );
                    console.error( vsprintf( '%s: Some attributes are missing: %s', [ 'itemsObject', missingAttributes ] ) );
                }

                break;

            case 'lastItem':
                // Extend default attributes object with default values
                attributes = $.extend({}, attributes );

                // Define required attributes properties
                requiredData = [ 'type' ];

                // Check if the attributes object has all the required properties
                if ( inArray( requiredData, Object.keys( attributes ) ) ){
                    // Get metabox
                    let metabox = getDOMElements( 'metabox', {
                        type: attributes.type
                    });

                    // Get last item
                    let lastItem = metabox.find( '.item:not(.item--user-selector):not(.item--start):not(.item--closure)' ).last();

                    response = lastItem;
                }
                else {
                    // Show to the developer what attributes are missing
                    let missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );
                    console.error( vsprintf( '%s: Some attributes are missing: %s', [ 'itemsObject', missingAttributes ] ) );
                }

                break;

            case 'user-selector':
                // Get user selector container
                response = $( '.user-selector' );

                break;
        }
    }
    else {
         // Has to add error code
        console.error( vsprintf( '%s: The element "%s" isn\'t defined', [ 'getDOMElements', element ] ) );
    }

    return response;
}

/**
 * Get tokens object
 * To be changed soon
 *
 * @since 0.5
 *
 * @param {string}  itemType - The item type, 'trigger', 'action' or 'closure'
 *
 * @return {object} Object with all the tokens
 */

export function getTokens( itemType, dataType ){
    let tokensList = [];

    // Create common tokens
    const commonTokens = {
        id:     'common',
        name:   UncannyAutomator.i18n.tokens.global.common,
        icon:   'uo-icon--bolt',
        tokens: [
            {
                id:   'site_name',
                type: 'text',
                name:  UncannyAutomator.i18n.tokens.global.siteName,
            },
        ]
    }

    // Check if it's a field in a trigger or user-selector section,
    // in an anonymous recipe then don't add user related tokens
    if ( ! ( UncannyAutomator.recipe.type == 'anonymous' && ( itemType == 'trigger' || itemType == 'user-selector' ) ) ){
        // Add user related tokens
        commonTokens.tokens.push(
            {
                id:   'user_id',
                type: 'int',
                name:  UncannyAutomator.i18n.tokens.global.userID,
            },
            {
                id:   'user_username',
                type: 'text',
                name:  UncannyAutomator.i18n.tokens.global.userUsername,
            },
            {
                id:   'user_firstname',
                type: 'text',
                name:  UncannyAutomator.i18n.tokens.global.userFirstName,
            },
            {
                id:   'user_lastname',
                type: 'text',
                name:  UncannyAutomator.i18n.tokens.global.userLastName,
            },
            {
                id:   'user_email',
                type: 'email',
                name:  UncannyAutomator.i18n.tokens.global.userEmail,
            },
            {
                id:   'user_displayname',
                type: 'text',
                name:  UncannyAutomator.i18n.tokens.global.userDisplay,
            },
            {
                id:   'reset_pass_link',
                type: 'url',
                name:  UncannyAutomator.i18n.tokens.global.userResetPassLink,
            }
        );
    }

    // Add other common tokens
    commonTokens.tokens.push(
        {
            id:   'admin_email',
            type: 'email',
            name:  UncannyAutomator.i18n.tokens.global.adminEmail,
        },
        {
            id:   'site_url',
            type: 'url',
            name:  UncannyAutomator.i18n.tokens.global.siteUrl,
        },
        {
            id:   'recipe_name',
            type: 'text',
            name:  UncannyAutomator.i18n.tokens.global.recipeName,
        },
        {
            id:   'current_date',
            type: 'text',
            name:  UncannyAutomator.i18n.tokens.global.currentDate,
        },
        {
            id:   'current_time',
            type: 'text',
            name:  UncannyAutomator.i18n.tokens.global.currentTime,
        }
    );

    // Global tokens
    tokensList.push( commonTokens );

    // Check if it's a field in a trigger or user-selector section,
    // in an anonymous recipe then don't add user related tokens
    if ( ! ( UncannyAutomator.recipe.type == 'anonymous' && ( itemType == 'trigger' || itemType == 'user-selector' ) ) ){
        let tokensGroupAdvanced = {
            id:         'advanced',
            name:       UncannyAutomator.i18n.tokens.global.advanced,
            icon:       'uo-icon--bolt',
            tokens:         [
                {
                    id:   'USERMETA:KEY',
                    type: 'text',
                    name:  UncannyAutomator.i18n.tokens.global.userMetaKey,
                },
            ]
        }

        tokensList.push( tokensGroupAdvanced );
    }

    let items = {};

    // Filter by itemType
    // If it isn't defined then return all items
    if ( isDefined( itemType ) ){
        if ( itemType == 'action' || itemType == 'closure' || itemType == 'user-selector' ){
            items = $.extend( {}, items, itemsObject( 'get', {
                filter: {
                    itemType: 'trigger'
                }
            }));
        }

        if ( itemType == 'closure' ){
            items = $.extend( {}, items, itemsObject( 'get', {
                filter: {
                    itemType: 'action'
                }
            }));
        }
    }
    else {
        items = jQuery.extend( true, {}, UncannyAutomator.recipe.items );
    }

    $.each( items, function( itemId, item ){
        let itemTokens = [];

        // Automatic tokens from the fields
        $.each( item.options, function( groupId, group ){
            $.each( group.fields, function( index, field ){
                let fieldAttributes = field.getAttributes();

                // Check the field type
                if ( fieldAttributes.fieldType == 'repeater' ){
                    // Iterate the rows
                    field.fieldRows.forEach(( row, index ) => {
                        // Get the row index
                        const rowIndex = index;

                        // Iterate the row fields
                        row.forEach(( Field ) => {
                            // Add field
                            itemTokens.push({
                                // Create the token
                                // ITEM_ID : ITEM_CODE : REPEATER_FIELD_ID : ROW_INDEX : FIELD_ID
                                id:      `${ item.id }:${ item.itemCode }:${ fieldAttributes.optionCode }:${ rowIndex }:${ Field.getAttributes().optionCode }`,
                                // Field name:
                                // REPEATER_FIELD_NAME - "Row" ROW_INDEX - FIELD_NAME
                                name:    `${ fieldAttributes.tokenName } - ${ vsprintf( UncannyAutomator.i18n.rowNumber, [ ( rowIndex + 1 ) ] ) } - ${ Field.getAttributes().tokenName }`,
                                type:    Field.getAttributes().fieldType == 'select' || Field.getAttributes().fieldType == 'textarea' ? 'text' : Field.getAttributes().fieldType,
                                value:   Field.getValue(),
                                fieldId: Field.getAttributes().optionCode
                            });
                        });
                    });
                }
                else if ( fieldAttributes.fieldType == 'group' ){
                    // Nothing here yet
                }
                else {
                    if( fieldAttributes.relevant_tokens ) {
                        $.each( fieldAttributes.relevant_tokens, function( index, title){
                            itemTokens.push({
                                id:      item.id + ':' + item.itemCode + ':' + index,
                                name:    title,
                                type:    fieldAttributes.fieldType == 'select' || fieldAttributes.fieldType == 'textarea' ? 'text' : fieldAttributes.fieldType,
                                value:   field.getValue(),
                                fieldId: fieldAttributes.optionCode
                            });
                        });
                    } else {
                        itemTokens.push({
                            id:      item.id + ':' + item.itemCode + ':' + fieldAttributes.optionCode,
                            name:    fieldAttributes.tokenName,
                            type:    fieldAttributes.fieldType == 'select' || fieldAttributes.fieldType == 'textarea' ? 'text' : fieldAttributes.fieldType,
                            value:   field.getValue(),
                            fieldId: fieldAttributes.optionCode
                        });
                    }
                }
            });
        });

        // Custom tokens
        $.each( item.tokens, function( index, token ){
            itemTokens.push({
                id:   item.id + ':' + token.tokenIdentifier + ':' + token.tokenId,
                name: token.tokenName,
                type: isDefined( token.tokenType ) ? token.tokenType : 'text',
            });
        });

        // Get sentence
        let itemSentence;
        if ( ! item.isMissing ){
            itemSentence = getGlobalItem( item.integrationCode, item.itemType, item.itemCode ).sentence;
        }
        else {
            itemSentence = item.sentence;
        }

        let tokensGroup = {
            id:             item.itemCode,
            name:           parseStringWithTokens( item, itemSentence, UncannyAutomator.recipe.items[ item.id ].meta ).html,
            integrationId:  item.integrationCode,
            tokens:         itemTokens
        }

        // Filter tokens using a custom function
        let filterTokens = ( tokensGroup, item ) => tokensGroup;
        if ( ! isEmpty( item.filterTokens ) ){
            // Try to overwrite empty function
            try {
                // Try to convert the string to function, but first remove the <script> tags and trim the string
                filterTokens = eval( `(${ item.filterTokens.replace( '<script>', '' ).replace( '</script>', '' ).trim() })` ); 
            } catch ( e ){ console.log( e ); console.log( item.filterTokens ); }
        }
        tokensGroup = filterTokens( tokensGroup, item );

        // Add the group of tokens
        tokensList.push( tokensGroup );
    });

    // Filter by field type
    // We're not filtering tokens if the {fieldType} is 'text' or 'url', those accepts all tokens
    if ( isDefined( dataType ) && ! inArray( dataType, [ 'text', 'textarea', 'url' ] ) ){

        let filteredTokensList = [];

        $.each( tokensList, function( index, tokenGroup ){
            let filteredTokens = [];

            $.each( tokenGroup.tokens, function( index, token ){
                // If we're filtering floats, then promote int tokens to float
                token.type = dataType != 'float' ? token.type : token.type == 'int' ? 'float' : token.type;

                // Check if the fieldType matchs
                // Remember that we're not filtering tokens if the {fieldType} is 'text' or 'url',
                // AND we are promoting int to float if {fieldType} is 'float'
                if ( dataType == token.type ){
                    filteredTokens.push( token );
                }
            });

            if ( filteredTokens.length > 0 ){
                tokenGroup.tokens = filteredTokens;
                filteredTokensList.push( tokenGroup );
            }
        });

        tokensList = filteredTokensList;
    }

    return tokensList;
}

/**
 * Get all valid field types.
 * Used for validation
 *
 * @since 0.4
 *
 * @return {array} All the valid field types
 */

export function getFieldTypes(){
    // Set valid field types
    let validFieldTypes = [ 'select', 'textarea', 'text', 'url', 'email', 'int', 'float', 'checkbox', 'radio', 'group', 'repeater' ];

    return validFieldTypes;
}

/**
 * Parse string with tokens
 *
 * @since 0.5
 *
 * @param {object}   item - The item
 * @param {string}   string - The string with the tokens
 * @param {object}   data - Object with the data to replace the text in the tokens.
 *
 * @return {object}  Object with information about the string
 *         {string}  response.plain - The string with the tokens replaced with the data
 *         {string}  response.html - The string with the tokens replaced with the data and enclosed in HTML tags
 *         {object}  response.elements
 *         {array}   response.elements.normal - Array of jQuery object. All the non-tokens part of the sentence
 *         {array}   response.elements.tokens - Array of jQuery object. All the tokens part of the sentence
 *         {array}   response.parts
 *         {object}  response.parts[ n ] - Object with information about each part of the sentence
 */

export function parseStringWithTokens( item = null, string = '', data = {} ){
    // Create response object
    let response = {
        plain:      '',
        html:       $( '<div/>' ),
        elements:   {
            normal: [],
            token: []
        },
        parts: []
    }

    // Trim string
    string = string.trim();

    // Check if the string isn't empty
    if ( ! isEmpty( string ) ){
        // Create regex
        let tokensRegex  = new RegExp( /\{\{(.*?)\}\}/gm ),
            normals      = [],
            tokens       = [],
            mappedString = string;

        // Get tokens
        let tokenNumber = 0,
            token;

        string = string.replace( tokensRegex, function( full_match, token ){
            let tokenParts = token.split( ':' );

            // Get the token placeholder text
            const tokenPlaceholderText = tokenParts[ 0 ];

            // Get the token value ID
            const tokenValueID = tokenParts[ 1 ];

            // Get the ID of the group of fields this token has to open
            // Check if the token should open a different group of options
            const optionsID = isDefined( tokenParts[ 2 ] ) ? tokenParts[ 2 ] : tokenValueID;

            token = {
                type:      'token',
                id:        tokenValueID,
                text:      tokenPlaceholderText,
                optionsID: optionsID,
                hasFields: isDefined( item.options[ optionsID ] )
            }

            // Check if we have data for the token
            if ( isDefined( data[ vsprintf( '%s_readable', [ token.id ] ) ] ) || isDefined( data[ token.id ] ) ){
                let readableText = data[ vsprintf( '%s_readable', [ token.id ] ) ];
                token.originalText  = token.text;
                token.text          = isDefined( readableText ) ? readableText : data[ token.id ];
            }

            // Add token
            tokens.push( token );

            // Add token to the mapped string
            mappedString = mappedString.replace( full_match, '{{token:' + tokenNumber + '}}' );
            tokenNumber++;

            return '{{v}}';
        });

        let stringParts = string.split( '{{v}}' );

        // Get normals
        let normalNumber = 0;
        $.each( stringParts, function( key, sentenceSection ){
            if ( sentenceSection != '' ){
                // Create normal part
                let normal = {
                    type: 'normal',
                    text: sentenceSection
                }

                // Add normal text
                normals.push( normal );

                // Add "normal" part to the mapped string
                mappedString = mappedString.replace( sentenceSection, '{{normal:' + normalNumber + '}}' );
                normalNumber++;
            }
        });

        // Create response.parts
        let match;

        while ( ( match = tokensRegex.exec( mappedString ) ) !== null ){
            // Get info
            let part = match[ 1 ].split( ':' );

            part = {
                type:  part[ 0 ],
                index: part[ 1 ]
            }

            // Get part object and add to parts
            if ( part.type == 'normal' ){
                response.parts.push( normals[ part.index ] );
            }
            else {
                response.parts.push( tokens[ part.index ] );
            }
        }

        // Create plain text and HTML versions
        $.each( response.parts, function( index, part ){
            // Plain text
            response.plain += part.text;

            // HTML
            if ( part.text != ' ' ){
                let elementAttributes,
                    text = part.text;

                // Check if first character is a space
                let startsWithSpace = text[ 0 ] == ' ';
                if ( startsWithSpace ){
                    text = text.slice( 1 );
                }

                // Check if last character is a space
                let endsWithSpace   = text[ text.length - 1 ] == ' ';
                if ( endsWithSpace ){
                    text = text.slice( 0, -1 );
                }

                if ( ( part.type == 'normal' ) || ( part.type == 'token' && ! part.hasFields && ! item.isMissing ) ){
                    elementAttributes = {
                        'class': 'item-title__normal'
                    }
                }
                else {
                    let CSSClass = 'item-title__token';

                    elementAttributes = {
                        'class':           CSSClass,
                        'data-token-id':   part.id,
                        'data-options-id': part.optionsID
                    }

                    // Check if it's using a valid ID
                    if ( isDefined( item.options[ part.optionsID ] ) ){
                        // Check if the section is valid
                        if ( item.options[ part.optionsID ].isValid ){
                            // Add filled class
                            elementAttributes[ 'class' ] += ' item-title__token--filled';
                        }
                    }

                    // Check if it's filled
                    if ( isDefined( part.originalText ) ){
                        // Add filled class
                        // elementAttributes[ 'class' ] += ' item-title__token--filled';

                        // Define text of the token
                        let abbreviatedText = abbreviateText( text, 30 );

                        // Check if the text was long enough to abbreviate it
                        if ( abbreviatedText.isAbbreviated ){
                            // Add tooltip with the complete text
                            elementAttributes[ 'uap-tooltip' ] = text;

                            // Replace original text
                            text = abbreviatedText.text;
                        }
                    }
                }

                let element = $( '<span/>', elementAttributes ).text( decodeEntities( text ) );

                // Add to the elements array
                response.elements[ part.type ].push( element );

                // Check if we have to preppend a space
                if ( startsWithSpace ){
                    // response.html.append( ' ' );
                }

                // Append HTML
                response.html.append( element ); 

                /*

                // Append to the HTML
                // 2.0, remove "item-title__normal"
                // If it's a token, add the element
                if ( part.type == 'token' ){
                    response.html.append( element ); 
                }
                // Otherwise, add the text
                else {
                    response.html.append( text );
                }

                */

                // Check if we have to append a space
                if ( endsWithSpace ){
                    // response.html.append( ' ' );
                }
            }
            else {
                // response.html.append( ' ' );
            }
        });

        // Convert array of jQuery elements into wrapped set of elements
        response.elements.normal = $( response.elements.normal ).map( function(){ return this.get() });
        response.elements.token  = $( response.elements.token ).map( function(){ return this.get() }).filter( ':not(.item-title__normal)' );
    }

    return response;
}

/**
 * Determine if a variable is set and is not NULL
 *
 * @since 0.2
 *
 * @param  {mixed}      variable - The variable being evaluated
 * @return {boolean}    TRUE if the variable is defined
 */

export function isDefined( variable ){
    // Returns true if the variable is undefined
    return typeof variable !== 'undefined' && variable !== null;
}

/**
 * Determine whether a variable is empty
 *
 * @since 0.2
 *
 * @param   {mixed}     variable - The variable being evaluated
 * @return  {boolean}   TRUE if the variable is empty
 */

export function isEmpty( variable ){
    let response = true;

    // Check if the variable is defined, otherwise is empty
    if ( isDefined( variable ) ){
        // Check if it's array
        if ( $.isArray( variable ) ){
            response = variable.length == 0;
        }
        else if ( isObject( variable ) ){
            response = $.isEmptyObject( variable );
        }
        else {
            response = variable == '';
        }
    }

    return response;
}

/**
 * Check if two strings are equal
 *
 * @since 0.2
 *
 * @param   {string}    string1 - String to be compared
 * @param   {string}    string2 - String to be compared
 * @param   {object}    settings - Object with settings
 * @param   {boolean}   settings.caseSensitive=true - If true, uppercase and lowercase chars will be treated as distinct
 *
 * @return  {boolean} TRUE if both strings are equal
 */

export function compareStrings( string1, string2, settings = { caseSensitive: true } ){
    // Create response variable
    let response = false;

    // Check if it's case sensitive
    if ( ! settings.caseSensitive ){
        response = string1.toString().toLowerCase() == string2.toString().toLowerCase();
    }
    else {
        response = string1.toString() == string2.toString();
    }

    return response;
}

/**
 * Check if a value exists in an array
 * If the needle is an array then it will check if all his elements exists in the haystack
 *
 * @since 0.2
 *
 * @param   {mixed}     needle - The searched value. The comparison between values is strict. If the needle is an array then it will check if all his elements exists in the haystack
 * @param   {array}     haystack - An array through which to search.
 *
 * @return  {boolean} TRUE if needle is found in the array or if needle is an array, TRUE if all the elements from the needle are in haystack
 */

export function inArray( needle, haystack ){
    let response = false;

    if ( Array.isArray( needle ) ){
        // Check that all the elements from the array "value" are in the array "array"
        response = arrayDifference( needle, haystack ).length == 0;
    }
    else {
        // Check if one element is in an array
        response = $.inArray( needle, haystack ) !== -1;
    }

    return response;
}

/**
 * Compare the values of two arrays and get the difference
 * Compares array1 against one array and returns the values in array1 that are not present in array2.
 * Examples:
 * +-------------------+-------------+--------------+
 * |      array1       |   array2    |    return    |
 * +-------------------+-------------+--------------+
 * | [ 1, 2, 3, 4, 5 ] | [ 1, 3, 4 ] | [ 2, 5]      |
 * | [ 1, 2, 3 ]       | [ 2, 3 ]    | [ 1 ]        |
 * | [ 2, 3 ]          | [ 1, 2, 3 ] | []           |
 * | [ 1, 2 ]          | [ 1, 2 ]    | []           |
 * | [ '1', '2' ]      | [ 1, 2 ]    | [ '1', '2' ] |
 * +-------------------+-------------+--------------+
 *
 * @since 0.2
 *
 * @param   {array} array1 - The array to compare from
 * @param   {array} array2 - An array to compare against
 *
 * @return  {array} Array containing all the entries from array1 that are not present in array2
 */

export function arrayDifference( array1, array2 ){
    return $( array1 ).not( array2 ).get();
}

/**
 * JavaScript vsprintf implementation
 * Return a formatted string
 *
 * @requires sprintf-js
 *
 * @since 0.2
 *
 * @param {string}  format - The placeholders in the format string are marked by % and are followed by one or more of these elements, in this order:
 *    1. An optional number followed by a $ sign that selects which argument index to use for the value.
 *       If not specified, arguments will be placed in the same order as the placeholders in the input string.
 *    2. An optional + sign that forces to preceed the result with a plus or minus sign on numeric values. By default, only the - sign is used on negative numbers.
 *    3. An optional padding specifier that says what character to use for padding (if specified).
 *       Possible values are 0 or any other character precedeed by a ' (single quote). The default is to pad with spaces.
 *    4. An optional - sign, that causes sprintf to left-align the result of this placeholder. The default is to right-align the result.
 *    5. An optional number, that says how many characters the result should have.
 *       If the value to be returned is shorter than this number, the result will be padded.
 *       When used with the j (JSON) type specifier, the padding length specifies the tab size used for indentation.
 *    6. An optional precision modifier, consisting of a . (dot) followed by a number, that says how many digits should be displayed for floating point numbers.
 *       When used with the g type specifier, it specifies the number of significant digits. When used on a string, it causes the result to be truncated.
 *    7. A type specifier that can be any of:
 *    7.1  %  yields a literal % character
 *    7.2  b  yields an integer as a binary number
 *    7.3  c  yields an integer as the character with that ASCII value
 *    7.4  d or i  yields an integer as a signed decimal number
 *    7.5  e  yields a float using scientific notation
 *    7.6  u  yields an integer as an unsigned decimal number
 *    7.7  f  yields a float as is; see notes on precision above
 *    7.8  g  yields a float as is; see notes on precision above
 *    7.9  o  yields an integer as an octal number
 *    7.10 s  yields a string as is
 *    7.11 t  yields true or false
 *    7.12 T  yields the type of the argument1
 *    7.13 v  yields the primitive value of the specified argument
 *    7.14 x  yields an integer as a hexadecimal number (lower-case)
 *    7.15 X  yields an integer as a hexadecimal number (upper-case)
 *    7.16 j  yields a JavaScript object or array as a JSON encoded string
 * @param {array}  arguments_array - array of arguments
 *
 * @return {string} Return array values as a formatted string according to format.
 */

export function vsprintf( format, arguments_array ){
    var vsprintf = require('sprintf-js').vsprintf;
    return vsprintf( format, arguments_array );
}

/**
 * Find whether a variable is a string
 *
 * @since 0.2
 *
 * @param   {mixed}     variable - The variable being evaluated
 * @return  {boolean}   TRUE if the variable is an string
 */

export function isString( string ){
    return typeof string === 'string';
}

/**
 * Find whether a variable is an object
 *
 * @since 0.2
 *
 * @param   {mixed}     variable - The variable being evaluated
 * @return  {boolean}   TRUE if the variable is an object
 */

export function isObject( variable ){
    return variable !== null && typeof variable === 'object';
}

/**
 * Remove all spaces from a string
 *
 * @since 0.3
 *
 * @param   {string}    String
 * @return  {string}    String without spaces
 */

export function removeSpaces( string ){
    return string.replace( /\s/g, '' );
}

/**
 * Replace curly braces
 *
 * @since 0.5
 *
 * @param {string} string - The string to be searched
 * @param {string} start - The value to replace the for {{
 * @param {string} end - The value to replace the for }}
 *
 * @return {string} The value with the replaced values
 */

export function replaceCurlyBraces( string = '', start = '', end = '' ){
    string = string.replace( /{{/g, start );
    string = string.replace( /}}/g, end );
    return string;
}

/**
 * Creates a console log label
 *
 * @since  1.1
 *
 * @param  {String} text - Text to show after the label
 * @param  {Boolean} isGroup - Define if we have to do a console.group instead of console.log
 */

export function consoleLogLabel( text = '', isGroup = false, options = {} ){
	// Check if the user defined a custom background color
	if ( ! isDefined( options.bgColor ) ){
		options.bgColor = '#0790e8';
	}

    // Define console log Automator style
    let labelStyle = `background: ${ options.bgColor }; color: #fff; font-weight: 500; padding: 2px 6px; font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;`;

    // Do console log
    if ( isGroup ){
        if ( isDefined( options.collapseGroup ) && options.collapseGroup ){
            console.groupCollapsed( '%cAutomator', labelStyle, text );
        }
        else {
            console.group( '%cAutomator', labelStyle, text );
        }
    }
    else {
        console.log( '%cAutomator', labelStyle, text );
    }
}

/**
 * Creates a custom console log with colors to
 * represent success and failure
 *
 * @since  2.0
 *
 * @param  {String} status - Message type. Use 'success' or 'error'
 * @param  {String} text - Text to show after the label
 */

export function consoleLogResult( status = 'error', text = '' ){
    // Define console log Automator style
    let labelStyle = 'color: #fff; padding: 2px 7px; font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif; font-weight: normal';

    // Add error or success colors
    if ( status == 'error' ){
        labelStyle = `background: #f13c3c; ${ labelStyle }`;
    }

    if ( status == 'success' ){
        labelStyle = `background: #40b528; ${ labelStyle }`;
    }

    // Do console log
    console.log( `%c${ text }`, labelStyle );
}

/**
 * Escape HTML characters
 *
 * @since  1.1
 * @param  {String} html The HTML
 * @return {String}      Scaped HTML
 */

export function escapeHTML( html = '' ){
    let tagsToReplace = {
        '&': '&amp;',
        '<': '&lt;',
        '>': '&gt;'
    };

    let replaceTag = function( tag ){
        return tagsToReplace[ tag ] || tag;
    }

    return html.replace( /[&<>]/g, replaceTag );
}

/**
 * Abbreviate long texts. This function will convert a string
 * like "123456789" to "123...789"
 * 
 * @param  {String} text    The string
 * @param  {Int}    length  The max length of the string
 * @return {Object}         Response two properties, isAbbreviated and text
 */

export function abbreviateText( text = '', length = 10 ){
    // Default response
    let response = {
        isAbbreviated: false,
        text: text
    }

    // Only abbreviate the text if the text is longer than the length
    if ( text.length > length ){
        // Get length of each parts (start and end)
        let partLengths = Math.floor( length / 2 );

        // Get parts
        let textParts = {
            start: text.substring( 0, partLengths ),
            end:   text.slice( -partLengths )
        }

        // Create abbrevited text
        response.text = `${ textParts.start } ... ${ textParts.end }`;
        response.isAbbreviated = true;
    }

    // Return response
    return response;
}

/**
 * Get an unique ID
 */

export function getUniqueId(){
    // Math.random should be unique because of its seeding algorithm.
    // Convert it to base 36 (numbers + letters), and grab the first 9 characters
    // after the decimal.
    return '_' + Math.random().toString( 36 ).substr( 2, 9 );
}

/**
 * Insert parameter to an URL.
 * If the parameter already exists it will update it
 * 
 * @param  {String} url        The URL
 * @param  {Object} parameters The parameters
 * @param  {Object} options    The options. It supports two properties, "setAsURL" and "refresh"
 * @return {String}            The URL with the parameters
 */
export function addParametersToURL( url = '', parameters = {}, options = {} ){
    // If the URL is empty, use the site URL
    if ( isEmpty( url ) ){
        url = window.location.href;
    }
    
    // Try to get an URL object
    try {
        url = new URL( url );
    } catch ( e ){
        // If something goes wrong, just return the original URL
        return url;
    }

    // Get the current parameters in the URL
    let urlParameters = url.search.substr( 1 ).split( '&' );

    // Iterate the parameters
    for ( let [ key, value ] of Object.entries( parameters ) ){
        key   = encodeURI( key );
        value = encodeURI( value );

        let i = urlParameters.length;
        let x;

        while ( i-- ){
            x = urlParameters[ i ].split( '=' );

            if ( x[ 0 ] == key ){
                x[ 1 ] = value;
                urlParameters[i] = x.join( '=' );
                break;
            }
        }

        if ( i < 0 ){
            urlParameters[ urlParameters.length ] = [ key, value ].join( '=' );
        }
    }

    // Remove empty parameters
    urlParameters = urlParameters.filter( parameter => ! isEmpty( parameter ) );

    // Add parameters to the URL
    url.search = urlParameters.join( '&' );

    // Set default options
    options = $.extend({}, {
        setAsURL: false,
        refresh:  false
    }, options );

    // Check if we have to set it as the current URL
    if ( options.setAsURL ){
        // Check if we have to reload
        if ( options.refresh ){
            // This will reload the page, it's likely better to store this until finished
            window.location.href = url.href; 
        }
        else {
            // Get title of the current page
            let pageTitle = document.title;

            // Push history and update URL
            window.history.pushState({}, pageTitle, url.href );
        }
    }

    // Return URL
    return url.href;
}

/**
 * Decodes HTML entities
 *
 * @since  2.1.4
 * @param  {String} encodedString The string with encoded entities
 * @return {String}               The same string, with the entities decoded
 */
export function decodeEntities( encodedString = '' ){
    const $textarea = document.createElement( 'textarea' );
    $textarea.innerHTML = encodedString;
    return $textarea.value;
}


