import {
	isDefined,
	vsprintf
} from './Utilities.js';

class SendFeedback {

	constructor(){
		this.build();
	}

	build(){
		let sendFeedbackContainer = $( '<div/>', { 'class': 'uap-report-bug' });

		// Temporal

		/*
		if ( this.showAgain() ){
			sendFeedbackContainer.addClass( 'uap-report-bug--open' );

			let sendFeedbackOverlay = $( '<div/>', { 'class': 'uap-report-bug__overlay' });
			let sendFeedbackCircle = $( '<div/>', { 'class': 'uap-report-bug-circle' });
			let sendFeedbackCircleContainer = $( '<div/>', { 'class': 'uap-report-bug-circle__container' });

			let sendFeedbackCircleText = $( '<div/>', { 'class': 'uap-report-bug-circle__text' });
			sendFeedbackCircleText.text( vsprintf( UncannyAutomator.i18n.sendFeedback.message, [ 'Uncanny Automator' ] ) );
			sendFeedbackCircleText.appendTo( sendFeedbackCircleContainer );

			let sendFeedbackDontShowContainer = $( '<div/>', { 'class': 'uap-report-bug-circle__dont-show' });
			let sendFeedbackDontShowLabel = $( '<label/>', { 'class': 'uap-report-bug-circle-checkbox' });
			let sendFeedbackDontShowSpan = $( '<span/>', { 'class': 'uap-report-bug-circle__span' });
			sendFeedbackDontShowSpan.text( UncannyAutomator.i18n.sendFeedback.dontShowAgain );
			sendFeedbackDontShowSpan.appendTo( sendFeedbackDontShowLabel );
			let sendFeedbackDontShowInput = $( '<input/>', { 'class': 'uap-report-bug-circle-checkbox__input', 'type': 'checkbox' });
			let sendFeedbackDontShowInputRender = $( '<div/>', { 'class': 'uap-report-bug-circle-checkbox__render' });
			sendFeedbackDontShowInputRender.prependTo( sendFeedbackDontShowLabel );
			sendFeedbackDontShowInput.prependTo( sendFeedbackDontShowLabel );
			sendFeedbackDontShowLabel.appendTo( sendFeedbackDontShowContainer );
			sendFeedbackDontShowContainer.appendTo( sendFeedbackCircleContainer );

			let sendFeedbackFooter = $( '<div/>', { 'class': 'uap-report-bug-circle__footer' });
			let sendFeedbackFooterBtn = $( '<div/>', { 'class': 'uap-report-bug-circle__btn' });
			sendFeedbackFooterBtn.text( UncannyAutomator.i18n.sendFeedback.gotIt );
			sendFeedbackFooterBtn.appendTo( sendFeedbackFooter );
			sendFeedbackFooter.appendTo( sendFeedbackCircleContainer );

			sendFeedbackCircleContainer.appendTo( sendFeedbackCircle );
			sendFeedbackCircle.appendTo( sendFeedbackOverlay );
			sendFeedbackOverlay.appendTo( sendFeedbackContainer );

			// Bind
			this.bind( sendFeedbackFooterBtn, sendFeedbackDontShowInput, sendFeedbackOverlay, sendFeedbackContainer );
		}
		*/

		// Check version
		let version,
			utmSource;

		if ( ! isDefined( UncannyAutomator.wp ) || ! UncannyAutomator.wp ){
			version   = vsprintf( 'Uncanny Automator %s', [ UncannyAutomator.version ] );
			utmSource = 'uncanny_automator';
		}
		else {
			version   = vsprintf( 'Uncanny Automator %s %s', [ 'Pro', UncannyAutomator.version ] );
			utmSource = vsprintf( 'uncanny_automator_%s', [ 'pro' ] );
		}

		let sendFeedbackButton = $( '<a/>', {
			'class': 'uap-report-bug__btn',
			'uap-tooltip': UncannyAutomator.i18n.sendFeedback.title,
			'uap-flow': 'left',
			'href': vsprintf( 'https://automatorplugin.com/feedback/?version=%s&utm_source=%s&utm_medium=%s', [ version, utmSource, 'send_feedback' ] ),
			'target': '_blank'
		});

		let sendFeedbackButtonIcon = $( '<span/>', { 'class': 'uap-report-bug__icon' });
		sendFeedbackButton.append( sendFeedbackButtonIcon );
		sendFeedbackButton.appendTo( sendFeedbackContainer );

		// Append to the body

		$('body').append( sendFeedbackContainer );
	}

	bind( close_btn, dont_show_again_checkbox, overlay, mainContainer ){

		close_btn.on( 'click', function(){
			// Storage
			if ( dont_show_again_checkbox.is(':checked') ){
				localStorage.uapSendFeedbackWelcome = 'hide';
			}

			// Hide
			overlay.fadeOut(150);
			mainContainer.removeClass( 'uap-report-bug--open' );
		});

	}

	showAgain(){
		/**
		 * Version 2.0 introduced the recipe selector, which is
		 * shown as an overlay on page load. As having two overlays
		 * is bad for UX, we will hide this one
		 */
		
		let output = false;

		/*
		let output = true;

		if ( localStorage.uapSendFeedbackWelcome && localStorage.uapSendFeedbackWelcome == 'hide' ){
			output = false;
		}
		*/

		return output;
	}
}

export default SendFeedback;


