import {
    restCall,
    getGlobalItem,
    isDefined,
    replaceCurlyBraces,
    addParametersToURL
} from './Utilities.js';

import Modal from './Modal.js';

import Triggers from './Triggers.js';
import Actions  from './Actions.js';

class ItemHeaderMenu {
    /**
     * Create item actions
     *
     * @since 0.5
     *
     * @return jQuery DOM element with the item actions
     */

    constructor( itemType, integrationID, itemCode, itemId ){
        this.itemType      = itemType;
        this.integrationID = integrationID;
        this.itemCode      = itemCode;
        this.itemId        = itemId;
        this.item          = UncannyAutomator.recipe.items[ itemId ];

        return this.createItemActions();
    }

    createItemActions(){
        // Create elements
        let container     = $( '<div/>', { 'class': 'item-actions' }),
            btn           = $( '<div/>', { 'class': 'item-actions__btn' }),
            btnIcon       = $( '<i/>', { 'class': 'uo-icon uo-icon--ellipsis-h-alt' }),
            dropdown      = $( '<div/>', { 'class': 'item-actions__dropdown' }),
            dropdownUl    = $( '<ul/>', { 'class': 'item-actions-dropdown__ul' });

        btn.append( btnIcon );
        container.append( btn );

        // Delete
        let deleteAction  = $( '<li/>', { 'class': 'item-actions-dropdown__action' }),
            deleteText    = $( '<span/>' ).text( UncannyAutomator.i18n.delete );

        deleteAction.append( deleteText );
        dropdownUl.append( deleteAction );

        // Support
        if ( isDefined( this.itemType ) && isDefined( this.itemCode ) ) {
            if ( ! isDefined( this.item) || isDefined( this.item ) && ! this.item.isMissing ){
                let globalItemData = getGlobalItem( this.integrationID, this.itemType, this.itemCode );

                if ( isDefined( globalItemData.support_link ) ){
                    let supportAction = $( '<a/>', {
                        'href':  addParametersToURL( globalItemData.support_link, {
                            'utm_source':  'uncanny_automator',
                            'utm_medium':  'action_menu',
                            'utm_content': 'support_menu_link'
                        }),
                        'class': 'item-actions-dropdown__action',
                        'target': '_blank'
                    }),
                        supportText   = $( '<span/>' ).text( UncannyAutomator.i18n.support );

                    supportAction.append( supportText );
                    dropdownUl.append( supportAction );
                }
            }
        }

        dropdown.append( dropdownUl );
        container.append( dropdown );

        // Listen clicks to the open button
        btn.on( 'click', () => {
            // Evaluate if it's open
            const isOpen = container.hasClass( 'item-actions--open' );

            // Check if it's not open
            if ( ! isOpen ){
                // Open the dropdown
                container.addClass( 'item-actions--open' );

                // Listen clicks outside the dropdown
                $( document ).on( 'mouseup.uap-actions-click-outside', ( event ) => {
                    if ( ! container.is( event.target ) && container.has( event.target ).length === 0 ){
                        // Close dropdown
                        container.removeClass( 'item-actions--open' );

                        // Remove event listener to listen clicks outside the dropdown
                        $( document ).off( 'mouseup.uap-actions-click-outside' );
                    }
                });           
            }
            else {
                // Close dropdown
                container.removeClass( 'item-actions--open' );

                // Remove event listener to listen clicks outside the dropdown
                $( document ).off( 'mouseup.uap-actions-click-outside' );
            }
        });

        // Delete
        deleteAction.on( 'click', () => {
            if ( isDefined( this.itemId ) && UncannyAutomator.recipe.isLive && UncannyAutomator.recipe.items[ this.itemId ].isLive ){
                // Hide dropdown with options
                container.removeClass( 'item-actions--open' );

                // Create modal box
                // Read Modal.js comments for more information
                let modal = new Modal({
                    title:       UncannyAutomator.i18n.confirmationModal.title,
                    warning:     replaceCurlyBraces( UncannyAutomator.i18n.confirmationModal.deleteWarning, '<strong>', '</strong>' ),
                    buttons: {
                        cancel:  UncannyAutomator.i18n.cancel,
                        confirm: UncannyAutomator.i18n.confirm,
                    }
                });

                modal.setEvents({
                    afterSetEvents: () => {
                        modal.show();
                    },
                    onConfirm: () => {
                        let elements        = modal.getElements();
                        let confirmButton   = elements.modal.box.buttons.confirm;

                        // Add loading class to button
                        confirmButton.addClass( 'uap-btn--loading' ).prop( 'disabled', true );

                        // Do call and hide
                        restCall( 'delete', {
                            'ID': this.itemId
                        }, () => {
                            // Destroy modal
                            modal.destroy();

                            // Refresh UI
                            if ( this.itemType == 'trigger' ){
                                new Triggers();

                                // Check if it's an anonymous recipe
                                if ( UncannyAutomator.recipe.type == 'anonymous' ){
                                    // Render the Actions metabox again
                                    // We're doing this because we need to update the user selector
                                    new Actions();
                                }
                            }
                            
                            if ( this.itemType == 'action' || this.itemType == 'closure' ){
                                new Actions();
                            }

                            // Check if the recipe is still valid, if it isn't then make the recipe draft
                            if ( ! UncannyAutomator.recipe.isValid ){
                                UncannyAutomator.recipe.publish.statusToggle.setValue( false );
                            }
                        }, () => {
                            // Destroy modal
                            modal.destroy();

                            console.log( ' ERROR-TODO. Couldn\'t delete the item' );

                            // Refresh UI
                            if ( this.itemType == 'trigger' ){
                                new Triggers();
                            }
                            
                            if ( this.itemType == 'action' || this.itemType == 'closure' ){
                                new Actions();
                            }
                        });
                    },
                    afterHide: function(){
                        modal.destroy();
                    }
                });
            }
            else {
                // Delete without warning
                if ( isDefined( this.itemId ) ){
                    // Set loading to the "delete" option
                    deleteAction.addClass( 'item-actions-dropdown__action--loading' );

                    // Do the AJAX request to delete it
                    restCall( 'delete', {
                        'ID': this.itemId
                    }, () => {
                        // Reset UI
                        if ( this.itemType == 'trigger' ){
                            new Triggers();

                            // Check if it's an anonymous recipe
                            if ( UncannyAutomator.recipe.type == 'anonymous' ){
                                // Render the Actions metabox again
                                // We're doing this because we need to update the user selector
                                new Actions();
                            }
                        }
                        
                        if ( this.itemType == 'action' || this.itemType == 'closure' ){
                            new Actions();
                        }
                    }, () => {
                        console.log( ' ERROR-TODO. Couldn\'t delete the item' );

                        // Reset UI
                        if ( this.itemType == 'trigger' ){
                            new Triggers();
                        }
                        
                        if ( this.itemType == 'action' || this.itemType == 'closure' ){
                            new Actions();
                        }
                    });
                }
                else {
                    // Reset UI
                    if ( this.itemType == 'trigger' ){
                        new Triggers();
                    }
                    
                    if ( this.itemType == 'action' ){
                        new Actions();
                    }
                }
            }
        });

        return container;
    }
}

export default ItemHeaderMenu;


