import {
	userSelectorObject,
	recipeObject,
	restCall,
	getDOMElements,
	isDefined,
	isEmpty,
	vsprintf
} from './Utilities';

import Field from './Field.js';

import Actions from './Actions';

import RecipeNotices from './RecipeNotices';

class UserSelector {
	constructor(){
		// On page load, get current step
		UncannyAutomator.recipe.userSelector.step = userSelectorObject( 'get-step' );

		// Render
		this.render();
	}

	render(){
		// Create variable to save the content to be added
		let $content = ``;

		// Get content depending of the step
		switch ( UncannyAutomator.recipe.userSelector.step ){
			case 'select-source':
				// Set validation to false
				// We're setting it to false here in case the user already
				// has valid values, but wanted to go to the first step
				// to change the source
				UncannyAutomator.recipe.userSelector.isValid = false;

				// Validate recipe
				recipeObject( 'validate' );

				// Where the recipe creator select the source of the user.
				// The options are "Existing user" and "New user".
				$content = this.getSelectSourceNodes();
				break;

			case 'set-options':
				// Where the recipe creator defines values of the fields
				$content = this.getOptionsNodes();
				break;

			case 'show-sentence':
				// Validate recipe
				recipeObject( 'validate' );

				// Set error mode
				UncannyAutomator.recipe.errorMode = false;

				// Show error
				new RecipeNotices();

				// When the data is complete and valid.
				$content = this.getSentenceNodes();
				break;
		}

		// Get container
		let $metabox = getDOMElements( 'metabox', {
            type: 'action'
        });

        console.log( '%cRender User Selector', 'background: red; color: #fff; padding: 2px 10px;');

        // Append content
        $metabox.prepend( $content );
	}

	getSelectSourceNodes(){
		// Create elements
		let $userSelector = $( `
			<div class="user-selector">
				<div class="item item--user-selector">
					<div class="item__content">
						<div class="item-integrations">
							<div class="item-integration user-selector-option user-selector-option--existing-user" data-source="existingUser">
								<div class="item-integration__logo"></div>
								<div class="item-integration__name">
									${ UncannyAutomator.i18n.userSelector.userType.existing }
								</div>
							</div>
							<div class="item-integration user-selector-option user-selector-option--new-user" data-source="newUser">
								<div class="item-integration__logo"></div>
								<div class="item-integration__name">
									${ UncannyAutomator.i18n.userSelector.userType.new }
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>	
		` );

		// Add header
		$userSelector.find( '.item' ).prepend( this.getHeaderNodes() );

		// Get user type boxes
		let $userTypeButtons = $userSelector.find( '.user-selector-option' );

		// Bind click on the user type buttons
		$userTypeButtons.on( 'click', ( event ) => {
			// Get clicked button
			let $option = $( event.currentTarget );

			// Get user type
			let userSource = $option.data( 'source' );
			
			// Save selected source in main object
			userSelectorObject( 'set-source', {
				source: userSource
			});

			// Define next step
			UncannyAutomator.recipe.userSelector.step = 'set-options';

			// Render the actions metabox again
			new Actions();
		});

		// Return element
		return $userSelector;
	}

	getOptionsNodes(){
		// Create elements
		let $optionsContainer = $( `
			<div class="user-selector">
				<div class="item item--user-selector">
					<div class="item__content">
						<div class="item-options"></div>
					</div>
				</div>
			</div>
		` );

		// Add header
		$optionsContainer.find( '.item' ).prepend( this.getHeaderNodes() );

		// Get options container, we will add the fields there
		let $fieldsContainer = $optionsContainer.find( '.item-options' );

		// Add fields
		$fieldsContainer.append( this.getOptionsFields() );

		// Return node
		return $optionsContainer;
	}

	getOptionsFields(){
		// Create container
		const $container = $( `<div></div>` );

		// Create the fields container
		let $fields = $( `<div class="item-options__group"></div>` );

		// Create array to save all the fields
		let optionsFields = [];

		// Update fields data object so it's an object and not an array
		userSelectorObject( 'update-fields', {
			source: 'existingUser',
			fields: {}
		});

		userSelectorObject( 'update-fields', {
			source: 'newUser',
			fields: {}
		});

		// Get WordPress roles, but remove the "administrator" one
		let roles = UncannyAutomator.recipe.userSelector.resources.roles;
		delete roles.administrator;

		// Check what's the source
		switch ( UncannyAutomator.recipe.userSelector.source ){
			case 'existingUser':
				// Create group
				let $groupOfFields = $( `<div class="user-selector__field-group user-selector__field-group--two-columns"></div>` );

				// Create "Unique field" field
				let uniqueField = new Field( 'select', {
					itemType: 'user-selector',
					optionCode: 'uniqueField',
					label: UncannyAutomator.i18n.userSelector.existingUser.uniqueFieldLabel,
					currentValue: UncannyAutomator.recipe.userSelector.fields.existingUser.uniqueField,
					isRequired: true,
					options: [
						{
							value: 'email',
							text:  UncannyAutomator.i18n.userSelector.existingUser.uniqueFieldOptionEmail
						},
						{
							value: 'id',
							text:  UncannyAutomator.i18n.userSelector.existingUser.uniqueFieldOptionId
						},
						{
							value: 'username',
							text:  UncannyAutomator.i18n.userSelector.existingUser.uniqueFieldOptionUsername
						},
					]
				});
				optionsFields.push( uniqueField );

				// Create value field
				let valueField = new Field( 'text', {
					itemType: 'user-selector',
					optionCode: 'uniqueFieldValue',
					currentValue: UncannyAutomator.recipe.userSelector.fields.existingUser.uniqueFieldValue,
					label: UncannyAutomator.i18n.userSelector.existingUser.valueFieldLabel,
					placeholder: UncannyAutomator.i18n.userSelector.existingUser.valueFieldPlaceholder,
					isRequired: true,
					supportsTokens: true
				});
				optionsFields.push( valueField );

				// Create and add form element
				let $uniqueFieldFormElement = uniqueField.getFormElement();
				$groupOfFields.append( $uniqueFieldFormElement );

				let $valueFieldFormElement = valueField.getFormElement();
				$groupOfFields.append( $valueFieldFormElement );

				// Add elements to the main container
				$fields.append( $groupOfFields );

				// Create field to decide what to do if the user doesn't exist
				let fallbackField = new Field( 'radio', {
					itemType: 'user-selector',
					optionCode: 'fallback',
					name: 'user-selector-existing-user-fallback',
					label: UncannyAutomator.i18n.userSelector.existingUser.createNewUserFieldLabel,
					isRequired: true,
					currentValue: UncannyAutomator.recipe.userSelector.fields.existingUser.fallback,
					options: [
						{
							value: 'create-new-user',
							text: UncannyAutomator.i18n.userSelector.existingUser.createNewUserFieldOptionCreateUser
						},
						{
							value: 'do-nothing',
							text: UncannyAutomator.i18n.userSelector.existingUser.createNewUserFieldOptionDoNothing
						}
					]
				});
				optionsFields.push( fallbackField );

				// Create and add form element
				let $fallbackFieldFormElement = fallbackField.getFormElement();
				$fields.append( $fallbackFieldFormElement );

				// Listen changes
				$uniqueFieldFormElement.on( 'automator:field:change', () => {
					// Get and save value
					userSelectorObject( 'update-fields', {
						source: 'existingUser',
						fields: {
							uniqueField: uniqueField.getValue()
						}
					});
				});

				$valueFieldFormElement.on( 'automator:field:change', () => {
					// Get and save value
					userSelectorObject( 'update-fields', {
						source: 'existingUser',
						fields: {
							uniqueFieldValue: valueField.getValue()
						}
					});
				});

				// Listen changes
				$fallbackFieldFormElement.on( 'automator:field:change', () => {
					// Get and save value
					userSelectorObject( 'update-fields', {
						source: 'existingUser',
						fields: {
							fallback: fallbackField.getValue()
						}
					});

					// Render the actions metabox again
					new Actions();
				});

				// Check if we have to add more fields or the do-nothing notice
				if ( UncannyAutomator.recipe.userSelector.fields.existingUser.fallback == 'do-nothing' ){
					// Create "Do nothing" notice
					const $doNothingNotice = $( `
						<div class="user-selector__warning">
							${ UncannyAutomator.i18n.userSelector.existingUser.doNothingMessage }
						</div>
					` );

					$fields.append( $doNothingNotice );
				}
				else if ( UncannyAutomator.recipe.userSelector.fields.existingUser.fallback == 'create-new-user' ){
					// Create container
					let $subOptionsContainer = $( '<div/>', {
						'class': 'item-options'
					});

					// Create new users fields
					let newUserFields = [
						{
							itemType:   'user-selector',
							fieldType:  'text',
							optionCode: 'firstName',
							currentValue: UncannyAutomator.recipe.userSelector.fields.existingUser.firstName,
							label: UncannyAutomator.i18n.userSelector.newUser.firstName,
							isRequired: false,
							supportsTokens: true
						},
						{
							itemType:   'user-selector',
							fieldType:  'text',
							optionCode: 'lastName',
							currentValue: UncannyAutomator.recipe.userSelector.fields.existingUser.lastName,
							label: UncannyAutomator.i18n.userSelector.newUser.lastName,
							isRequired: false,
							supportsTokens: true
						},
						{
							itemType:   'user-selector',
							fieldType:  'text',
							optionCode: 'email',
							currentValue: UncannyAutomator.recipe.userSelector.fields.existingUser.email,
							label: UncannyAutomator.i18n.userSelector.newUser.email,
							isRequired: true,
							supportsTokens: true
						},
						{
							itemType:   'user-selector',
							fieldType:  'text',
							optionCode: 'username',
							currentValue: UncannyAutomator.recipe.userSelector.fields.existingUser.username,
							label: UncannyAutomator.i18n.userSelector.newUser.username,
							isRequired: true,
							supportsTokens: true
						},
						{
							itemType:   'user-selector',
							fieldType:  'text',
							optionCode: 'displayName',
							currentValue: UncannyAutomator.recipe.userSelector.fields.existingUser.displayName,
							label: UncannyAutomator.i18n.userSelector.newUser.displayName,
							isRequired: false,
							supportsTokens: true
						},
						{
							itemType:   'user-selector',
							fieldType:  'text',
							optionCode: 'password',
							currentValue: UncannyAutomator.recipe.userSelector.fields.existingUser.password,
							label: UncannyAutomator.i18n.userSelector.newUser.password,
							description: UncannyAutomator.i18n.userSelector.newUser.passwordDescription,
							isRequired: false,
							supportsTokens: true
						},
						{
							itemType:   'user-selector',
							fieldType:  'select',
							optionCode: 'role',
							currentValue: ! isEmpty( UncannyAutomator.recipe.userSelector.fields.existingUser.roles ) ? UncannyAutomator.recipe.userSelector.fields.existingUser.roles : 'subscriber',
							label: UncannyAutomator.i18n.userSelector.newUser.roles,
							isRequired: true,
							supportsTokens: false,
							options: Object.keys( roles ).map( ( key, index ) => {
								return {
									value: key,
									text:  roles[ key ]
								}
							})
						},
						{
							itemType: 'user-selector',
							fieldType: 'radio',
							optionCode: 'logUserIn',
							name: 'logUserIn',
							currentValue: UncannyAutomator.recipe.canLogNewUserIn ? 'no' : ( ! isEmpty( UncannyAutomator.recipe.userSelector.fields.existingUser.logUserIn ) ? UncannyAutomator.recipe.userSelector.fields.existingUser.logUserIn : 'no' ),
							isHidden: UncannyAutomator.recipe.canLogNewUserIn,
							label: UncannyAutomator.i18n.userSelector.logUserIn,
							supportsTokens: false,
							options: [{
									value: 'yes',
									text:  UncannyAutomator.i18n.yes
								},
								{
									value: 'no',
									text:  UncannyAutomator.i18n.no
								}
							]
						}
					];

					// Create fields
					newUserFields.forEach(( fieldObject ) => {
						// Create field
						const field = new Field( fieldObject.fieldType, fieldObject );

						// Get container
						let $fieldFormElement = field.getFormElement();
						$subOptionsContainer.append( $fieldFormElement );

						/**
						 * We don't need this for now, but we could need it in the future
						 * 

						// Listen changes
						$fieldFormElement.on( 'automator:field:change', () => {
							// Get ID
							const fieldId = fieldObject.optionCode;

							// Get value
							const value = field.getValue();
						});

						*/

						// Save field
						optionsFields.push( field );
					});

					// Append container
					$fields.append( $subOptionsContainer );
				}
				break;

			case 'newUser':
				// Create new users fields
				let newUserFields = [
					{
						itemType:   'user-selector',
						fieldType:  'text',
						optionCode: 'firstName',
						currentValue: UncannyAutomator.recipe.userSelector.fields.newUser.firstName,
						label: UncannyAutomator.i18n.userSelector.newUser.firstName,
						isRequired: false,
						supportsTokens: true
					},
					{
						itemType:   'user-selector',
						fieldType:  'text',
						optionCode: 'lastName',
						currentValue: UncannyAutomator.recipe.userSelector.fields.newUser.lastName,
						label: UncannyAutomator.i18n.userSelector.newUser.lastName,
						isRequired: false,
						supportsTokens: true
					},
					{
						itemType:   'user-selector',
						fieldType:  'text',
						optionCode: 'email',
						currentValue: UncannyAutomator.recipe.userSelector.fields.newUser.email,
						label: UncannyAutomator.i18n.userSelector.newUser.email,
						isRequired: true,
						supportsTokens: true
					},
					{
						itemType:   'user-selector',
						fieldType:  'text',
						optionCode: 'username',
						currentValue: UncannyAutomator.recipe.userSelector.fields.newUser.username,
						label: UncannyAutomator.i18n.userSelector.newUser.username,
						isRequired: true,
						supportsTokens: true
					},
					{
						itemType:   'user-selector',
						fieldType:  'text',
						optionCode: 'displayName',
						currentValue: UncannyAutomator.recipe.userSelector.fields.newUser.displayName,
						label: UncannyAutomator.i18n.userSelector.newUser.displayName,
						isRequired: false,
						supportsTokens: true
					},
					{
						itemType:   'user-selector',
						fieldType:  'text',
						optionCode: 'password',
						currentValue: UncannyAutomator.recipe.userSelector.fields.newUser.password,
						label: UncannyAutomator.i18n.userSelector.newUser.password,
						description: UncannyAutomator.i18n.userSelector.newUser.passwordDescription,
						isRequired: false,
						supportsTokens: true
					},
					{
						itemType:   'user-selector',
						fieldType:  'select',
						optionCode: 'role',
						currentValue: ! isEmpty( UncannyAutomator.recipe.userSelector.fields.newUser.roles ) ? UncannyAutomator.recipe.userSelector.fields.newUser.roles : 'subscriber',
						label: UncannyAutomator.i18n.userSelector.newUser.roles,
						isRequired: true,
						supportsTokens: false,
						options: Object.keys( roles ).map( ( key, index ) => {
							return {
								value: key,
								text:  roles[ key ]
							}
						})
					},
					{
						itemType: 'user-selector',
						fieldType: 'radio',
						optionCode: 'logUserIn',
						name: 'logUserIn',
						currentValue: UncannyAutomator.recipe.canLogNewUserIn ? 'no' : ( ! isEmpty( UncannyAutomator.recipe.userSelector.fields.existingUser.logUserIn ) ? UncannyAutomator.recipe.userSelector.fields.existingUser.logUserIn : 'no' ),
						isHidden: ! UncannyAutomator.recipe.canLogNewUserIn,
						label: UncannyAutomator.i18n.userSelector.logUserIn,
						supportsTokens: false,
						options: [{
								value: 'yes',
								text:  UncannyAutomator.i18n.yes
							},
							{
								value: 'no',
								text:  UncannyAutomator.i18n.no
							}
						]
					}
				];

				// Create fields
				newUserFields.forEach(( fieldObject ) => {
					// Create field
					const field = new Field( fieldObject.fieldType, fieldObject );

					// Get container
					let $fieldFormElement = field.getFormElement();
					$fields.append( $fieldFormElement );

					// Save field
					optionsFields.push( field );
				});

				// Create fallback field
				let newUserFallbackField = new Field( 'radio', {
					itemType:   'user-selector',
					optionCode: 'fallback',
					name: 'user-selector-new-user-fallback',
					label: UncannyAutomator.i18n.userSelector.newUser.existingUserFieldLabel,
					isRequired: true,
					currentValue: UncannyAutomator.recipe.userSelector.fields.newUser.fallback,
					options: [
						{
							value: 'select-existing-user',
							text: UncannyAutomator.i18n.userSelector.newUser.existingUserFieldOptionExisting
						},
						{
							value: 'do-nothing',
							text: UncannyAutomator.i18n.userSelector.newUser.existingUserFieldOptionDoNothing
						}
					]
				});
				optionsFields.push( newUserFallbackField );

				// Add fallback field to the container
				let $newUserfallbackFieldFormElement = newUserFallbackField.getFormElement();
				$fields.append( $newUserfallbackFieldFormElement );

				// Listen changes
				$newUserfallbackFieldFormElement.on( 'automator:field:change', () => {
					// Update the value of all the fields (don't validate them yet)
					let fieldsValues = {};

					// Iterate fields
					optionsFields.forEach(( Field ) => {
						// Save the field using the optionCode as ID and field value as value
						fieldsValues[ Field.getAttributes().optionCode ] = Field.getValue();
					});

					// Get and save value
					userSelectorObject( 'update-fields', {
						source: 'newUser',
						fields: fieldsValues
					});

					// Render the actions metabox again
					new Actions();
				});

				// Check if we have to add more fields or the do-nothing notice
				if ( UncannyAutomator.recipe.userSelector.fields.newUser.fallback == 'do-nothing' ){
					// Create "Do nothing" notice
					const $doNothingNotice = $( `
						<div class="user-selector__warning">
							${ UncannyAutomator.i18n.userSelector.newUser.doNothingMessage }
						</div>
					` );

					$fields.append( $doNothingNotice );
				}
				else if ( UncannyAutomator.recipe.userSelector.fields.newUser.fallback == 'select-existing-user' ){
					// Create container
					let $subOptionsContainer = $( '<div/>', {
						'class': 'item-options'
					});

					// Create "Prioritized Field" field
					let prioritizedField = new Field( 'select', {
						itemType:     'user-selector',
						optionCode:   'prioritizedField',
						label:        UncannyAutomator.i18n.userSelector.newUser.prioritizedFieldLabel,
						currentValue: UncannyAutomator.recipe.userSelector.fields.newUser.prioritizedField,
						defaultValue: 'email',
						description:  UncannyAutomator.i18n.userSelector.newUser.prioritizedFieldDescription,
						isRequired:   true,
						options: [
							{
								value: 'email',
								text:  UncannyAutomator.i18n.userSelector.newUser.email
							},
							{
								value: 'username',
								text:  UncannyAutomator.i18n.userSelector.newUser.username
							},
						]
					});
					optionsFields.push( prioritizedField );

					// Get field container
					let $prioritizedField = prioritizedField.getFormElement();
					$subOptionsContainer.append( $prioritizedField );

					// Append container
					$fields.append( $subOptionsContainer );
				}

				break;
		}

		// Add fields
		$container.append( $fields );

		// Create and add save and cancel buttons
		const $buttonsContainer = $( `<div class="item-options__buttons"></div>` );

		// Save button
		const $saveButton = $( `<div class="uap-btn uap-btn--primary">${ UncannyAutomator.i18n.save }</div>` );
		const $cancelButton = $( `<div class="uap-btn uap-btn--secondary">${ UncannyAutomator.i18n.cancel }</div>` );

		// Add listeners
		// On save
		$saveButton.on( 'click', () => {
			// Validate fields
			// Create default response
			let areAllFieldsValid = true;

			// Create object to get all the fields values
			let fieldsValues = {}

			// Iterate fields and validate them
			optionsFields.forEach(( Field ) => {
				// Get field validation
				const isFieldValid = Field.validate().isValid;

				// Update global validation
				areAllFieldsValid = areAllFieldsValid && isFieldValid;

				// Check if the field is valid
				if ( isFieldValid ){
					// Save value
					fieldsValues[ Field.getAttributes().optionCode ] = Field.getValue();
				}
			});

			// Save values
			userSelectorObject( 'update-fields', {
				source: UncannyAutomator.recipe.userSelector.source,
				fields: fieldsValues
			});

			// Check if all fields are valid, otherwise
			// do nothing. The .validate() method will
			// render the errors
			if ( areAllFieldsValid ){
				// Add loading class to button
				$saveButton.addClass( 'uap-btn--primary uap-btn--loading uap-btn--disabled' );

				// Make request
				restCall( 'user-selector', {
					recipeId: UncannyAutomator.recipe.id,
					source: UncannyAutomator.recipe.userSelector.source,
					fields: UncannyAutomator.recipe.userSelector.fields[ UncannyAutomator.recipe.userSelector.source ]
				}, ( response ) => {
					// On success
					// Set user selector as valid
					UncannyAutomator.recipe.userSelector.isValid = true;

					// Set step
					userSelectorObject( 'set-step', {
						step: 'show-sentence'
					});

					// Render actions again
					new Actions();
				}, ( response ) => {
					// On error
				});
			}
		});

		// On cancel
		$cancelButton.on( 'click', () => {
			// Delete fields information
			// userSelectorObject( 'reset-fields' );

			// Set step
			userSelectorObject( 'set-step', {
				step: 'select-source'
			});

			// Render actions again
			new Actions();
		})

		// Add buttons
		$buttonsContainer.append( $saveButton );
		$buttonsContainer.append( $cancelButton );
		$container.append( $buttonsContainer );

		return $container;
	}

	getSentenceNodes(){
		// Create elements
		let $optionsContainer = $( `
			<div class="user-selector user-selector--summary">
				<div class="item item--user-selector">
					<div class="item__content">
						<div class="item-options">
							<div class="user-selector-summary"></div>
						</div>
					</div>
				</div>
			</div>
		` );

		$optionsContainer.find( '.item' ).prepend( this.getHeaderNodes() );

		// Get container
		let $summaryContainer = $optionsContainer.find( '.user-selector-summary' );

		// Create first row
		let $sourceRow = $( `
			<div class="user-selector-summary-row">
				<div class="user-selector-summary-row__icon">
					<span class="uo-icon uo-icon--atom"></span>
				</div>
				<div class="user-selector-summary-row__content"></div>
			</div>
		` );

		// Create token
		let $sourceRowToken = $( `
			<div class="user-selector-summary-row__token"></div>
		` );
		$sourceRow.find( '.user-selector-summary-row__content' ).append( $sourceRowToken );

		// If the source is "existingUser"
		if ( UncannyAutomator.recipe.userSelector.source == 'existingUser' ){
			// Then add the "Existing user" string
			$sourceRowToken.text( UncannyAutomator.i18n.userSelector.userType.existing );
		}
		// If it's "newUser"
		else if ( UncannyAutomator.recipe.userSelector.source == 'newUser' ){
			// Then add the "New user" string
			$sourceRowToken.text( UncannyAutomator.i18n.userSelector.userType.new );
		}

		// Listen clicks
		$sourceRowToken.on( 'click', () => {
			// Go to the first step.
			
			// Delete fields information
			// userSelectorObject( 'reset-fields' );

			// Set step
			userSelectorObject( 'set-step', {
				step: 'select-source'
			});

			// Render actions again
			new Actions();
		});

		// Add to container
		$summaryContainer.append( $sourceRow );

		// Create first row
		let $dataRow = $( `
			<div class="user-selector-summary-row">
				<div class="user-selector-summary-row__icon">
					<span class="uo-icon uo-icon--fingerprint"></span>
				</div>
				<div class="user-selector-summary-row__content"></div>
			</div>
		` );

		// Create token
		let $dataRowSentence;

		if ( UncannyAutomator.recipe.userSelector.source == 'existingUser' ){
			$dataRowSentence = $( `
				<div>
				${
					vsprintf(
						UncannyAutomator.i18n.userSelector.summary.matches,
						[
							`<div class="user-selector-summary-row__token">${ UncannyAutomator.i18n.userSelector.summary[ UncannyAutomator.recipe.userSelector.fields.existingUser.uniqueField ] }</div>`,
							`<div class="user-selector-summary-row__token">${ UncannyAutomator.recipe.userSelector.fields.existingUser.uniqueFieldValue }</div>`
						]
					)
				}
				</div>`
			);
		}
		else if ( UncannyAutomator.recipe.userSelector.source == 'newUser' ){
			$dataRowSentence = $( `
				<div>
				${
					vsprintf(
						UncannyAutomator.i18n.userSelector.summary.withEmail,
						[
							`<div class="user-selector-summary-row__token">${ UncannyAutomator.recipe.userSelector.fields.newUser.email }</div>`
						]
					)
				}
				</div>
				`
			);
		}

		$dataRow.find( '.user-selector-summary-row__content' ).append( $dataRowSentence );

		// Listen clicks
		$dataRowSentence.find( '.user-selector-summary-row__token' ).on( 'click', () => {
			// Go to the second step.
			
			// Set step
			userSelectorObject( 'set-step', {
				step: 'set-options'
			});

			// Render actions again
			new Actions();
		});

		// Add data row
		$summaryContainer.append( $dataRow );

		// Add fallback row
		let $fallbackRow = $( `
			<div class="user-selector-summary-row">
				<div class="user-selector-summary-row__icon">
					<span class="uo-icon uo-icon--directions"></span>
				</div>
				<div class="user-selector-summary-row__content"></div>
			</div>
		` );

		let otherwiseText = '';

		if ( UncannyAutomator.recipe.userSelector.source == 'existingUser' ){
			if ( UncannyAutomator.recipe.userSelector.fields.existingUser.fallback == 'create-new-user' ){
				otherwiseText = UncannyAutomator.i18n.userSelector.summary.createNewUser;
			}
			else if ( UncannyAutomator.recipe.userSelector.fields.existingUser.fallback == 'do-nothing' ){
				otherwiseText = UncannyAutomator.i18n.userSelector.summary.doNothing;
			}
		}
		else if ( UncannyAutomator.recipe.userSelector.source == 'newUser' ){
			if ( UncannyAutomator.recipe.userSelector.fields.newUser.fallback == 'select-existing-user' ){
				otherwiseText = UncannyAutomator.i18n.userSelector.summary.selectExistingUser;
			}
			else if ( UncannyAutomator.recipe.userSelector.fields.newUser.fallback == 'do-nothing' ){
				otherwiseText = UncannyAutomator.i18n.userSelector.summary.doNothing;
			}
		}

		let $fallbackRowSentence = $( `
			<div>
			${
				vsprintf(
					UncannyAutomator.i18n.userSelector.summary.otherwise,
					[
						`<div class="user-selector-summary-row__token">${ otherwiseText }</div>`
					]
				)
			}
			</div>`
		);
		$fallbackRow.find( '.user-selector-summary-row__content' ).append( $fallbackRowSentence );

		// Listen clicks
		$fallbackRow.find( '.user-selector-summary-row__token' ).on( 'click', () => {
			// Go to the second step.

			// Set step
			userSelectorObject( 'set-step', {
				step: 'set-options'
			});

			// Render actions again
			new Actions();
		});

		// Add data row
		$summaryContainer.append( $fallbackRow );

		// Return nodes
		return $optionsContainer;
	}

	getHeaderNodes(){
		// Define title variable
		let title;

		// Get title variable
		switch ( UncannyAutomator.recipe.userSelector.step ){
			case 'select-source':
			case 'show-sentence':
				// Define title
				title = UncannyAutomator.i18n.userSelector.sentenceTitle;

				break;

			case 'set-options':
				// Define title
				title = UncannyAutomator.i18n.userSelector.setOptions;

				break;
		}

		// Create output
		let $output = $( `
			<div>
				<div class="item-actions">
					<div class="item-actions__btn">
						<i class="uo-icon uo-icon--ellipsis-h-alt"></i>
					</div>
					<div class="item-actions__dropdown">
						<ul class="item-actions-dropdown__ul">
							<a href="#" target="_blank" class="item-actions-dropdown__action">
								<span>${ UncannyAutomator.i18n.support }</span>
							</a>
						</ul>
					</div>
				</div>
				<div class="item-icon"></div>
				<div class="item-title clear">
					<div class="item-title__describe">
						<div class="item-title__non-editable">
							${ title }
						</div>
					</div>
				</div>
			</div>
		` );

		// Add functionality to open and close the actions dropdown
		// Listen click on the three dots icon
        $output.find( '.item-actions__btn' ).on( 'click', () => {
        	// Find the actions container and toogle the open class
        	$output.find( '.item-actions' ).toggleClass( 'item-actions--open' )
        } );

		// Return output
		return $output;
	}
}

export default UserSelector;


